import { call, put } from 'redux-saga/effects';

function* measureResponseTime(
  action,
  sagaFunction,
  responseAction,
  title,
  interactType,
) {
  const startTime = new Date().getTime();
  action.payload.interactType = interactType;

  try {
    yield call(sagaFunction, action);
  } finally {
    const endTime = new Date().getTime();
    const elapsedTime = endTime - startTime;
    console.log(
      `${title} request took ${elapsedTime} ms to receive a response.`,
    );
    yield put(responseAction({ [title]: elapsedTime }));
  }
}

export default measureResponseTime;
