import { Flex, Table, Tbody, Th, Thead, Tr } from '@chakra-ui/react';
import ListTableRow from '../ListTableRow';
import { tableHeaders as headers } from '../../config';

function ListTable({ items, selectItem, stopAction }) {
  return (
    <Flex
      flexDirection="column"
      width="100%"
      backgroundColor="white"
      padding="10px"
      h="500px"
    >
      <Table fontFamily="Montserrat" fontWeight="500" variant="simple">
        <Thead>
          <Tr>
            <Th width="20px" padding="5px">
              {/* This untitled header is for Device Status */}
            </Th>
            {headers.map((item, index) => (
              <Th
                fontFamily="Montserrat"
                textTransform="capitalize"
                color="grey1"
                minWidth="70px"
                padding="5px"
                key={index}
              >
                {item}
              </Th>
            ))}
            <Th width="20px" padding="5px">
              {/* This should not be there, there is some additional style which is causing an empty space in the absence of this element */}
            </Th>
          </Tr>
        </Thead>
        <Tbody>
          {items.map((item, index) => (
            <ListTableRow
              key={index}
              index={index}
              item={item}
              onRowSelection={selectItem}
              stopAction={stopAction}
            />
          ))}
        </Tbody>
      </Table>
    </Flex>
  );
}

export default ListTable;
