// @ts-nocheck
import { PayloadAction } from '@reduxjs/toolkit';
import { createSlice } from 'utils/@reduxjs/toolkit';
import { useInjectReducer, useInjectSaga } from 'utils/redux-injectors';
import { testAnalyticsSaga } from './saga';

import { testAnalyticsState } from './types';

export const initialState: testAnalyticsState = {
  loading: false,
  error: null,
  testAnalyticsData: [],
  sessionId: '',
  sessionIdDetail: {},
  paginationPageIndex: [],
  pageIndexValue: {},
  testAnalyticPageCount: 1,
  getSearchKey: '',
  resetKeyFlag: false,
  pageIndexValueTest: 0,
  getFilterKey: {},
  pageIndexValueOngoing: 0,
  getFilterKeyOngoing: {},
  pageIndexValueSchedule: 0,
  getFilterKeySchedule: {},
};

const getPageIndexData = (state, objectData) => {
  if (state.paginationPageIndex.length <= 0) {
    state.paginationPageIndex = [...state.paginationPageIndex, objectData];

    return objectData;
  }
  const findValue = state.paginationPageIndex.filter(
    item => item.key === objectData.key,
  );
  const findindex = state.paginationPageIndex.findIndex(
    item => item.key === objectData.key,
  );
  if (findValue.length <= 0) {
    state.paginationPageIndex = [...state.paginationPageIndex, objectData];
    return objectData;
  } else {
    state.paginationPageIndex[findindex] = objectData;

    return findValue[0];
  }
};
const getPageNumber = (data: number) => {
  return Math.ceil(data / 20);
};

export const testAnalyticsSlice = createSlice({
  name: 'testAnalytics',
  initialState,
  reducers: {
    loadtestAnalyticsData: (state, action: PageStateAction<any>) => {
      state.loading = true;
      //console.log(action.payload)
      if (action.payload.tabType === 'scheduled') {
        state.pageIndexValueSchedule = action.payload.page - 1;
      } else if (action.payload.tabType === 'started') {
        state.pageIndexValueOngoing = action.payload.page - 1;
      } else {
        state.pageIndexValueTest = action.payload.page - 1;
      }
    },
    resetPageIndex: (state, action: PageStateAction<any>) => {
      // state.pageIndexValueTest = action.payload;
      if (action.payload.tabType === 'scheduled') {
        state.pageIndexValueSchedule = action.payload.value;
      } else if (action.payload.tabType === 'started') {
        state.pageIndexValueOngoing = action.payload.value;
      } else if (action.payload.tabType === 'completed') {
        state.pageIndexValueTest = action.payload.value;
      } else {
        state.pageIndexValueSchedule = action.payload.value;
        state.pageIndexValueOngoing = action.payload.value;
        state.pageIndexValueTest = action.payload.value;
      }
    },
    testAnalyticsDataLoaded: (state, action: PayloadAction<any>) => {
      state.loading = false;
      state.error = null;
      state.testAnalyticsData = action.payload;
    },
    triggerSerachKey: (state, action: PayloadAction<any>) => {
      state.getSearchKey = action.payload;
    },

    getPageCountfromApi: (state, action: PayloadAction<any>) => {
      state.testAnalyticPageCount = getPageNumber(action.payload);
    },

    testAnalyticsDataError: (state, action: PayloadAction<any>) => {
      state.loading = false;
      state.error = action.payload;
    },
    getSessionIdDetail: (state, action: PayloadAction<any>) => {
      localStorage.setItem('testId', action.payload.uuid._id);

      state.sessionId = action.payload.uuid._id;
      state.sessionIdDetail = action.payload;
    },
    getPaginationPageIndex: (state, action: PayloadAction<any>) => {
      state.pageIndexValue = getPageIndexData(state, action.payload);
    },
    resetAllKey: (state, action: PayloadAction<any>) => {
      state.resetKeyFlag = action.payload;
    },
  },
});

export const { actions: testAnalyticsActions } = testAnalyticsSlice;

export const useTestAnalyticsSlice = () => {
  useInjectReducer({
    key: testAnalyticsSlice.name,
    reducer: testAnalyticsSlice.reducer,
  });
  useInjectSaga({ key: testAnalyticsSlice.name, saga: testAnalyticsSaga });
  return { testAnalyticsactions: testAnalyticsSlice.actions };
};
