import { formatMessage } from './index';

const handleKeyDown = (event, channelName, ref) => {
  console.log({ event });
  if (
    !event.ctrlKey &&
    !event.altKey &&
    !event.metaKey &&
    !(event.key === 'Meta')
  ) {
    event.preventDefault();
    // handle non-modifier keys
    if (event.key == 'Shift') return undefined;
    let msg = ['input.type', channelName, { text: event.key }];
    console.log(`Key "${event.key}" was pressed`);
    ref.current.sendMessage(formatMessage(msg));
  }
};

const addKeyListener = (channelName, ref) => {
  document.onkeydown = event => handleKeyDown(event, channelName, ref);
  console.log('Added key down listener');
  //@ts-ignore
  window.hasKeyListener = true;
};

const removeKeyListener = () => {
  document.onkeydown = null;
  console.log('Removed key down listener');
  //@ts-ignore
  window.hasKeyListener = false;
};

export { addKeyListener, removeKeyListener };
