import { FILE_CATEGORIES, FILE_EXTENSIONS } from '../constants/fileCategory';

function getFileCategory(platform) {
  if (platform === 'ios') {
    return FILE_CATEGORIES.IOS_APPLICATION;
  } else if (platform === 'android') {
    return FILE_CATEGORIES.ANDROID_APPLICATION;
  } else if (platform === 'tv' || platform === 'TV') {
    return FILE_CATEGORIES.TV_APPLICATION;
  } else {
    // Handle other platforms or invalid inputs
    return `${platform}-application`;
  }
}

function getFileExtension(platform) {
  if (platform === 'ios') {
    return FILE_EXTENSIONS.IOS;
  } else if (platform === 'android') {
    return FILE_EXTENSIONS.ANDROID;
  } else if (platform === 'tv' || platform === 'TV') {
    return FILE_EXTENSIONS.TV_INTERACT;
  } else {
    return '';
  }
}

function getExtractFileExtension(file) {
  const fileExtensionExtract = file.files[0].name.split('.').pop();
  const fileExtension = `.${fileExtensionExtract}`;
  return fileExtension;
}
export { getFileCategory, getFileExtension, getExtractFileExtension };
