// @ts-nocheck
import { Select } from '@chakra-ui/react';
import React from 'react';

function SelectComponent({
  title,
  selectOptions,
  selectValue,
  setSelectValue,
  type,
  name,
  width,
}) {
  // console.log(selectOptions)
  const sorted =
    type === 'project' && selectOptions && selectOptions.length > 0
      ? selectOptions
          .filter(({ name }) => name)
          .sort((a, b) => a.name.localeCompare(b.name))
      : selectOptions;

  return (
    <Select
      placeholder={title}
      size="sm"
      width={width || '230px'}
      color="grey13"
      h="34px"
      border="1px solid "
      borderColor="grey1"
      borderRadius="5px"
      data-select={title + '_' + name}
      value={selectValue}
      onChange={e => setSelectValue(e.target.value)}
      _focus={{ boxShadow: ' 0 0 0 white', borderColor: 'grey1' }}
    >
      {sorted &&
        sorted.map((item, index) => (
          <option
            data-select-project={
              type === 'project' && item.name + '_' + name + '_' + index
            }
            id={
              type === 'project'
                ? 'option_id_' + name + '_' + index + '_' + item.name
                : 'option_id_' + name + '_' + index + '_' + item.fileName
            }
            data-select-file={
              type !== 'project' && item.fileName + '_' + name + '_' + index
            }
            //id = {type !== 'project' && item.fileName + '_' + name+"_"+index}
            key={index}
            value={type === 'project' ? item.name : index}
          >
            {type === 'project' ? item.name : item.fileName}
          </option>
        ))}
    </Select>
  );
}

export default SelectComponent;
