import { MAX_SESSION_DURATION_IN_MINUTES } from 'app/constants';

const getCurrentDateTime = (): Date => {
  return new Date();
};

const getCurrentDateTimeString = (): string => {
  return new Date().toISOString();
};

const getCurrentDateTimeFromString = (dateTimeString: string): Date => {
  return new Date(dateTimeString);
};

const getTimeDifferenceWithPresent = (
  dateTime: Date,
): { days: number; minutes: number } => {
  const timeDifference = Math.abs(
    getCurrentDateTime().getTime() - dateTime.getTime(),
  );
  const minutesDifference = timeDifference / (1000 * 60); // Convert milliseconds to minutes
  const daysDifference = Math.floor(minutesDifference / (24 * 60)); // Calculate days

  const remainingMinutes = minutesDifference - daysDifference * 24 * 60;

  return {
    days: daysDifference,
    minutes: remainingMinutes,
  };
};

const isTimeNotExceedingMaxSessionTime = (dateTime: Date): boolean => {
  const { days, minutes } = getTimeDifferenceWithPresent(dateTime);

  return days === 0 && minutes < MAX_SESSION_DURATION_IN_MINUTES;
};

export {
  getCurrentDateTime,
  getCurrentDateTimeString,
  getCurrentDateTimeFromString,
  getTimeDifferenceWithPresent,
  isTimeNotExceedingMaxSessionTime,
};
