import {
  selectMobileInteractSlice,
  selectLrInteractSlice,
  mobileInteractSelectors,
  lrInteractSelectors,
} from '../slice/selectors';
import { useMobileInteractSlice, useLrInteractSlice } from '../slice';
import { useGlobalSlice } from 'app/slice';
import { selectGlobal } from 'app/slice/selectors';

import ROUTES from 'app/constants/Routes';

export const getConfig = type => {
  let config;

  switch (type) {
    case 'mobileInteract':
      config = {
        globalSlice: useGlobalSlice,
        slice: useMobileInteractSlice,
        selector: mobileInteractSelectors,
        globalSelector: selectGlobal,
        route: ROUTES.MOBILE_INTERACT,
      };
      break;
    case 'lrInteract':
      config = {
        globalSlice: useGlobalSlice,
        slice: useLrInteractSlice,
        selector: lrInteractSelectors,
        globalSelector: selectGlobal,
        route: ROUTES.LR_INTERACT,
      };
      break;
    default:
      return;
  }
  return config;
};
