//@ts-nocheck
import { Flex, Stack, Text, useToast } from '@chakra-ui/react';
import ButtonComp from 'app/components/ButtonComp';
import SelectComponent from 'app/pages/Appium/components/SelectComponent';
import UploadButton from 'app/pages/Appium/components/UploadButton';
import LabelFieldComponent from 'app/pages/AutomationTest/components/LabelFieldComponent';
import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import CardComponent from '../../components/CardComponent';
import CardRow from '../../components/CardRow.tsx';
import { useSyntheticMonitoringSlice } from '../../slice';
import { selectSyntheticMonitoring } from '../../slice/selectors';

function EventConfiguration() {
  const {
    kpiPayload,
    journeyPayload,
    updateJourneyResponse,
    updateJourneyError,
    journeyId
  } = useSelector(selectSyntheticMonitoring);
  const dispatch = useDispatch();
  const { actions } = useSyntheticMonitoringSlice();
  const toast = useToast();
  
  useEffect(() => {
    if (updateJourneyResponse.hasOwnProperty('status')) {
      if (updateJourneyResponse.status === 200) {
        toast({
          title: updateJourneyResponse.body.message,
          status: 'success',
          isClosable: true,
          position: 'bottom-right',
        });
        dispatch(actions.resetJourneyPayload({}));
        dispatch(actions.resetKpiPayload({}));
     
      }else{
        toast({
          title: updateJourneyResponse.body.message,
          status: 'error',
          isClosable: true,
          position: 'bottom-right',
        });
      }
      dispatch(actions.updatedJourney({}));
      dispatch(actions.getJourneyId(''))
    }
  }, [updateJourneyResponse]);
  useEffect(() => {
    if (updateJourneyError !== null && updateJourneyError === 3) {
      toast({
        title: 'Something went wrong',
        status: 'error',
        isClosable: true,
        position: 'bottom-right',
      });
      dispatch(actions.updateJourneyError(null));
    }
  }, [updateJourneyError]);

  const submitJourneyHandler = () => {
      if(!journeyPayload.name  || journeyId === '' ){
        toast({
          title: 'Journey Name or Id not found',
          status: 'error',
          isClosable: true,
          position: 'bottom-right',
        });
        return
      }
    if (journeyPayload.name && journeyId) {
      
      const kpiNames = kpiPayload.map(({ uuid }) => uuid);
      const payload = { name: journeyPayload.name, kpi: kpiNames };
      // payload.kpis = [...kpiNames];
      if (journeyId !== '') {
       // console.log(payload)
        dispatch(actions.loadUpdateJourney({ id: journeyId, body: payload }));
      }
    }
  };

  return (
    <Stack spacing="10px">
      <LabelFieldComponent label="Journey Name">
        <Text fontWeight="600">{journeyPayload.name}</Text>
      </LabelFieldComponent>
      <LabelFieldComponent>
        <Flex alignItems="center" width="100%" height="200px">
          <Flex
            alignItems="center"
            padding="2px"
            width="100%"
            height="100%"
            flexDirection="column"
            css={{
              '&::-webkit-scrollbar': {
                width: '6px',
                //height: '30px',
                border: '5px solid #F3F6F9',
              },
              '&::-webkit-scrollbar-track': {
                boxShadow: 'inset 0 0 6px white',
              },
              '&::-webkit-scrollbar-thumb': {
                backgroundColor: '#C1C1C1',
                borderRadius: '5px',
                boreder: '2px solid',
              },
            }}
            overflow="auto"
            overflowX="hidden"
          >
            {kpiPayload.length > 0 &&
              kpiPayload.map((kpiData, index) => (
                <CardRow key={index} kpiData={kpiData}></CardRow>
              ))}
          </Flex>
        </Flex>
      </LabelFieldComponent>
      <LabelFieldComponent>
        <Flex padding="10px 0px">
          <ButtonComp
            buttonHandler={submitJourneyHandler}
            padding="7px 12px"
            backgroundColor="blue1"
            color="white"
            fontWeight="500"
            marginLeft="0px"
          >
            Submit
          </ButtonComp>
        </Flex>
      </LabelFieldComponent>
    </Stack>
  );
}

export default EventConfiguration;
