// @ts-nocheck
import React from 'react';
import { Tabs, Tab, TabList, TabPanels, TabPanel } from '@chakra-ui/react';
import SubHeaderComp from '../SubHeaderComp';
import SubContainer from '../SubContainer';

function TabsComponent({
  listofTabs,
  listofComponents,
  defaultIndex,
  isSubContainer,
  rightContent,
  orientation,
  isBorder,
  isLazy,
  isDisabled,
}) {
  return (
    <Tabs isLazy={isLazy} defaultIndex={defaultIndex} orientation={orientation}>
      <SubHeaderComp>
        <TabList
          borderBottomWidth={isBorder ? '3px' : '0px'}
          // borderBottomWidth= "3px"
          borderTop={isBorder ? '2px solid #EDEDED' : '0px'}
          borderColor="grey3"
          width="100%"
          fontFamily="Montserrat"
          fontSize="16px"
          fontWeight="500"
          color="grey15"
        >
          {listofTabs &&
            listofTabs.map((tab, index) => (
              <Tab
                isDisabled={index === 0 && isDisabled}
                paddingLeft="0px"
                paddingRight="0px"
                marginLeft="15px"
                fontWeight="500"
                key={index}
                data-tab-id={`tab-button-${tab}-${index}`}
                marginRight="15px"
                borderBottom="3px solid transparent"
                _focus={{ boxShadow: ' 0 0 0 white' }}
                _selected={{
                  color: 'black',
                  fontWeight: '600',
                  borderBottomColor: 'red',
                }}
              >
                {tab}
              </Tab>
            ))}
        </TabList>
        {rightContent}
      </SubHeaderComp>

      <TabPanels bg="white" height={isSubContainer ? '100vh' : '800px'}>
        {listofComponents &&
          listofComponents.map((tabcomp, index) => (
            <TabPanel key={index} padding="0" margin="0">
              {isSubContainer && <SubContainer>{tabcomp}</SubContainer>}
              {!isSubContainer && tabcomp}
            </TabPanel>
          ))}
      </TabPanels>
    </Tabs>
  );
}

export default TabsComponent;
