import { decodeToken } from 'react-jwt';
import { getIdToken } from './getUserIdToken';

export const getRoleName = () => {
  const token = getIdToken();
  if (!token) {
    return '';
  }
  const myDecodedToken = decodeToken(token);
  if (!myDecodedToken) {
    return '';
  }
  const userType = myDecodedToken['custom:userRole'];
  if (userType === 'TenantAdmin') {
    return 'admin';
  } else if (userType === 'TenantCare') {
    return 'user';
  } else {
    return 'user';
  }
};
