// @ts-nocheck
import {Box} from '@chakra-ui/react';
import React, {useEffect, useRef, useState} from 'react';
import Log from './log';
import {extractDeviceLog} from 'app/pages/Interact/slice/utils/getExtractMessage';
import {useInteractSlice} from 'app/pages/Interact/hooks';

const transformLogObj = (logString, deviceType) => {
  const getIosPid = pid => {
    let startIndex = pid.indexOf('[') + 1; // Find the index of '[' and add 1 to skip it
    let endIndex = pid.indexOf(']'); // Find the index of ']'
    let valueInsideBrackets = pid.substring(startIndex, endIndex);
    return valueInsideBrackets;
  };

  console.log('logString2343434', logString);
  const obj = {
    timeStamp: '',
    pid: '',
    tid: '',
    level: '',
    message: '',
  };
  if (deviceType === 'android') {
    const arr = logString.split(' ');
    if (arr.length) {
      obj.timeStamp = arr[0] + ' ' + arr[1];
      obj.pid = arr[2];
      obj.tid = arr[3];
      obj.level = arr[4];
      let message = '';
      arr.forEach((item, index) => {
        if (
          index !== 0 &&
          index !== 1 &&
          index !== 2 &&
          index !== 3 &&
          index !== 4
        ) {
          message = message + ' ' + item;
        }
      });
      obj.message = message || '';
    }
  } else if (deviceType === 'ios') {
    const arr = logString.split(' ').filter(item => item.length > 0);
    // console.log('iosLogString', arr);
    if (arr.length) {
      obj.timeStamp = arr[0] + ' ' + arr[1] + ' ' + arr[2];
      obj.pid = getIosPid(arr[3]);
      obj.level = arr[4].substr(0, arr[4].length - 1);
      let message = '';
      arr.forEach((item, index) => {
        if (
          index !== 0 &&
          index !== 1 &&
          index !== 2 &&
          index !== 3 &&
          index !== 4
        ) {
          message = message + ' ' + item;
        }
        obj.message = message || '';
      });
    }
  }
  return obj;
};
const LogViewer = ({ filters, levelOptions, setIsStreamStarted, log }) => {
  const bottomRef = useRef();

  const { device } = useInteractSlice();
  const [deviceLog, setDeviceLog] = useState([]);

  useEffect(() => {
    // console.log('123456654321 platform===============', device.platform);
    setIsStreamStarted(true);
    // console.log('123456654321 log========================', log);
    const logString = extractDeviceLog(log);
      // console.log('checjsssssssssssssssss logString=================', logString);
    const logArr = logString.split('\\n');
      // console.log('123456654321 logArr====================', logArr);
    logArr.forEach(log => {
      const tempLog = log.replaceAll('  ', ' ');

      const devLog = transformLogObj(tempLog, device.platform);
        // console.log('123456654321 devLog==============', tempLog);
      setDeviceLog(prev => {
        if (prev.length === 100) {
          prev.shift();
        }
        return [...prev, devLog];
      });
    });
  }, [log]);

  const handleFilter = log => {
    // console.log('log 1223233', log);
    const levelKey = levelOptions?.find(item => {
      return item?.option === filters?.level;
    });
    return (
      log?.pid?.includes(filters.pid) &&
      log?.tid?.includes(filters.tid) &&
      (levelKey?.androidKey || levelKey?.iphoneKey
        ? device?.platform === 'android'
          ? levelKey?.androidKey === log.level
          : levelKey?.iphoneKey === log.level
        : true) &&
      (filters.isExclude && filters.search.length
        ? !JSON.stringify(log)
            .toLowerCase()
            .includes(filters.search.toLowerCase())
        : JSON.stringify(log)
            .toLowerCase()
            .includes(filters.search.toLowerCase()))
    );
  };

  console.log('deviceLog  fdf dfd fdfd f', deviceLog);

  return (
    <>
      <Box
        height={525}
        overflow={'auto'}
        padding={'30px 20px'}
        backgroundColor={'#404253'}
        color={'#898A95'}
      >
        <Box fontSize={'12px'}>
          <Box marginBottom={'10px'}>
            Connecting to device cloud
            {device && (
              <Box marginBottom={'10px'}>
                Starting device {device.modelName}
              </Box>
            )}
          </Box>
        </Box>
        {deviceLog?.map(log => {
          return handleFilter(log) ? <Log log={log} /> : null;
        })}
        <div ref={bottomRef}></div>
      </Box>
    </>
  );
};

export default LogViewer;
