import { isTokenExpired } from './IsTokenExpire';
import { getRefreshSession } from './authentication/getRefreshSession';
import { getIdToken } from './authentication/getUserIdToken';

export class ResponseError extends Error {
  public response: Response;

  constructor(response: Response) {
    super(response.statusText);
    this.response = response;
  }
}
/**
 * Parses the JSON returned by a network request
 *
 * @param  {object} response A response from a network request
 *
 * @return {object}          The parsed JSON from the request
 */
function parseJSON<T>(response: Response): Promise<T> {
  return response.json() as Promise<T>;
}
/**
 * Checks if a network request came back fine, and throws an error if not
 *
 * @param  {object} response   A response from a network request
 *
 * @return {object|undefined} Returns either the response, or throws an error
 */
function checkStatus(response: Response) {
  if (response.status >= 200 && response.status < 300) {
    return response;
  }
  const error = new ResponseError(response);
  error.response = response;
  throw error;
}

/**
 * Requests a URL, returning a promise
 *
 * @param  {string} url       The URL we want to request
 * @param  {object} [options] The options we want to pass to "fetch"
 *
 * @return {object}           The response data
 */

function fetchProtected(url: string, options: any): Promise<Response> {
  const accessToken = getIdToken();

  if (accessToken) {
    //@ts-ignore
    options.headers['Authorization'] = `Bearer ${accessToken}`;
  }
  //console.log({ accessToken, url, options });
  return fetch(url, options);
}

export async function requestNew(url: string, options?: RequestInit) {
  try {
    const isExpire = isTokenExpired();
    if (isExpire && options && typeof options === 'object') {
      const sessionData = await getRefreshSession();
      const idToken =
        sessionData.hasOwnProperty('idToken') && sessionData.idToken.jwtToken;
      if (idToken) {
        options.headers.Authorization = `Bearer ${idToken}`;
      }
    }
    const res = await fetchProtected(url, options); //fetch(url, options);
    return await parseJSON(res);
  } catch (error: any) {
    return error;
  }
}
