import { useEffect } from 'react';
import { useInteractSlice } from '.';

import { getSavedSessionId } from '../utils';
import {
  isDeviceBusyAndNotSelfOwned,
  isDeviceFree,
  isDeviceSelfOwned,
} from 'app/utils/device';

const useStartSession = (isCleanExit: boolean) => {
  const { interactType, actions, selector, dispatch, device, getEmailId } =
    useInteractSlice();
  const { sessionLoading, session, sessionError, devices, selectedRemote } =
    selector;

  useEffect(() => {
    if (
      devices &&
      devices.length &&
      !isDeviceBusyAndNotSelfOwned(device, getEmailId()) &&
      (interactType !== 'lrInteract' || Object.keys(selectedRemote).length)
    ) {
      const deviceId = window.location.href.split('/').pop() || '';

      if (!session && deviceId && !isCleanExit) {
        const savedSessionId = getSavedSessionId(interactType, deviceId);
        isDeviceSelfOwned(device, getEmailId()) && savedSessionId
          ? dispatch(actions.sessionStarted({ id: savedSessionId }))
          : dispatch(actions.startSession({}));
      }
    }
  }, [
    selectedRemote,
    devices,
    session,
    isCleanExit,
    interactType,
    actions,
    dispatch,
  ]);

  return {
    sessionLoading,
    session,
    sessionError,
  };
};

export default useStartSession;
