import { call, put, select, takeLatest } from 'redux-saga/effects';
import { measureResponseTime } from 'app/utils';
import {
  loadDevices,
  startSession,
  stopSession,
  uploadScreenshot,
  loadAppbuilds,
  createProject,
  loadProjects,
  uploadAppbuildSaga,
  deleteScreenshot,
} from '../saga';

function* createInteractSaga(actions, interactType) {
  yield takeLatest(actions.loadDevices.type, function* (action) {
    yield measureResponseTime(
      action,
      loadDevices,
      actions.setResponseTimes,
      'loadDevices',
      interactType,
    );
  });
  yield takeLatest(actions.startSession.type, function* (action) {
    yield measureResponseTime(
      action,
      startSession,
      actions.setResponseTimes,
      'startSession',
      interactType,
    );
  });
  yield takeLatest(actions.stopSession.type, function* (action) {
    yield measureResponseTime(
      action,
      stopSession,
      actions.setResponseTimes,
      'stopSession',
      interactType,
    );
  });

  yield takeLatest(actions.screenshotDeleting.type, deleteScreenshot);
  yield takeLatest(actions.screenshotUploading.type, uploadScreenshot);
  yield takeLatest(actions.appbuildLoading.type, loadAppbuilds);
  yield takeLatest(actions.uploadAppBuild.type, uploadAppbuildSaga);
  yield takeLatest(actions.projectLoading.type, loadProjects);
  yield takeLatest(actions.createProjectLoading.type, createProject);
}

export default createInteractSaga;
