// import { Image } from '@chakra-ui/react';

import { Box, Checkbox, Button, Image } from '@chakra-ui/react';
import { useState } from 'react';
import { StyledImageWrapper, StyledSceenshotImage } from './styles';

const ScreenshotImage = ({ item, index, setSelectedItems, selectedItems }) => {
  //const [isSelected, setIsSelected] = useState(false);
  const [isHovered, setIsHovered] = useState(false);
  const { ischecked, ...imageId } = item;

  //console.log({ item, imageId });
  const handleMouseEnter = () => {
    setIsHovered(true);
  };

  const handleMouseLeave = () => {
    setIsHovered(false);
  };
  const updateSelectList = (selectedItems, setSelectedItems, item) => {
    console.log({ selectedItems });
    if (selectedItems && selectedItems.length > 0) {
      const itemFound = selectedItems.filter(
        selectedItem => selectedItem.fileName === item.fileName,
      );

      // If items are found with the same fileName, remove them
      if (itemFound.length > 0) {
        const updatedSelectedItems = selectedItems.filter(
          selectedItem => selectedItem.fileName !== item.fileName,
        );
        setSelectedItems(updatedSelectedItems);
      } else {
        // If no items are found, add the current item
        setSelectedItems([...selectedItems, item]);
      }
    } else {
      setSelectedItems([...selectedItems, item]);
    }
  };

  const handleCheckboxChange = () => {
    updateSelectList(selectedItems, setSelectedItems, item);
  };
  console.log({ selectedItems }, 'eslect');
  return (
    <StyledImageWrapper
      onMouseEnter={handleMouseEnter}
      onMouseLeave={handleMouseLeave}
    >
      {(isHovered || ischecked || true) && (
        <Checkbox
          padding="5px"
          // defaultChecked={selectedItems.some(
          //   selectedItem => selectedItem.fileName === item.fileName,
          // )}
          isChecked={selectedItems.some(
            selectedItem => selectedItem.fileName === item.fileName,
          )}
          onChange={() => handleCheckboxChange(imageId)}
        />
      )}
      <>
        {imageId && (
          <StyledSceenshotImage
            key={index}
            data-image={`screenshot-item-image-${index}`}
            src={imageId?.capturedImage}
            alt="screenshot-image"
          />
        )}
      </>
    </StyledImageWrapper>
  );
};

export default ScreenshotImage;
