import { useEffect, useRef } from 'react';

import { delay, Stream } from 'app/utils';
import { addStopPrefix, getSavedSessionId, retryStreamStart } from '../utils';

import { useInteractSlice } from '.';

const useSendStopStreamMessage = (isCleanExit, setDeviceStopTried) => {
  const {
    interactType,

    selector: { session, selectedRemote },
    controllerDetails,
  } = useInteractSlice();
  const firstRun = useRef(true);

  useEffect(() => {
    const stop = async () => {
      const { controllerChannelName, deviceChannelNames } = controllerDetails;
      const deviceId = window.location.href.split('/').pop() || '';
      const savedSessionId = getSavedSessionId(interactType, deviceId);
      const state = Stream();
      await state.startViewer(controllerChannelName, () => {}, true);
      await new Promise(resolve => retryStreamStart(resolve, state, true));

      if (state.failedToStartStream) {
        setDeviceStopTried(true);
        firstRun.current = false;
        console.log('Failed to open data channel');
      } else {
        let stopMessage;
        if (
          interactType === 'lrInteract' &&
          selectedRemote &&
          Object.keys(selectedRemote).length
        ) {
          stopMessage = addStopPrefix([
            savedSessionId,
            ...deviceChannelNames,
            selectedRemote['device-uuid'],
            'MARK_FREE',
          ]);
        } else {
          stopMessage = addStopPrefix([
            savedSessionId,
            ...deviceChannelNames,
            'MARK_FREE',
          ]);
        }
        /* stopMessage = addStopPrefix(deviceChannelNames); */
        console.log(`Sending ${stopMessage}`);
        /* await delay(1000); */

        state.sendMessage(stopMessage);

        //TODO: remove delay and use remote message for confirmation
        await delay(1000);
        state.stopViewer();
        setDeviceStopTried(true);
        firstRun.current = false;
      }
    };
    if (isCleanExit && firstRun.current) {
      stop();
    }
  }, [
    interactType,
    selectedRemote,
    isCleanExit,
    controllerDetails,
    setDeviceStopTried,
  ]);
};

export default useSendStopStreamMessage;
