//@ts-nocheck
import React, {useCallback, useEffect, useRef, useState} from "react";
import {Box, Flex, useToast,} from "@chakra-ui/react";
import ButtonComp from "../../../components/ButtonComp";
import {StyledContainer, StyledVideoPlayer} from "../../Interact/components/InteractionItems/styles";
import useVideoPlayer from "../hooks/useVideoPlayer";
import useContainerDimensions from "../hooks/useContainerDimensions";

import styles from "./VideoPlayer.style";
import {Loader} from "../../Interact/components";

const MIN_DIMENSIONS = {
    height: 800 * (9 / 16), width: 800
}

type VideoPlayerProps = {
    channelName: string;
    onDisconnectClicked: Function;
}

const VideoPlayer = (props: VideoPlayerProps) => {

    const {
        channelName,
        onDisconnectClicked
    } = props;

    const toast = useToast();

    const videoRef = useRef<HTMLVideoElement>();
    const containerRef = useRef<HTMLDivElement>();
    const dimensions = useContainerDimensions(containerRef)

    const [isVideoStreamConnected, setIsVideoStreamConnected] = useState(false);

    const reconnectIfNotConnectedRef = useRef();

    const eventCallback = useCallback((target, status) => {
        if (target == "SINGALING_CLIENT") {
            switch (status) {
                case "open":
                    toast({
                        title: 'Connecting to Video Stream',
                        status: 'info',
                        isClosable: true,
                        position: 'bottom-right',
                    });
                    break;
                case "error":
                    toast({
                        title: 'Failed to connect! Is the device online?',
                        status: 'info',
                        isClosable: true,
                        position: 'bottom-right',
                    });
                    break;
            }
        } else if (target == "PEER") {
            switch (status) {
                case "connecting":
                    toast({
                        title: 'Connecting to device...',
                        status: 'info',
                        isClosable: true,
                        position: 'bottom-right',
                    });
                    break;
                case "connected":
                    toast({
                        title: 'Device Connected',
                        status: 'success',
                        isClosable: true,
                        position: 'bottom-right',
                    });
                    break;
                case "disconnected":
                    setIsVideoStreamConnected(false);
                    toast({
                        title: 'Video streaming disconnected, recovering...',
                        status: 'warning',
                        isClosable: true,
                        position: 'bottom-right',
                    });
                    break;
                case "failed":
                    setIsVideoStreamConnected(false);
                    toast({
                        title: 'Device disconnected!',
                        status: 'error',
                        isClosable: true,
                        position: 'bottom-right',
                    });
                    reconnectIfNotConnectedRef.current?.()
                    break;
            }
        }

    }, [toast]);

    const {
        startStream,
        stopStream,
        remoteVideo,
        remoteVideoError,
    } = useVideoPlayer({channelName, eventCallback})

    const [videoStyle, setVideoStyle] = useState({
        ...styles.videoView,
        ...MIN_DIMENSIONS,
    })

    useEffect(() => {
        const videoTrack = remoteVideo?.getVideoTracks()?.[0]
        console.log("videoTrack--> ", videoTrack?.getSettings?.(), "dimensions-->", dimensions)

        if (
            !dimensions || dimensions?.height || dimensions?.width ||
            dimensions?.height <= 0 || dimensions?.width <= 0
        ) {
            return
        }

        if (!videoTrack) {
            setVideoStyle({
                ...styles.videoView,
                height: 0.8 * dimensions.height,
                width: 0.8 * dimensions.width,
            });
            return;
        }

        const {
            height: videoHeight,
            width: videoWidth,
            aspectRatio: videoAspectRatio
        } = videoTrack.getSettings();

        if (!videoAspectRatio || videoAspectRatio <= 0) {
            return;
        }

        const heightPermittedByWidth = (1 / videoAspectRatio) * (0.8 * dimensions.width);
        const heightPermittedByContainerHeightAvailable = 0.8 * dimensions.height;
        setVideoStyle({
            ...styles.videoView,
            height: Math.min(heightPermittedByWidth, heightPermittedByContainerHeightAvailable),
            // width: Math.max(MIN_DIMENSIONS.width, 0.8 * dimensions.width),
            aspectRatio: videoAspectRatio
        });
    }, [dimensions, remoteVideo]);

    const onReconnect = useCallback(async () => {
        setIsVideoStreamConnected(false)
        try {
            await stopStream()
        } catch (e) {
            console.log("stopStream failed --> ", e)
        }
        await startStream()
    }, [stopStream, startStream]);

    reconnectIfNotConnectedRef.current = useCallback(async (attempts = 3) => {
        console.log("reconnectIfNotConnected:!", attempts, isVideoStreamConnected)
        if (isVideoStreamConnected) {
            console.log("device already connected!", attempts)
            return
        }
        if (attempts == 0) {
            console.log("Failed to reconnect!");
            toast({
                title: 'Failed to Reconnect to device! Is the device online?',
                status: 'error',
                isClosable: true,
                position: 'bottom-right',
            });
            return
        } else {
            toast({
                title: 'Attempting to reconnect...',
                status: 'info',
                isClosable: true,
                position: 'bottom-right',
            });
        }
        await onReconnect()
        setTimeout(() => {
            reconnectIfNotConnectedRef.current?.(attempts - 1)
        }, 10000)
    }, [isVideoStreamConnected, onReconnect, toast]);

    const onDisconnect = useCallback(async () => {
        setIsVideoStreamConnected(false)
        onDisconnectClicked?.()
    }, [stopStream, startStream]);


    useEffect(() => {
        console.log('remoteVideo useEffect called --> ', remoteVideo, videoRef)
        if (remoteVideo && videoRef?.current) {
            try {
                console.log('remoteVideo, remoteStream --> ', remoteVideo)
                videoRef.current.srcObject = remoteVideo;
                videoRef.current.addEventListener("loadedmetadata", () => {
                    videoRef.current?.play().catch((e) => {
                        console.error("Failed to play video: ", e)
                    })
                })
                // toast({
                toast({
                    title: 'Device Video Connected',
                    status: 'success',
                    isClosable: true,
                    position: 'bottom-right',
                });
                //     title: 'Connecting video stream',
                //     status: 'info',
                //     isClosable: true,
                //     position: 'bottom-right',
                // });
                setIsVideoStreamConnected(true)
            } catch (err) {
                console.error(err)
            }
        }
    }, [remoteVideo, videoRef, toast]);

    // start stream on mount, stop on unmount!
    useEffect(() => {
        startStream().then(
            () => console.log("Stream started!"),
            error => console.error("Stream start error: ", error)
        )
        return () => {
            stopStream().then(
                () => console.log("Stream stopped!"),
                error => console.error("Stream stop error: ", error)
            )
        }
    }, []);

    return (
        <StyledContainer>
            <StyledVideoPlayer>
                <Flex
                    ref={containerRef}
                    style={styles.container}
                >
                    <Flex style={styles.videoContainer}>
                        <Box position={'relative'}>
                            <Loader
                                style={styles.videoOverlay}
                                messageStyle={styles.overlayMessage}
                                overlay
                                // show
                                show={!isVideoStreamConnected}
                                message={"Connecting ..."}
                            />
                            <video
                                key={"remote-view"}
                                className="remote-view"
                                ref={videoRef}
                                autoPlay
                                playsInline
                                disablePictureInPicture
                                muted
                                style={videoStyle}
                            />
                        </Box>
                    </Flex>

                    <Flex style={styles.actionContainer}>
                        <ButtonComp
                            style={styles.button}
                            backgroundColor={"red1"}
                            buttonHandler={onDisconnect}
                        >
                            {isVideoStreamConnected ? "Disconnect" : "Change VideoCode"}
                        </ButtonComp>

                        <ButtonComp
                            style={styles.button}
                            backgroundColor={"blue1"}
                            buttonHandler={onReconnect}
                        >
                            Reconnect
                        </ButtonComp>
                    </Flex>
                </Flex>
            </StyledVideoPlayer>
        </StyledContainer>
    );
};

export default React.memo(VideoPlayer);
