import ROUTES from 'app/constants/Routes';
import config from '../config';

const paths = {
  [ROUTES.MOBILE_INTERACT]: 'mobileInteract',
  [ROUTES.LR_INTERACT]: 'lrInteract',
};

export const findMatchingDetailPath = location => {
  for (const path of Object.keys(paths)) {
    /* the [A-Za-z0-9_-] character set will match letters, digits, underscores, and hyphens. The + at the end means that it should match one or more of these characters. This regular expression will match strings that start with the value of path, followed by a forward slash, and then a sequence of characters that can include letters, digits, underscores, or hyphens. */
    const regex = new RegExp(`^${path}/[A-Za-z0-9_-]+$`);
    if (regex.test(location)) {
      return config[paths[path]];
    }
  }
  return null; // Return null if no match is found
};

export const isBreadcrumbPath = location => {
  if (Object.keys(paths).includes(location)) {
    return config[paths[location]];
  }
  return null;
};
