import { createSelector } from '@reduxjs/toolkit';

import { RootState } from 'types';
import { initialState } from '.';

// First select the relevant part from the state
const selectSlice = (state: RootState) => state.testDashboard || initialState;

export const selectTestDashboardList = createSelector(
  [selectSlice],
  state => state.testdashboardList,
);
export const selectTestDashboardDFolderList = createSelector(
  [selectSlice],
  state => state.testdashboardFolderList,
);

export const selectLoadingDashboardList = createSelector(
  [selectSlice],
  state => state.loading,
);
export const selectLoadingDashboardFolderList = createSelector(
  [selectSlice],
  state => state.loadingFolderdash,
);
export const selectLoadingDashboard = createSelector(
  [selectSlice],
  state => state.loadingEmbedUrl,
);
export const selectTestDashboardListError = createSelector(
  [selectSlice],
  state => state.error,
);
export const selectTestDashboardEmbedUrl = createSelector(
  [selectSlice],
  state => state.EmbedUrlData,
);
