import { call, put } from 'redux-saga/effects';

import { allActions } from '../';
import { DevicesErrorType } from '../types';
import { request, postRequestConfig } from 'utils/request';
import { startSession as startSessionUrl } from '../urls';
import { saveSessionInLocalStorage, getSessionType } from '../../utils';

import getAction from '../utils/getAction';

function* startSession(action: any) {
  const { interactType } = action.payload;
  const deviceId = window.location.href.split('/').pop();
  const projectName = getSessionType(interactType);
  const testType = projectName;
  const actionName = getAction(interactType);
  const actions = allActions[actionName];
  try {
    const sessionLoadingResponse = yield call(
      request,
      startSessionUrl(deviceId),
      postRequestConfig({
        projectName,
        testType,
      }),
    );
    if (
      !sessionLoadingResponse.hasOwnProperty('body') ||
      sessionLoadingResponse.statusCode === 500
    ) {
      yield put(actions.sessionError(DevicesErrorType.RESPONSE_ERROR));
    }
    const sessionId = sessionLoadingResponse.body.testID;
    saveSessionInLocalStorage(interactType, deviceId, sessionId);
    yield put(actions.sessionStarted({ id: sessionId }));
  } catch (e) {
    yield put(actions.sessionError(DevicesErrorType.RESPONSE_ERROR));
  }
}

export default startSession;
