//@ts-nocheck
import {
  Flex,
  HStack,
  Icon,
  Img,
  Input,
  InputGroup,
  InputLeftElement,
  Stack,
  Text,
  useToast,
} from '@chakra-ui/react';
import ButtonComp from 'app/components/ButtonComp';
import { HiLockClosed } from 'react-icons/hi';
import { useDispatch } from 'react-redux';
import mozarkIcon from '../../../styles/Assets/mozark-logo.png';
import { REACT_APP_LOGOUT_REDIRECTPAGE } from 'utils/enviroment';
import { useAuth } from 'utils/authentication/useAuth';
import { useEffect, useState } from 'react';
import { useLocation } from 'react-router-dom';
import ROUTES from 'app/constants/Routes';

const RedirectPage = REACT_APP_LOGOUT_REDIRECTPAGE;

function ChangePasswordView() {
  const [newPassword, setNewPassword] = useState('');
  const [confirmNewPassword, setConfirmNewPassword] = useState('');
  const [notmatchMessage, setNotmatchingPassMess] = useState('');
  const [validateionMessage, setValidateionMessage] = useState('');
  const toast = useToast();
  const { cognitoUser, isFirstLogin, userRole, isAuthenticated } = useAuth();

  useEffect(() => {
    // eslint-disable-next-line no-dupe-keys
    console.log({ isAuthenticated, userRole, isFirstLogin });
    if (!isFirstLogin || (isAuthenticated && userRole)) {
      setTimeout(() => {
        console.log('redirect login----------------------');
        window.location.href = ROUTES.LOGIN_PAGE;
      }, 3000);
    }
  }, [isAuthenticated, userRole, isFirstLogin]);

  const checkPasswordMather = () => {
    if (newPassword !== confirmNewPassword) {
      setNotmatchingPassMess(
        '* New password and Confirm password should be same',
      );
    } else {
      setNotmatchingPassMess('');
    }

    const regex = /^(?=.*[0-9])(?=.*[!@#$%^&*()])(?=.*[a-z])(?=.*[A-Z]).{8,}$/;
    if (!regex.test(newPassword)) {
      setValidateionMessage(
        'Your new password does not meet the requirements.',
      );
    } else {
      setValidateionMessage('');
    }
  };
  const validatePassword = password => {
    const regex = /^(?=.*[0-9])(?=.*[!@#$%^&*()])(?=.*[a-z])(?=.*[A-Z]).{8,}$/;
    return regex.test(password);
  };
  async function completeNewPasswordChallengeAsync(
    cognitoUser: any,
    newPassword: string,
  ): Promise<any> {
    try {
      return await new Promise((resolve, reject) => {
        cognitoUser.completeNewPasswordChallenge(newPassword, {
          onSuccess: result => {
            console.log({ result });
            resolve(result);
          },
          onFailure: err => {
            console.log({ err });
            reject(err);
          },
        });
      });
    } catch (error) {
      throw error;
    }
  }

  const changePasswordHandler = async event => {
    if (confirmNewPassword === '' || newPassword === '') {
      toast({
        title: 'Please Enter Old and New password field to Change Password',
        status: 'error',
        isClosable: true,
        position: 'bottom-right',
      });
      return;
    }

    if (confirmNewPassword !== newPassword) {
      toast({
        title: 'Your New password is Not Confirmed Correctly',
        status: 'error',
        isClosable: true,
        position: 'bottom-right',
      });
      return;
    }
    const regex = /^(?=.*[0-9])(?=.*[!@#$%^&*()])(?=.*[a-z])(?=.*[A-Z]).{8,}$/;
    if (!regex.test(newPassword)) {
      toast({
        title: 'Your new password does not meet the requirements.',
        status: 'error',
        isClosable: true,
        position: 'bottom-right',
      });
      return;
    }
    // if (!validatePassword(confirmNewPassword)) {
    //   return;
    // }

    try {
      // console.log({cognitoUser});
      if (!cognitoUser) {
        return undefined;
      }
      const result = await completeNewPasswordChallengeAsync(
        cognitoUser,
        confirmNewPassword,
      );
      console.log('Password change success:', result);
    } catch (error) {
      console.error('Password change failed:', error);
      // Redirect to the failure page after a 5-second delay
      setTimeout(() => {
        window.location.href = RedirectPage;
      }, 5000);
    }
  };

  return (
    <Flex width="100%" fontFamily="Montserrat" flexWrap="wrap" height="100vh">
      <Flex bg="white" w="44%" h="100vh">
        <Img
          w="100%"
          src="https://hotstar-automation.mozark.ai/AppFunctional/assets/images/Forgot.jpg"
        ></Img>
      </Flex>
      <Flex width="50%" color="grey2">
        <Flex width="100%" flexDirection="column" padding="20px">
          <HStack padding="20px 40px">
            <Img width="160px" h="30px" src={mozarkIcon}></Img>
          </HStack>
          <form>
            <Stack width="100%" margin="0px 15%" spacing="20px">
              <Stack spacing="0px">
                <Text fontWeight="600" fontSize="2.2vw" color="grey15">
                  Change password
                </Text>
                <Text fontSize="1.5vw">Enter the new password</Text>
              </Stack>

              <Flex
                width="70%"
                margin="0px 20%"
                fontSize="1.1vw"
                marginTop="10px"
              >
                Password must contain at least 1 number , 1 special character ,
                1 uppercase , Minimum length is 8 characters
              </Flex>

              <Stack
                width="70%"
                margin="0px 20%"
                marginTop="20px"
                spacing="0px"
              >
                <InputGroup>
                  <InputLeftElement
                    marginTop="5px"
                    marginLeft="5px"
                    children={
                      <Icon w="18px" h="18" color="grey6" as={HiLockClosed} />
                    }
                  ></InputLeftElement>
                  <Input
                    type="password"
                    // pattern=".+@globex\.com"
                    pattern="^(?=.*[0-9])(?=.*[!@#$%^&*()])(?=.*[a-z])(?=.*[A-Z]).{8,}$"
                    title="Include Aa-Zz 0-9 [!@#$%^&*()] atleast 8 char "
                    size="20"
                    fontSize="16px"
                    required
                    value={newPassword}
                    onChange={e => setNewPassword(e.target.value)}
                    borderRadius="1px"
                    paddingLeft="40px"
                    placeholder="Enter new Password"
                    height="50px"
                    _focus={{
                      boxShadow: ' 0 0 0 white',
                      borderLeft: '5px solid ',
                      borderLeftColor: 'blue1',
                    }}
                  />
                </InputGroup>
                <InputGroup>
                  <InputLeftElement
                    marginTop="5px"
                    marginLeft="5px"
                    children={
                      <Icon w="18px" h="18" color="grey6" as={HiLockClosed} />
                    }
                  ></InputLeftElement>
                  {/* <input type="password" pattern="^(?=.*[0-9])(?=.*[!@#$%^&*()])(?=.*[a-z])(?=.*[A-Z]).{8,}$" 
 title="Include Aa-Zz 0-9 [!@#$%^&*()] atleast 8 char "/> */}
                  <Input
                    type="password"
                    // pattern=".+@globex\.com"
                    size="20"
                    fontSize="16px"
                    pattern="^(?=.*[0-9])(?=.*[!@#$%^&*()])(?=.*[a-z])(?=.*[A-Z]).{8,}$"
                    title="Include Aa-Zz 0-9 [!@#$%^&*()] atleast 8 char "
                    required
                    value={confirmNewPassword}
                    onChange={e => setConfirmNewPassword(e.target.value)}
                    borderRadius="1px"
                    paddingLeft="40px"
                    placeholder="Confirm New Password"
                    height="50px"
                    _focus={{
                      boxShadow: ' 0 0 0 white',
                      borderLeft: '5px solid ',
                      borderLeftColor: 'blue1',
                    }}
                    onKeyUp={checkPasswordMather}
                  ></Input>
                </InputGroup>
                <Text h="30px" color="red1">
                  {notmatchMessage || validateionMessage}
                </Text>
              </Stack>
              <Stack spacing="50px">
                {/* <Flex align="center" justify="space-between" width="70%"></Flex> */}
                <Flex width="70%">
                  <Flex width="50%">
                    <ButtonComp
                      height="38px"
                      width="90%"
                      //buttonHandler={changePasswordHandler}
                      padding="12px "
                      backgroundColor="blue1"
                      color="white"
                      isDisabled={validateionMessage ? true : false}
                    >
                      <button
                        disabled={validateionMessage ? true : false}
                        onClick={changePasswordHandler}
                        style={{
                          fontWeight: '600',
                          textTransform: 'uppercase',
                          fontSize: '1.1vw',
                        }}
                      >
                        {' '}
                        Update Password{' '}
                      </button>
                    </ButtonComp>
                  </Flex>

                  {/* 
                <Stack>
                  <Text>following conditions for setting Password</Text> 
                  <Text color= "red1"> * Require numbers</Text> 
              <Text color= "red1" > * Require special character   </Text>
              <Text color= "red1" > * Require uppercase letters</Text>
<Text color= "red1" > * Require lowercase letters </Text>
 <Text color= "red1" > * Atleast 8 characters </Text>
                </Stack> */}
                </Flex>
              </Stack>
            </Stack>
          </form>
        </Flex>
      </Flex>

      {/* <Link  to='/forgotpassword' >Forgot password</Link>  */}
    </Flex>
  );
}

export default ChangePasswordView;
