import { forwardRef, useEffect, useState } from 'react';

import { useDisclosure } from '@chakra-ui/react';
import { useNavigate } from 'react-router-dom';

import { useInteractSlice } from 'app/pages/Interact/hooks';
import { NoContent, PopupComponent } from 'app/components';
import {
  StyledCenterWrapper,
  StyledTitleWrapper,
} from 'app/pages/Interact/styles';
import useReceivedMessage from 'app/pages/Interact/hooks/useReceivedMessage';
import { formatMessage } from 'app/pages/Interact/utils';

const MessageBox = forwardRef(({ onExitSession, device }, ref) => {
  const { isOpen, onOpen, onClose } = useDisclosure();
  const [idleError, setStatusMessage] = useState('');
  const [timer, setTimer] = useState(0);
  useReceivedMessage(ref, setStatusMessage, setTimer);

  console.log({ idleError });

  function exitButton() {
    console.log('exit end :', new Date());
    setStatusMessage('');
    onExitSession();
    console.log('navigate listing page :', new Date());
  }
  const continueButton = () => {
    if (ref && ref?.current && ref?.current?.sendMessage) {
      setStatusMessage('');
      onClose();
      let msg = ['input.ping', device?.serial, 'ping'];

      ref.current.sendMessage(formatMessage(msg));
    }
  };
  useEffect(() => {
    if (idleError) {
      setStatusMessage(
        `This session will expire in a few seconds as there has been no interactions for the last few minutes. Press Continue to use the device ?`,
      );
    }
    if (!idleError) {
      setStatusMessage('');
    }
    return () => {
      setStatusMessage('');
    };
  }, [idleError, setStatusMessage, timer]);

  useEffect(() => {
    if (idleError) {
      onOpen();
    } else {
      onClose();
    }
  }, [idleError, onClose, onOpen]);

  useEffect(() => {
    if (isOpen) {
      console.log('exit start :', new Date());
      const getTime = timer * 1000;
      let timerId = setTimeout(exitButton, getTime);
      return () => clearTimeout(timerId);
    }
  }, [isOpen, timer]);

  if (!idleError) {
    return null;
  }

  return (
    <>
      <PopupComponent
        title="Interact"
        width="450px"
        isOpen={isOpen}
        onClose={onClose}
        buttonTitle1="Continue"
        buttonTitle2="Exit"
        buttonHandler1={continueButton}
        buttonHandler2={exitButton}
        mainButtonColor="red"
      >
        <StyledCenterWrapper>
          <StyledTitleWrapper>{idleError}</StyledTitleWrapper>
        </StyledCenterWrapper>
      </PopupComponent>
      {/* <NoContent message={message} />; */}
    </>
  );
  //return <NoContent message={message} />;
});

export default MessageBox;
