import GetIdToken from 'Authentication/configuration/GetIdToken';
import { take, call, put, select, takeLatest } from 'redux-saga/effects';
import { request } from 'utils/request';
import { syntheticMonitoringActions as actions } from '.';
import { SyntheticMonitoringErrorType } from './types';
import { requestNew } from 'utils/requestWithError';
import { REACT_APP_APP_TESTING_BASE_URL } from 'utils/enviroment';
const BASE_URL =  REACT_APP_APP_TESTING_BASE_URL;

function* getProjectDataSaga() {
  const idToken = GetIdToken();
  const requestURL = `${BASE_URL}/project`;
  try {
    const response = yield call(request, requestURL);
    ////console.log(response)
    if (Array.isArray(response)) {
      yield put(actions.projectDataLoaded(response));
    } else {
      yield put(
        actions.projectDataError(SyntheticMonitoringErrorType.TEST_NOT_FOUND),
      );
    }
  } catch (e) {
    yield put(
      actions.projectDataError(SyntheticMonitoringErrorType.RESPONSE_ERROR),
    );
  }
}
function* getKpiDataSaga() {
  const idToken = GetIdToken();
  const requestURL = `${BASE_URL}/monitoring/list/kpi`;
  const config = {
    method: 'GET',
    headers: {
      'Content-type': 'application/json',
      Authorization: `Bearer ${idToken}`,
    },
  };
  try {
    const response = yield call(request, requestURL , config);
    //console.log(response);
    if (response.hasOwnProperty('body')) {
      yield put(actions.kpiDataLoaded(response.body));
    } else {
      yield put(
        actions.kpiDataLoadedError(SyntheticMonitoringErrorType.TEST_NOT_FOUND),
      );
    }
  } catch (e) {
    yield put(
      actions.kpiDataLoadedError(SyntheticMonitoringErrorType.RESPONSE_ERROR),
    );
  }
}
