function* createProjectSaga(param) {
  //console.log(param);
  const idToken = GetIdToken();
  const requestURL = `${BASE_URL}/project`;
  const payload = {
    method: 'POST',
    headers: {
      'Content-type': 'application/json',
    },
    body: JSON.stringify(param.payload),
  };

  try {
    const response = yield call(request, requestURL, payload);
    //console.log(response);
    if (Object.keys(response).length > 0) {
      yield put(actions.projectCreatedResponse(response));
    } else {
      yield put(
        actions.createProjectError(SyntheticMonitoringErrorType.TEST_NOT_FOUND),
      );
    }
  } catch (e) {
    yield put(
      actions.createProjectError(SyntheticMonitoringErrorType.EXIST_ERROR),
    );
  }
}
