function* deleteItemSaga(param){
  const idToken = GetIdToken();
  const requestURL = `${BASE_URL}/monitoring/delete/${param.payload.name}?id=${param.payload.id}`;
  const payload = {
    method: 'DELETE',
    headers: {
      'Content-type': 'application/json',
      Authorization: `Bearer ${idToken}`,
    },
  };

  try {
    // const response = yield call(request, requestURL, payload);
    // console.log(response);
    const response = yield call(requestNew, requestURL, payload);
    //const response = yield result.json();
    if (response.hasOwnProperty('body')) {
      yield put(actions.itemDeleted(response));
    } else {
      yield put(
        actions.itemDeleteError(SyntheticMonitoringErrorType.TEST_NOT_FOUND),
      );
    }
  } catch (e) {
    yield put(
      actions.itemDeleteError(SyntheticMonitoringErrorType.EXIST_ERROR),
    );
  }
}
function* fetchItemSaga(param){
  const idToken = GetIdToken();
  const requestURL = `${BASE_URL}/monitoring/list/${param.payload.name}?id=${param.payload.id}`;
  const payload = {
    method: 'GET',
    headers: {
      'Content-type': 'application/json',
      Authorization: `Bearer ${idToken}`,
    },
  };

  try {
    const response = yield call(requestNew, requestURL, payload);
    if (response.hasOwnProperty('body')) {
      yield put(actions.itemUpdatedFetch(response));
      if (param.payload.name === 'journey') {
        yield put(actions.createJourneyPayload(response.body));
      }else{
        yield put(actions.KpiEditPayload(response.body));
      }
     
    } else {
      yield put(
        actions.itemUpdatedFetchError(SyntheticMonitoringErrorType.TEST_NOT_FOUND),
      );
    }
  } catch (e) {
    yield put(
      actions.itemUpdatedFetchError(SyntheticMonitoringErrorType.EXIST_ERROR),
    );
  }
}
export function* syntheticMonitoringSaga() {
  yield takeLatest(actions.loadProjectData.type, getProjectDataSaga);
  yield takeLatest(actions.loadcreateProject.type, createProjectSaga);
  yield takeLatest(actions.loadKpiListData.type, getKpiDataSaga);
  yield takeLatest(actions.loadJourneyListData.type, getJourneyDataSaga);
  yield takeLatest(actions.loadEventListData.type, getEventDataSaga);
  yield takeLatest(actions.loadCreateJourney.type, createJourneySaga);
  yield takeLatest(actions.loadUpdateJourney.type, updateJourneySaga);
  yield takeLatest(actions.loadUpdateKpi.type, updateKpiSaga);
  yield takeLatest(actions.loadUpdateEvent.type, updateEventSaga);
  yield takeLatest(actions.loadCreateKpi.type, createKpiSaga);
  yield takeLatest(actions.loadCreateEvent.type, createEventSaga);
  yield takeLatest(actions.loadDeleteItem.type, deleteItemSaga);
  yield takeLatest(actions.loadUpdateFetchItem.type, fetchItemSaga);
}
