import { Flex } from '@chakra-ui/react'
import React from 'react'
import CardTitleComponent from './CardTitleComponent'

function AddKpiList({kpiPayload , isEdit , width}) {
  return (
    <Flex alignItems="center" width={width || "1000px"}>
    <Flex
      alignItems="center"
      padding="2px"
      width="100%"
      css={{
        '&::-webkit-scrollbar': {
          height: '6px',
          //height: '30px',
          border: '5px solid #F3F6F9',
        },
        '&::-webkit-scrollbar-track': {
          boxShadow: 'inset 0 0 6px white',
        },
        '&::-webkit-scrollbar-thumb': {
          backgroundColor: '#C1C1C1',
          borderRadius: '5px',
          boreder: '2px solid',
        },
      }}
      overflow="auto"
    >
      {kpiPayload.length > 0 && (
        <>
          {kpiPayload.map((item, index) => (
            <CardTitleComponent key={index} type="KPI" kpiData={item} isEdit={isEdit}></CardTitleComponent>
          ))}
        </>
      )}
    </Flex>
  </Flex>
  )
}

export default AddKpiList