import React, { createContext, useContext } from 'react';
import { useLocation } from 'react-router-dom';

import { getLocationSplits } from 'app/utils';

interface TestingContextType {
  testingType: string;
  featureRoute: string;
  fullPath: string;
}

const TestingContext = createContext<TestingContextType | undefined>(undefined);

export const useTestingContext = () => {
  const context = useContext(TestingContext);
  if (context === undefined) {
    throw new Error(
      'useTestingContext must be used within a TestingContextProvider',
    );
  }
  return context;
};

export const TestingContextProvider: React.FC = ({ children }) => {
  const location = useLocation();
  const { testingType, featureRoute, fullPath } = getLocationSplits(location);

  return (
    <TestingContext.Provider value={{ testingType, featureRoute, fullPath }}>
      {children}
    </TestingContext.Provider>
  );
};
