import { Flex, HStack, Icon, Text, Spacer } from '@chakra-ui/react';
import React from 'react';
import { BsGrid, BsImage, BsTrash } from 'react-icons/bs';
import { FiDownload } from 'react-icons/fi';
import IconComponent from './IconComponent';

const ActionComponent = ({
  selectedItems,
  deleteItemHandler,
  downloadItemHandler,
}) => {
  const itemCount = selectedItems.length;
  return (
    <HStack padding="4px 2px" width="100%">
      {itemCount && <Text>{itemCount} selected</Text>}
      <Spacer></Spacer>
      <HStack spacing="10px">
        <IconComponent onClick={downloadItemHandler} icon={FiDownload} />
        <IconComponent onClick={deleteItemHandler} icon={BsTrash} />
        <IconComponent icon={BsGrid} />
        <IconComponent icon={BsImage} />
      </HStack>
    </HStack>
  );
};

export default ActionComponent;
