// @ts-nocheck
import {
  Avatar,
  Badge,
  Box,
  Divider,
  Flex,
  HStack,
  Stack,
  Text,
} from '@chakra-ui/react';
import {
  selectFeatureFlagConfigData,
  selectUserRole,
} from 'Authentication/slice/selectors';
import React from 'react';
import { useSelector } from 'react-redux';
import { Link } from 'react-router-dom';
import ROUTES from '../../../constants/Routes';

function UserInfoCard({
  userHandler,
  username,
  userRole,
  logout,
  RedirectPage,
}) {
  const features = useSelector(selectFeatureFlagConfigData);
  const userRoleType = userRole; // useSelector(selectUserRole);
  return (
    <Stack
      width="100%"
      padding="15px"
      fontWeight="500"
      fontFamily="Montserrat"
      color="#3F4254"
      borderRadius="5px"
    >
      <HStack>
        <Avatar src="#" />
        <Box ml="3">
          <Text
            fontSize="12px"
            width="190px"
            overflow="hidden"
            whiteSpace="nowrap"
            textOverflow="ellipsis"
            fontWeight="600"
          >
            {username}
          </Text>
          <Badge ml="1" colorScheme="green">
            {userRole}
          </Badge>
          <Text textTransform="none" fontSize="sm"></Text>
        </Box>
      </HStack>

      <>
        <Divider colorScheme="grey" />
        <Stack padding="5px 10px" borderRadius="5px">
          <Text fontWeight="600">Settings</Text>
          {userRoleType === 'admin' && (
            <Link to={`${ROUTES.SETTINGS}`}>
              <Text paddingLeft="10px" color="blue1">
                Manage User
              </Text>
            </Link>
          )}
          <Link to={`${ROUTES.MANAGE_ACCESS}`}>
            <Text paddingLeft="10px" color="blue1">
              Manage Access
            </Text>
          </Link>
          {features.features.enable['user-management-authentication-2fa'] && (
            <Link to={`${ROUTES.ENABLE_MFA}`}>
              <Text paddingLeft="10px" color="blue1">
                Enable 2FA
              </Text>
            </Link>
          )}
        </Stack>
      </>

      <Divider colorScheme="grey" />
      <HStack padding="0px 10px">
        <Text
          cursor="pointer"
          color="blue1"
          onClick={() => {
            logout();
          }}
        >
          Logout
        </Text>
      </HStack>
    </Stack>
  );
}

export default UserInfoCard;
