// @ts-nocheck
import { Box, Container, Flex, Img, Text } from '@chakra-ui/react';
import React, { useEffect } from 'react';
import { Icon } from '@chakra-ui/icons';
import { NavLink, useNavigate, useLocation } from 'react-router-dom';
import { FiTv } from 'react-icons/fi';
import { FiDatabase } from 'react-icons/fi';
import { BsBarChartLine, BsJournalPlus } from 'react-icons/bs';

import { MdMobileFriendly, MdOutlineAnalytics } from 'react-icons/md';
import { RiFileListFill, RiFileSettingsLine } from 'react-icons/ri';
import { CgDebug } from 'react-icons/cg';
import { RiDeviceLine } from 'react-icons/ri';
import { AiFillSchedule, AiOutlineSetting } from 'react-icons/ai';
import { AiOutlineCloud } from 'react-icons/ai';
import { useState } from 'react';
import SidebarTabComp from '../SidebarTabComp';
import { useSelector } from 'react-redux';
import {
  selectFeatureFlagConfigData,
  selectUserRole,
} from 'Authentication/slice/selectors';
import { BiMobile } from 'react-icons/bi';
import { products } from 'app/productConfig';
import ROUTES from '../../constants/Routes';
import ArrowExpand from './ArrowExpand';

const getIcon = iconName => {
  let Icon = null;
  switch (iconName) {
    case 'MdOutlineAnalytics':
      Icon = MdOutlineAnalytics;
      break;
    case 'RiFileSettingsLine':
      Icon = RiFileSettingsLine;
      break;
    case 'RiDeviceLine':
      Icon = RiDeviceLine;
      break;
    case 'CgDebug':
      Icon = CgDebug;
      break;
    case 'AiOutlineCloud':
      Icon = AiOutlineCloud;
      break;
    case 'AiFillSchedule':
      Icon = AiFillSchedule;
      break;
    case 'BsJournalPlus':
      Icon = BsJournalPlus;
      break;
    default:
      Icon = MdOutlineAnalytics;
  }
  return Icon;
};

function DynamicSidebar({ handleSidebar, sidebarToggle }) {
  const [hide, setHide] = useState(false);

  const [getFeature, setFeatures] = useState([]);
  const location = useLocation();
  const pathsWithoutSidebar = [
    ROUTES.MANAGE_ACCESS,
    ROUTES.SETTINGS,
    ROUTES.ENABLE_MFA,
    ROUTES.LANDING_PAGE,
  ];
  useEffect(() => {
    const pathname = location.pathname;
    if (pathsWithoutSidebar.includes(pathname)) {
      setHide(true);
    } else {
      setHide(false);
    }

    const featureItem = products.find(product =>
      pathname.startsWith(product.link),
    );
    if (featureItem) {
      setFeatures(featureItem.features);
    }
  }, [location, pathsWithoutSidebar]);

  const sideShow = {
    width: '63px',
    border: '1px solid #EDEDED',
    transition: 'ease-in-out',
    fontWeight: '300',
  };
  const sideShowActive = {
    ...sideShow,
    width: '200px',
    fontWeight: '500',
  };

  if (hide) {
    return null;
  }

  return (
    <Flex
      fontFamily="Montserrat"
      height="calc(100vh - 60px)"
      fontSize="14px"
      background="grey13"
      style={sidebarToggle ? sideShowActive : sideShow}
      className="sidebarShow "
      zIndex="10"
      flexDirection="column"
      position="absolute"
      left="0px"
      top="60px"
    >
      <Flex height="60px" alignItems="center" justifyContent="flex-end">
        <ArrowExpand
          sidebarToggle={sidebarToggle}
          handleSidebar={handleSidebar}
        />
      </Flex>
      {getFeature.map((tab, index) => (
        <SidebarTabComp
          key={index}
          icon={<Icon as={getIcon(tab.icon)} w="24px" h="24px"></Icon>}
          link={tab.itemLink}
          sidebarToggle={sidebarToggle}
        >
          {tab.itemName}
        </SidebarTabComp>
      ))}
    </Flex>
  );
}

export default DynamicSidebar;
