/**
 *
 * App
 *
 * This component is the skeleton around the actual pages, and should only
 * contain code that should be seen on all pages. (e.g. navigation bar)
 */

import * as React from 'react';
import { useEffect, useRef, useState } from 'react';
import { Helmet } from 'react-helmet-async';
import './App.css';
import { GlobalStyle } from 'styles/global-styles';
import { useTranslation } from 'react-i18next';
import { ErrorBoundary } from 'react-error-boundary';
import { Flex } from '@chakra-ui/react';
import { BrowserRouter, Routes, Route, useNavigate } from 'react-router-dom';
import ROUTES from 'app/constants/Routes';
import ROUTE_CONFIG from './routeConfig';
import idleTimeDetector from 'utils/idleTimeDetector';
import ModalWithTimer from 'app/components/ModalWithTimer';
import { useAuth } from 'utils/authentication/useAuth';
import LogRocket from 'logrocket';
import setupLogRocketReact from 'logrocket-react';

import { TestingContextProvider } from 'app/hooks';

//import { selectLoading, selectUserAuth, selectUserRole } from 'Authentication/slice/selectors';

export function App() {
  const { i18n } = useTranslation();
  const [isIdle, setIsIdle] = useState(false);
  const idleTimeDetectorRef = useRef<any>();

  const { logout, isAuthenticated } = useAuth();

  useEffect(() => {
    LogRocket.init('mozark-pte/app-testing');

    setupLogRocketReact(LogRocket);

    LogRocket.identify('mozark-pte/app-testing', {
      name: 'John Doe',
      email: 'johndoe@example.com',
      subscriptionType: 'pro',
    });
  }, []);

  const handleIdle = () => {
    setIsIdle(true);
    idleTimeDetectorRef.current.stopTracking();
  };

  const handleExit = () => {
    console.log('handleExit triggered');
    logout();
    /* const sessionId = localStorage.getItem('sessionIdForDeviceInteract'); */
    /* if (sessionId) { */
    /* setShowLoader(true); */
    /* stopExistingSession(); */
    /* setTimeout(() => { */
    /*   window.location.replace(ROUTES.LOGOUT_PAGE); */
    /* }, 5000); */
    /* } */
    /* setIsIdle(false); */
  };

  const handleContinue = () => {
    idleTimeDetectorRef.current.stopTracking();
    idleTimeDetectorRef.current = idleTimeDetector(30);
    idleTimeDetectorRef.current.startTracking(handleIdle);
    setIsIdle(false);
  };

  useEffect(() => {
    if (isAuthenticated) {
      idleTimeDetectorRef.current = idleTimeDetector(30);
      idleTimeDetectorRef.current.startTracking(handleIdle);

      return () => {
        idleTimeDetectorRef.current.stopTracking();
      };
    }
  }, [isAuthenticated]);

  return (
    <>
      <ErrorBoundary
        fallbackRender={({ error, resetErrorBoundary }) => (
          <div role="alert">
            <div>Oh no</div>
            <pre>{error.message}</pre>
            <button
              onClick={() => {
                resetErrorBoundary();
              }}
            >
              Try again
            </button>
          </div>
        )}
      >
        <BrowserRouter>
          <Helmet
            titleTemplate="%s -App Testing & Monitoring"
            defaultTitle="App Testing & Monitoring"
            htmlAttributes={{ lang: i18n.language }}
          >
            <meta name="description" content="A App Analytics application" />
          </Helmet>
          <Flex>
            <TestingContextProvider>
              <ModalWithTimer
                isOpen={isIdle}
                onClose={() => {}}
                onExit={handleExit}
                onContinue={handleContinue}
              />
              <Routes>
                {ROUTE_CONFIG.map(route => (
                  <Route
                    exact
                    key={route.path}
                    path={route.path}
                    element={route.element}
                  >
                    {route.children &&
                      route.children.map(child => (
                        <Route
                          key={child.path}
                          path={child.path}
                          element={child.element}
                        />
                      ))}
                  </Route>
                ))}
              </Routes>
            </TestingContextProvider>
          </Flex>
          <GlobalStyle />
        </BrowserRouter>
      </ErrorBoundary>
    </>
  );
}
