import { useEffect, useRef } from 'react';
import TIME_CONSTANTS from '../constants/time';
import { useInteractSlice } from '.';

const useLoadDevices = (repeat = false) => {
  const { actions, selector, dispatch } = useInteractSlice();
  const { devicesLoading, devices, devicesError, deviceTimestamp } = selector;
  const lastTimestampRef = useRef();
  lastTimestampRef.current = deviceTimestamp;
  const isGreaterThanDelay = () => {
    if (repeat) {
      const delay =
        new Date().valueOf() -
        (lastTimestampRef.current +
          TIME_CONSTANTS.DEVICE_STATUS_UPDATE_TIME_DELAY);

      return delay > 0;
    }
  };

  useEffect(() => {
    const loadDevices = () => {
      dispatch(actions.loadDevices({}));
      if (repeat) {
        dispatch(actions.updateDeviceTimestamp(new Date().valueOf()));
      }
    };
    console.log(' effect body', isGreaterThanDelay());
    if (!Object.keys(devices).length || isGreaterThanDelay()) {
      loadDevices();
    }

    let intervalId;
    // Set up the interval to call the saga every 15 seconds
    if (repeat) {
      intervalId = setInterval(() => {
        loadDevices();
      }, 15000);
    }
    return () => {
      intervalId && clearInterval(intervalId);
    };
  }, []);

  return { devicesLoading, devices, devicesError };
};

export default useLoadDevices;
