import { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { Flex } from '@chakra-ui/react';

import {
  controllerStopMessage,
  getControllerMessageParameters,
} from '../../utils';

import { NoContent } from 'app/components';
import { ListTable, Loader } from '../../components';
import { useLoadDevices, useInteractSlice } from '../../hooks';
import StyledScrollableContainer from 'app/components/StyledScrollableContainer';

const ListDevices = () => {
  const [showLoader, setShowLoader] = useState(false);
  const [availableDevices, setAvailableDevices] = useState(false);

  const navigate = useNavigate();
  const {
    interactType,
    actions,
    selector: { devicesLoading, devices },
    dispatch,
    route,
  } = useInteractSlice();

  useLoadDevices(true);

  const stopRemote = async (event, device) => {
    event?.stopPropagation();
    setShowLoader(true);
    const serial = device.serial;
    await controllerStopMessage(device, interactType, serial);
    console.log('Stopping Session');
    dispatch(
      actions.stopSession({
        deviceId: serial,
      }),
    );

    setTimeout(() => {
      setShowLoader(false);
      navigate(route);
    }, 1000);
  };

  const selectDevice = device => {
    setShowLoader(true);
    navigate(`${route}/${device.serial}`);
  };

  useEffect(() => {
    dispatch(actions.exitSession(false));

    setAvailableDevices(
      devices.filter(
        item =>
          item.deviceParameters.hasOwnProperty('deviceStatus') &&
          item.deviceParameters.deviceStatus !== 'unavailable',
      ),
    );
  }, [devices]);

  const listAvailableDevices = () =>
    /* @ts-ignore */
    availableDevices.length ? (
      <ListTable
        items={availableDevices}
        selectItem={selectDevice}
        stopAction={stopRemote}
      />
    ) : (
      /* @ts-ignore */
      <NoContent message="No devices found" />
    );

  return (
    <StyledScrollableContainer isLabels={true}>
      {listAvailableDevices()}
      <Loader show={devicesLoading || showLoader} overlay />
    </StyledScrollableContainer>
  );
};

export default ListDevices;
