//@ts-nocheck
import React from 'react';
import { CloseButton, Flex, HStack, Stack, Text } from '@chakra-ui/react';
import { useDispatch } from 'react-redux';
import { useSyntheticMonitoringSlice } from '../../slice';
function CardTitleComponent({ kpiData, type , isEdit }) {
  const dispatch = useDispatch();
  const { actions } = useSyntheticMonitoringSlice();
  const deleteKpiHandler = kpiName => {
     if(isEdit){
      dispatch(actions.removeItemPayload(kpiName));
     }else{
      dispatch(actions.removeKpiItem(kpiName));
     }

  };
 
  return (
    <>
      <Flex
        align="center"
        minWidth="300px"
        justify="space-between"
        flexDirection="row"
        //h="100px"
        padding="5px 7px"
        background="white"
        _hover={{ background: 'grey14' }}
        border="1px solid"
        borderColor="grey14"
        borderRadius="4px"
        boxShadow="0px 1px 2px #00000029"
        //position="relative"
        //zIndex="0"
        margin="2px"
      >
        <Stack
          cursor="pointer"
          flex={1}
          display="flex"
          //title="Update"
          //onClick={e => editTrayHandler(itemData)}
        >
          <HStack>
            <Text whiteSpace="nowrap">{type} -</Text>
            <Text
               width="180px"
               textOverflow="ellipsis"
               overflow="hidden"
               whiteSpace="nowrap"
              title={kpiData.name}
            >{kpiData.name}</Text>
          </HStack>
          {type == 'KPI' &&  <HStack>
            <Text whiteSpace="nowrap">Type -</Text>
            <Text>{kpiData.type}</Text>
          </HStack> }
          {kpiData.hasOwnProperty('event') &&
          (<HStack>
            <Text whiteSpace="nowrap">Events -</Text>
            <Text
              width="180px"
              textOverflow="ellipsis"
              overflow="hidden"
              whiteSpace="nowrap"
             title={kpiData.event.map( event => event.name )}
            >
            {kpiData.event.map( event => event.name).join(', ')}
            </Text>
          </HStack>)}
          {(kpiData.hasOwnProperty('operation') && kpiData.operation) &&
          (<HStack>
            <Text whiteSpace="nowrap">Operation -</Text>
            <Text
              width="150px"
              textOverflow="ellipsis"
              overflow="hidden"
              whiteSpace="nowrap"
             title={kpiData.operation}
            >
            {kpiData.operation}
            </Text>
          </HStack>)}
        </Stack>
        <CloseButton
          onClick={e => deleteKpiHandler(kpiData.name)}
        ></CloseButton>
      </Flex>
    </>
  );
}

export default CardTitleComponent;
