import React, {CSSProperties} from 'react';
import {Button} from '@chakra-ui/react';

interface propsType {
  logo?: any;
  color?: string;
  children: String;
  buttonHandler?: any;
  padding?: string;
  width?: string;
  height?: string;
  borderColor?: string;
  backgroundColor?: string;
  marginLeft?: string;
  textTransform?: any;
  fontWeight?: number | string;
  focus?: string;
  fontSize?: string;
  isDisabled?: boolean;
  name?: string;
  style?: CSSProperties
}

function ButtonComp({
  logo,
  color,
  children,
  buttonHandler,
  padding,
  width,
  height,
  borderColor,
  backgroundColor,
  marginLeft,
  name,
  textTransform,
  fontWeight,
  focus,
  fontSize,
  isDisabled,
                      style
}: propsType) {
  return (
    <>
      <Button
          style={style}
        fontFamily="Montserrat"
        data-button-id={name}
        isDisabled={isDisabled}
        backgroundColor={backgroundColor || 'white3'}
        leftIcon={logo}
        textTransform={textTransform || 'uppercase'}
        border="1px solid"
        cursor="pointer"
        onClick={buttonHandler}
        textColor={color || 'blue1'}
        size="6"
        height={height}
        fontSize={fontSize || '14px'}
        borderColor={borderColor || 'transparent'}
        fontWeight={fontWeight || '600'}
        borderRadius="5"
        margin="1.5"
        _hover={{ backgroundColor: 'none' }}
        _focus={
          focus
            ? { boxShadow: ' 0px 0px 6px #008FE0' }
            : { boxShadow: '0px 0px 0px transparent' }
        }
        marginLeft={marginLeft}
        padding={padding || '.6em 0px'}
        w={width}
      >
        {children}
      </Button>
    </>
  );
}

export default ButtonComp;
