import {
  getCurrentDateTimeString,
  isTimeNotExceedingMaxSessionTime,
  getCurrentDateTimeFromString,
} from 'app/utils';
import { LOCAL_STORAGE_KEYS } from 'app/constants';

import { getParsedLocalStorageItem, setStringifiedLocalStorageItem } from '.';

export const saveSessionInLocalStorage = (
  interactType: string,
  deviceId: string,
  sessionId: string,
) => {
  const localStorageKey = LOCAL_STORAGE_KEYS.session(interactType);
  const deviceSessionPairWithTime = {
    [deviceId]: {
      sessionId,
      timestamp: getCurrentDateTimeString(),
    },
  };

  let storedPairs = getParsedLocalStorageItem(localStorageKey) || {};
  storedPairs = {
    ...storedPairs,
    ...deviceSessionPairWithTime,
  };

  setStringifiedLocalStorageItem(localStorageKey, storedPairs);
};

export const removeSessionFromLocalStorage = (
  interactType: string,
  keyToRemove: string,
) => {
  let sessionId = '';

  const localStorageKey = LOCAL_STORAGE_KEYS.session(interactType);
  const storedPairs = getParsedLocalStorageItem(localStorageKey);

  if (storedPairs && storedPairs.hasOwnProperty(keyToRemove)) {
    sessionId = storedPairs[keyToRemove].sessionId;
    const updatedPairs = { ...storedPairs };
    delete updatedPairs[keyToRemove];

    setStringifiedLocalStorageItem(localStorageKey, updatedPairs);
  }

  return sessionId;
};

export const getSavedSessionId = (interactType: string, deviceId: string) => {
  const localStorageKey = LOCAL_STORAGE_KEYS.session(interactType);
  const sessionDetails = getParsedLocalStorageItem(localStorageKey);
  let savedSessionId: number | string | null = null;
  if (sessionDetails) {
    const deviceValues = sessionDetails[deviceId];
    if (
      deviceValues &&
      isTimeNotExceedingMaxSessionTime(
        getCurrentDateTimeFromString(deviceValues.timestamp),
      )
    ) {
      savedSessionId = sessionDetails[deviceId].sessionId;
    }
  }
  return savedSessionId;
};
