import { useDispatch, useSelector } from 'react-redux';

import { useOutletContext, useLocation } from 'react-router-dom';
import { getEmailId } from 'Authentication/configuration/GetEmail';

import { getConfig } from '../config';


import { getInteractType } from '../utils';

const useInteractSlice = () => {
  const { testingType } = useOutletContext<any>();
  const location = useLocation();
  const dispatch = useDispatch();

  const interactType = getInteractType(testingType);
  const config = getConfig(interactType);
  const { actions } = config.slice();
  const { actions: globalActions } = config.globalSlice();

  const selector: any = useSelector(config.selector.selectAll);
  const globalSelector: any = useSelector(config.globalSelector);
  const controllerDetails: any = useSelector(state =>
    config.selector.selectControllerDetails(
      state,
      interactType,
      location.pathname,
    ),
  );
  const device: any = useSelector(state =>
    config.selector.selectSelectedDevice(
      state,
      interactType,
      location.pathname,
    ),
  );

  return {
    interactType,
    actions,
    selector,
    globalSelector,
    controllerDetails,
    device,
    dispatch,
    route: config.route,
    globalActions,
    getEmailId,
  };
};

export default useInteractSlice;
