import React, { createContext, useContext, useState, useEffect } from 'react';
import { getIdToken } from './getUserIdToken';
import { getRoleName } from './getUseRolename';
import { getUserSignOut } from './getUserSignout';
import ROUTES from 'app/constants/Routes';
import { REACT_APP_LOGOUT_REDIRECTPAGE } from 'utils/enviroment';

const RedirectPage = REACT_APP_LOGOUT_REDIRECTPAGE;

interface AuthContextInterface {
  isAuthenticated: boolean;
  userRole: string;
  login: (role: string) => void;
  logout: () => void;
  isFirstLogin: boolean;
  cognitoUser: any;
}

export const AuthContext = createContext<AuthContextInterface | null>(null);

export const AuthProvider = ({ children }: { children: React.ReactNode }) => {
  const AUTH_STORAGE_KEY = 'auth';
  const [isAuthenticated, setIsAuthenticated] = useState<boolean>(false);
  const [userRole, setUserRole] = useState<string>('');
  const [cognitoUser, setCognitoUser] = useState();
  const [isFirstLogin, setIsFirstLogin] = useState(false);
  // Load authentication state from localStorage on initial render
  useEffect(() => {
    setTimeout(() => {
      const token = getIdToken(); //localStorage.getItem(AUTH_STORAGE_KEY);
      const userRole = getRoleName();
      if (token && userRole) {
        setIsAuthenticated(true);
        setUserRole(userRole);
      }
    }, 0);
  }, []);

  // Save authentication state to localStorage when it changes
  // useEffect(() => {
  //   localStorage.setItem(
  //     AUTH_STORAGE_KEY,
  //     JSON.stringify({ isAuthenticated, userRole }),
  //   );
  // }, [isAuthenticated, userRole]);

  const login = (data: any, authuser: any, type: string) => {
    const userRoleType = getRoleName();
    setCognitoUser(authuser);
    if (type === 'changepassword') {
      setIsFirstLogin(true);

      // setIsAuthenticated(true);
      // setUserRole(userRole);
    } else {
      setIsAuthenticated(true);
      setUserRole(userRoleType);
    }
  };

  const logout = () => {
    //localStorage.removeItem(AUTH_STORAGE_KEY);
    getUserSignOut();
    setIsAuthenticated(false);
    setUserRole('');
    window.location.href = RedirectPage;
  };
  const value = {
    isAuthenticated,
    userRole,
    login,
    logout,
    isFirstLogin,
    cognitoUser,
  };
  return <AuthContext.Provider value={value}>{children}</AuthContext.Provider>;
};

export const useAuth = (): AuthContextInterface => {
  const auth = useContext<AuthContextInterface | null>(AuthContext);
  if (!auth) {
    throw new Error('useAuth must be used within an AuthProvider');
  }
  return auth;
};
