import {
  StyledTabs,
  StyledTabList,
  StyledTab,
  StyledTabPanels,
  StyledTabPanel,
} from './styles';

const SidebarTabs = ({ itemList }) => {
  const listofTabs = itemList.map(item => item.tab);
  const listofComponents = itemList.map(item => item.component);

  return (
    <StyledTabs orientation="vertical">
      <StyledTabList>
        {listofTabs.map((item, index) => (
          <StyledTab key={index}>{item.icon}</StyledTab>
        ))}
      </StyledTabList>
      <StyledTabPanels>
        {listofComponents.map((item, index) => (
          <StyledTabPanel key={index}>{item}</StyledTabPanel>
        ))}
      </StyledTabPanels>
    </StyledTabs>
  );
};

export default SidebarTabs;
