import { call, put, select } from 'redux-saga/effects';

import { allActions } from '..';
import { request, postRequestConfig } from 'utils/request';
import { generatePresignedUrl } from '../urls';

import getAction from '../utils/getAction';
import {
  configForImageUpload,
  getBuffer,
  getSignedUrlPayload,
} from '../utils/screenshotUpload';
import { REACT_APP_APP_TESTING_BASE_URL } from 'utils/enviroment';
import GetIdToken from 'Authentication/configuration/GetIdToken';

function* deleteScreenshot(action: any) {
  const { screenIds, type } = action.payload;
  const BASE_URL = REACT_APP_APP_TESTING_BASE_URL;
  const actionName = getAction(type);
  const actions = allActions[actionName];
  const idToken = GetIdToken();
  const sessionId = yield select(state => state?.[type]?.session);

  const requestUrl = `${BASE_URL}/testexecute/files/screenshot?testId=${sessionId}`;
  const config = {
    method: 'DELETE',
    headers: {
      'Content-type': 'application/json',
      Authorization: `Bearer ${idToken}`,
    },
    body: JSON.stringify({ image: screenIds }),
  };
  try {
    // delete
    const deleteResponse = yield call(request, requestUrl, config);

    if (deleteResponse.status === 200) {
      yield put(
        actions.screenshotDeleted({
          status: 200,
          //fileconfig: { ...fileconfig, capturedImage },
          message: 'screenshots deleteed',
        }),
      );
    } else {
      yield put(actions.screenshotDeletError('error deleteing screenshot'));
    }
  } catch (e) {
    console.log(e);
    yield put(
      actions.screenshotDeleteError('error deleteing screenshot final', e),
    );
  }
}

export default deleteScreenshot;
