import {
  CloseButton,
  Flex,
  HStack,
  Spacer,
  Stack,
  Text,
} from '@chakra-ui/react';
import React from 'react';
import RowElement from '../RowElement';

function CardRow({ kpiData }) {

  return (
    <>
      <Flex
        align="center"
        width="100%"
        justify="space-between"
        flexDirection="row"
        //  h="100px"
        padding="5px 7px"
        background="white"
        _hover={{ background: 'grey14' }}
        border="1px solid"
        borderColor="grey14"
        borderRadius="4px"
        boxShadow="0px 1px 2px #00000029"
        //position="relative"
        //zIndex="0"
        margin="2px"
      >
        <Stack
          cursor="pointer"
          flex={1}
          display="flex"
          //title="Update"
          //onClick={e => editTrayHandler(itemData)}
        >
          <RowElement
            name={kpiData.name}
            element="KPI"
            type={kpiData.type}
          ></RowElement>
          {kpiData.event.length > 0 &&
            kpiData.event.map((event, index) => (
              <RowElement
                name={event.name}
                element={ event.sequenceId === 1 ? 'Start Event' : 'End Event' }
                key={index}
                //type={event.type}
              ></RowElement>
            ))}
            {kpiData.operation && <RowElement
            name={kpiData.operation}
            element="Operation"
       
          ></RowElement>}
        </Stack>
        {/* <CloseButton
         onClick={e => deleteTrayHandler(itemData.trayID)}
        ></CloseButton> */}
      </Flex>
    </>
  );
}

export default CardRow;
