// @ts-nocheck
import { Flex, Input, Progress } from '@chakra-ui/react';
import DashedButton from 'app/components/DashedButton';
import React, { useRef } from 'react';

function UploadButton({
  placeholder,
  uploadButtonHandler,
  validate,
  progressValue,
  enableProgress,
  width,
  fileType,
}) {
  const fileUploadRef = useRef(null);
  const uploadFileHandler = () => {
    fileUploadRef.current.click();
    fileUploadRef.current.value = '';
  };

  return (
    <Flex flexDirection="column">
      <DashedButton
        height={enableProgress ? '29px' : '34px'}
        width={width || '230px'}
        buttonHandler={uploadFileHandler}
        isBorderBottom={enableProgress}
      >
        {placeholder}
        <Input
          data-input-file={placeholder}
          ref={fileUploadRef}
          onChange={e => uploadButtonHandler(e.target, fileType)}
          type="file"
          display="none"
          width={width || '230px'}
          height="39px"
          disabled={enableProgress}
          //accept={validate === 'android' ? '.apk' || '.xapk' : '.ipa'}
          accept={validate ? validate : ''}
        ></Input>
      </DashedButton>
      {enableProgress && (
        <Progress
          // value={progressValue}
          isIndeterminate
          height="5px"
          padding="0px 1px"
          width={width || '230px'}
          borderRadius="3px"
        />
      )}
    </Flex>
  );
}

export default UploadButton;
