import { forwardRef } from 'react';
import {
  Icon,
  Flex,
  HStack,
  Select,
  Spacer,
  Stack,
  Text,
  useToast,
} from '@chakra-ui/react';

import RefreshIconButton from 'app/components/RefreshIconButton';
import ButtonComp from 'app/components/ButtonComp';
import LoadingComponent from 'app/components/LoadingComponent';
import InputComponent from 'app/pages/Appium/components/InputComponent';
import SelectComponent from 'app/pages/Appium/components/SelectComponent';
import UploadButton from 'app/pages/Appium/components/UploadButton';
import { useEffect, useState } from 'react';
import { REACT_APP_APP_TESTING_BASE_URL } from 'utils/enviroment';
import { useLoadDevices, useInteractSlice } from 'app/pages/Interact/hooks';
import { getSessionType, formatMessage } from 'app/pages/Interact/utils';
import { getConfig } from 'app/pages/Interact/config';
import Loader from '../../Loader';
import useReceivedMessageApp from 'app/pages/Interact/hooks/useReceivedMessageApp';
import { FaList } from 'react-icons/fa';
import IconButtonComponent from 'app/components/IconButtonComponent';
import { getAppList } from 'app/pages/Interact/slice/utils/getListApps';
import {
  getExtractFileExtension,
  getFileCategory,
  getFileExtension,
} from 'app/pages/Interact/utils/getFileCategory';
import SpinnerProgress from '../../SpinnerProgress';
import WebInspectorUrl from './WebInspectorUrl';

const LocalUpload = forwardRef(({}, ref) => {
  const [projectName, setProjectName] = useState('');
  const [selectProject, setSelectProject] = useState('');
  const [selectAppBuild, setSelectAppBuild] = useState();
  const [selectApp, setSelectApp] = useState();
  const [getApps, setApps] = useState([]);
  const [isInstalling, setIsInstalling] = useState(false);
  const [loadingWebinpector, setWebInspector] = useState(false);
  const [webinpectorUrl, setWebInspectorUrl] = useState('');
  const [statusMessage, setStatusMessage] = useState('');
  const [message, setMessage] = useState('');
  const toast = useToast();

  const {
    interactType: type,
    actions,
    dispatch,
    device,
    selector,
  } = useInteractSlice();
  useReceivedMessageApp(ref, setMessage);
  useLoadDevices();

  const { platform, deviceParameters, ...remain } = device !== null && device;
  const webInspectorPort = deviceParameters?.webInspectorPort;
  const fileCategory = getFileCategory(platform);

  const {
    appbuildsLoading,
    appbuilds,
    appbuildsError,
    projectsLoading,
    projects,
    projectsError,
    appbuildUploading,
    appbuildUpload,
    appbuildUploadError,
    createProjectLoading,
    createProject,
    createProjectLoadError,
  } = selector;

  console.log({ message });
  useEffect(() => {
    if (message && typeof message === 'string') {
      const getMessage = message.split(',').map(s => s.trim());
      if (getMessage[0] === 'upload.app') {
        setIsInstalling(false);
        setStatusMessage(getMessage[2]);
        setTimeout(() => {
          setIsInstalling(false);
          setStatusMessage('');
          setMessage('');
        }, 8000);
      }
      if (getMessage[0] === 'app.inspect') {
        const createUrl = `http://34.93.159.18:${webInspectorPort}/`;
        setWebInspectorUrl(createUrl);
      }
    }
    return () => {
      setIsInstalling(false);
      setWebInspector(false);
      setStatusMessage('');
      setMessage('');
    };
  }, [message]);

  useEffect(() => {
    const testType = getSessionType(type);

    const fileCategory = getFileCategory(platform);
    const param = {
      fileCategory,
      project: selectProject,
      fileStatus: false,
      testType,
      type,
    };
    dispatch(actions.appbuildLoading(param));
  }, [dispatch, selectProject, actions, type]);

  const sendAppInstallMessage = () => {
    if (
      selectAppBuild === 0 ||
      (selectAppBuild && appbuilds && appbuilds.length)
    ) {
      const brand = device?.brand;
      const url = appbuilds[selectAppBuild].meta.s3Url;
      let msg = ['upload.app', device.serial, { url, brand }];

      ref.current.sendMessage(formatMessage(msg));
      setStatusMessage('');
      setIsInstalling(true);
      setTimeout(() => setIsInstalling(false), 35000);
    }
  };

  const sendAppActionMessage = actionType => {
    if (!selectApp) {
      toast({
        title: 'Please select App',
        status: 'error',
        isClosable: true,
        position: 'bottom-right',
      });
      return;
    }
    if (ref && ref?.current && ref?.current?.sendMessage && selectApp) {
      // const app = '';
      if (actionType === 'inspect') {
        setWebInspector(true);
        const createUrl = `http://34.93.159.18:${webInspectorPort}/`;
        setTimeout(() => {
          // setWebInspectorUrl('http://34.93.159.18:8011/');
          setWebInspectorUrl(createUrl);
          setWebInspector(false);
        }, 20000);
      }
      const brand = device?.brand;
      const app = selectApp.replace(/[\n\u0000]/g, '');
      let msg = [`${actionType}.app`, device.serial, { app, brand }];
      console.log(msg, app);
      ref.current.sendMessage(formatMessage(msg));
    }
  };
  const sendAppListMessage = async () => {
    if (ref && ref?.current && ref?.current?.sendMessage) {
      const brand = device?.brand;
      let msg = ['list.packages', device.serial, { brand }];
      ref.current.sendMessage(formatMessage(msg));
    }
    setWebInspector(false);
    const apps = await getAppList(ref);
    console.log({ apps });
    setApps(apps);
  };

  // list projects
  useEffect(() => {
    const testType = ''; //getSessionType(type);
    const param = {
      testType,
      type,
    };
    dispatch(actions.projectLoading(param));
    sendAppListMessage();
  }, []);

  useEffect(() => {
    if (
      createProject.hasOwnProperty('message') &&
      createProjectLoadError !== 1
    ) {
      if (createProject.message === 'Success') {
        toast({
          title: 'Project Created Successfully',
          status: 'success',
          isClosable: true,
          position: 'bottom-right',
        });
        const testType = getSessionType(type);
        setProjectName('');
        const param = {
          testType,
          type,
        };
        dispatch(actions.projectLoading(param));
      } else {
        toast({
          title: createProject.message,
          status: 'error',
          isClosable: true,
          position: 'bottom-right',
        });
      }
      dispatch(actions.createProjectLoaded({}));
    }

    if (createProjectLoadError === 1) {
      toast({
        title: "Couldn't create the project",
        status: 'error',
        isClosable: true,
        position: 'bottom-right',
      });
      dispatch(actions.createProjectLoadError({}));
    }
  }, [createProject, createProjectLoadError, dispatch]);

  const createProjectHandler = () => {
    if (projectName === '') {
      toast({
        title: 'Please enter project name',
        status: 'error',
        isClosable: true,
        position: 'bottom-right',
      });

      return;
    }
    const pattern = /[^a-zA-Z0-9_-]/;
    if (projectName.match(pattern)) {
      toast({
        title: 'Please enter valid name',
        status: 'error',
        isClosable: true,
        position: 'bottom-right',
      });
      return;
    }
    const testType = getSessionType(type);
    const param = {
      name: projectName,
      testType,
      type,
    };
    dispatch(actions.createProjectLoading(param));
  };
  const appBuildUploadHandler = file => {
    const testType = getSessionType(type);
    if (!selectProject) {
      return toast({
        title: 'Please Select Project ',
        status: 'error',
        isClosable: true,
        position: 'bottom-right',
      });
    }

    const fileExtension = getExtractFileExtension(file);
    const extentions = getFileExtension(platform);

    const allowedExtensions = extentions.split(',').map(s => s.trim());

    if (!allowedExtensions.includes(fileExtension)) {
      return toast({
        title: `Please select ${allowedExtensions.join(', ')} file`,
        status: 'error',
        isClosable: true,
        position: 'bottom-right',
      });
    }
    const filerequestData = {
      filename: file.files[0].name,
      project: selectProject,
      fileCategory,
      testType,
      type,
      file: file.files[0],
    };

    dispatch(actions.uploadAppBuild(filerequestData));
  };

  const refreshList = () => {
    if (!selectProject) {
      toast({
        title: 'Please Select Project ',
        status: 'error',
        isClosable: true,
        position: 'bottom-right',
      });
      return undefined;
    }
    const testType = getSessionType(type);
    const fileCategory = getFileCategory(platform);
    const param = {
      fileCategory,
      project: selectProject,
      fileStatus: false,
      testType,
      type,
    };
    dispatch(actions.appbuildLoading(param));
  };

  useEffect(() => {
    if (
      appbuildUpload.hasOwnProperty('status') &&
      appbuildUpload.status === 200
    ) {
      toast({
        title: 'Application Uploaded Successfully',
        status: 'success',
        isClosable: true,
        position: 'bottom-right',
      });
      dispatch(actions.appbuildUploaded({}));
      const testType = getSessionType(type);

      const fileCategory = getFileCategory(platform);

      const param = {
        fileCategory,
        project: selectProject,
        fileStatus: false,
        testType,
        type,
      };
      dispatch(actions.appbuildLoading(param));
    }

    if (appbuildUploadError != null && appbuildUploadError) {
      toast({
        title: "Couldn't upload file",
        status: 'error',
        isClosable: true,
        position: 'bottom-right',
      });
      dispatch(actions.appbuildUploadError(null));
    }
  }, [appbuildUploadError, appbuildUpload, dispatch, actions]);

  return (
    <>
      {/* <Loader show={createProjectLoading || appbuildsLoading} overlay /> */}
      <Stack spacing={4} color="grey13">
        <HStack>
          <>
            <SelectComponent
              width="70%"
              name="xcui"
              type="project"
              selectValue={selectApp}
              setSelectValue={setSelectApp}
              selectOptions={getApps}
              title="Select App"
            />
            <RefreshIconButton clickHandler={sendAppListMessage} />
            {/* <IconButtonComponent
                clickHandler={sendAppOpenMessage}
                icon={<FaList />}
              ></IconButtonComponent> */}

            <Spacer />
          </>
        </HStack>
        <HStack spacing="5px">
          <ButtonComp
            buttonHandler={() => sendAppActionMessage('launch')}
            padding="7px 5px"
          >
            Launch
          </ButtonComp>
          <ButtonComp
            buttonHandler={() => sendAppActionMessage('kill')}
            padding="7px 5px"
          >
            Kill
          </ButtonComp>
          <ButtonComp
            buttonHandler={() => sendAppActionMessage('uninstall')}
            padding="7px 5px"
          >
            unInstall
          </ButtonComp>
          {type === 'lrInteract' && webInspectorPort && (
            <ButtonComp
              buttonHandler={() => sendAppActionMessage('inspect')}
              padding="7px 5px"
            >
              Inspect
            </ButtonComp>
          )}
        </HStack>
        {type === 'lrInteract' && webInspectorPort && (
          <>
            {loadingWebinpector ? (
              <SpinnerProgress isLoader={true}>
                Generating web inspector url
              </SpinnerProgress>
            ) : null}

            {webinpectorUrl && !loadingWebinpector ? (
              <WebInspectorUrl text={webinpectorUrl} />
            ) : null}
          </>
        )}
      </Stack>
      {/* <Flex borderBottom="1px solid" borderBottomColor="grey16"></Flex> */}
      <Flex
        bg="white3"
        paddingLeft="20px"
        align="center"
        borderRadius="5px"
        h="35px"
      >
        App upload
      </Flex>
      <Stack spacing="2" color="grey13">
        {/*@ts-ignore */}
        <Stack spacing="2" color="grey13">
          <SelectComponent
            width="70%"
            name="xcui"
            type="project"
            selectValue={selectProject}
            setSelectValue={setSelectProject}
            selectOptions={projects}
            title="Select Project"
          />
          <Text>OR</Text>
          <HStack>
            <InputComponent
              width="70%"
              setInputValue={setProjectName}
              inputValue={projectName}
              title="Enter Project Name"
            />
            <Spacer />
            <ButtonComp buttonHandler={createProjectHandler} padding="7px 12px">
              Create
            </ButtonComp>
          </HStack>
          <HStack>
            {createProjectLoading ? (
              <SpinnerProgress isLoader={true}>
                Creating Project
              </SpinnerProgress>
            ) : null}
          </HStack>
        </Stack>
        <Spacer />
        <UploadButton
          width="70%"
          validate={getFileExtension(platform)}
          uploadButtonHandler={appBuildUploadHandler}
          enableProgress={appbuildUploading}
          placeholder="Upload App Build"
        />
      </Stack>
      <Flex
        bg="white3"
        paddingLeft="20px"
        align="center"
        borderRadius="5px"
        h="35px"
      >
        App Install
      </Flex>
      <HStack>
        <>
          <SelectComponent
            width="70%"
            name="xcui"
            type="filename"
            selectValue={selectAppBuild}
            setSelectValue={setSelectAppBuild}
            selectOptions={appbuilds}
            title="Select App Build"
          />
          <RefreshIconButton clickHandler={refreshList} />
          <Spacer />
          <ButtonComp buttonHandler={sendAppInstallMessage} padding="7px 12px">
            Install
          </ButtonComp>
        </>
      </HStack>
      {appbuildsLoading ? (
        <SpinnerProgress isLoader={true}>Downloading File</SpinnerProgress>
      ) : null}
      {isInstalling ? (
        <SpinnerProgress isLoader={true}>Installing File</SpinnerProgress>
      ) : (
        <SpinnerProgress>{statusMessage} </SpinnerProgress>
      )}
    </>
  );
});

export default LocalUpload;
