import { createSelector } from '@reduxjs/toolkit';

import { RootState } from 'types';
import { initialState } from '.';

// First select the relevant part from the state
const selectSlice = (state: RootState) => state.HomePage || initialState;

export const selectPageIndexInisialValue = createSelector(
  [selectSlice],
  state => state.pageIndexValue,
);
export const homeState = createSelector([selectSlice], state => state);