// @ts-nocheck
import { Stack, Text, useToast } from '@chakra-ui/react';
import ButtonComp from 'app/components/ButtonComp';
import InputComponent from 'app/pages/Appium/components/InputComponent';
import SelectComponent from 'app/pages/Appium/components/SelectComponent';
import LabelFieldComponent from 'app/pages/AutomationTest/components/LabelFieldComponent';
import { GetLastAuthUser } from 'Authentication/configuration/UserName';
import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useSyntheticMonitoringSlice } from '../../slice';
import { selectSyntheticMonitoring } from '../../slice/selectors';

function JourneyConfiguration() {
  const dispatch = useDispatch();
  const { projectData, projectResponse, createProjectError , createJourneyResponse, createJourneyError } = useSelector(
    selectSyntheticMonitoring
  );
  const { actions } = useSyntheticMonitoringSlice();
  const [selectProject, setSelectProject] = useState('');
  const [selectAppName, setSelectAppName] = useState('');
  const [selectAppVersion, setSelectAppVersion] = useState('');
  const [selectPackage, setSelectPackage] = useState('');
  const [projectName, setProjectName] = useState('');
  const [journeyName, setJourneyName] = useState('');
  const [journeyDescription, setJourneyDescription] = useState('');
  const toast = useToast();
  const username = GetLastAuthUser();
 

  useEffect(() => {
    if ((createJourneyResponse).hasOwnProperty('status')) {
      if (createJourneyResponse.status === 200) {
        toast({
          title: createJourneyResponse.body.message,
          status: 'success',
          isClosable: true,
          position: 'bottom-right',
        });   
        dispatch(actions.getJourneyId(createJourneyResponse.body.response.uuid))
         dispatch(actions.createJourneyPayload(createJourneyResponse.body.response));
            
      }else{
        toast({
          title: createJourneyResponse.body.message,
          status: 'error',
          isClosable: true,
          position: 'bottom-right',
        });
      }
      dispatch(actions.createdJourney({}));
    }
  }, [createJourneyResponse]);
  useEffect(() => {
    if (createJourneyError !== null && createJourneyError === 3) {
      toast({
        title: 'Something Went wrong',
        status: 'error',
        isClosable: true,
        position: 'bottom-right',
      });
      dispatch(actions.createJourneyError(null));
    }
  }, [createJourneyError]);
useEffect(()=>{
  dispatch(actions.resetJourneyPayload())
  dispatch(actions.resetKpiPayload())
},[])

  const addJourneyHandler = () => {
    if(!journeyName){
      toast({
        title: 'Please Enter Journey Name',
        status: 'error',
        isClosable: true,
        position: 'bottom-right',
      });
      return
    }
    if (journeyName) {
      const payload = {
        name: journeyName,
        //description: journeyDescription,
        //project: selectProject,
       // appName: selectAppName,
       // appversion: selectAppVersion,
        //packageName: selectPackage,
        kpi: [],
        //createdBy: username,
      };
      dispatch(actions.loadCreateJourney(payload));
     // dispatch(actions.createJourneyPayload(payload));
    }
  };



  const createProjectHandler = () => {
    if (projectName !== '') {
      const payload = {
        name: projectName,
        description: projectName,
        createdBy: username,
      };
      dispatch(actions.loadcreateProject(payload));
    }
  };

  return (
    
    <Stack spacing="10px">
      <LabelFieldComponent label="Journey" isRequired={true}>
        <InputComponent
          setInputValue={setJourneyName}
          inputValue={journeyName}
          title="Enter Journey Name"
        ></InputComponent>
             <ButtonComp buttonHandler={addJourneyHandler} padding="7px 12px">
          Add
        </ButtonComp>
      </LabelFieldComponent>
    </Stack>
  );
}

export default JourneyConfiguration;
