import { Box, Flex, Icon } from '@chakra-ui/react';
import React from 'react';
import { HiOutlineClipboardCopy } from 'react-icons/hi';

function TabHeader({
  heading,
  children,
  showCopyButton,
  onCopyClick,
  rightContent,
}) {
  const copyButtonComponent = () => {
    return (
      <Box alignContent="right">
        <Icon
          onClick={onCopyClick}
          w="20px"
          h="20"
          color="grey6"
          as={HiOutlineClipboardCopy}
          style={{ cursor: 'pointer' }}
        />
      </Box>
    );
  };

  return (
    <Flex width="100%" height="100%" flexDirection="column" fontSize="14px">
      <Flex
        align="center"
        justifyContent="space-between"
        color=""
        fontWeight="600"
        textTransform="uppercase"
        paddingLeft="20px"
        width="100%"
        borderBottom="3px solid "
        borderBottomColor="grey3"
        height="45px"
      >
        <Box>{heading}</Box>
        {showCopyButton && copyButtonComponent()}
        {rightContent}
      </Flex>
      <Flex flexDirection="column">{children}</Flex>
    </Flex>
  );
}

export default TabHeader;
