import { Flex, Image, Text } from '@chakra-ui/react';
import React from 'react';
import { useNavigate, useLocation } from 'react-router-dom';

function DashboardListRowGrid({ dashboardListData, index }) {
  let history = useNavigate();
  const location = useLocation()
  const pathname = location.pathname;
  console.log({pathname});
  const selectDashboardHandler = () => {
    //console.log(dashboardListData.DashboardId)
    localStorage.setItem('dashboard_name', dashboardListData.Name);
    history(`${pathname}/${dashboardListData.DashboardId}`);
  };

  return (
    <Flex
      width="200px"
      borderRadius="5px"
      data-dashboard-row={`dashboard-row-id${index}-${dashboardListData.DashboardId}`}
      margin="5px"
      cursor="pointer"
      //borderBottom="1px solid"
      // borderBottomColor="white10"
      color="grey13"
      height="200px"
      border="1px solid #00000029"
      boxShadow="0px 3px 6px #00000029"
      flexDirection="column"
      _hover={{ boxShadow: '0px 1px 2px #00000029' }}
      onClick={selectDashboardHandler}
    >
      <Image
        padding="4px"
        borderRadius="5px"
        src="https://d278wwcisk41ph.cloudfront.net/app/esm/images/analysis_icon.148b89c2baef5db3336154325294784e.38.svg"
        h="150px"
      ></Image>
      <Flex
        padding="4px"
        borderTop="1px solid #00000029"
        width="100%"
        textTransform="none"
      >
        {dashboardListData.Name}
      </Flex>
    </Flex>
  );
}

export default DashboardListRowGrid;
