import { call, put, takeLatest } from "redux-saga/effects";
import { HomePageActions as actions} from ".";
import GetIdToken from "Authentication/configuration/GetIdToken";
import { getEmailId } from "Authentication/configuration/GetEmail";
import { request } from "utils/request";
import { HomePageErrorType } from "./types";
import { REACT_APP_COGNITO_BASE_URL } from "utils/enviroment";

const BASE_URL_USER = REACT_APP_COGNITO_BASE_URL;
function* getMfaStatusSaga(){
  const email = getEmailId();

  const requestUrl = `${BASE_URL_USER}/user/${email}`;


  const idToken = GetIdToken();
  const config = {
    method: 'GET',
    headers: {
      'Content-type': 'application/json',
      Authorization: `Bearer ${idToken}`,
    },
  };
  try {
    const response = yield call(
      request,
      requestUrl,
      config,
    );
    if (response?.message) {
      yield put(actions.mfaStatusLoaded(response.message));
    } else {
      yield put(
        actions.mfaStatusFailed(HomePageErrorType.RESPONSE_ERROR),
      );
    }
  } catch (error) {
    yield put(actions.mfaStatusFailed(HomePageErrorType.RESPONSE_ERROR));
  }
}
export function* HomepageSaga() {
  yield takeLatest(actions.getMfaStatus.type, getMfaStatusSaga);
}
