//@ts-nocheck
import { Box, Image, InputRightElement } from '@chakra-ui/react';
import { Button } from '@chakra-ui/react';
import {
  Flex,
  HStack,
  Icon,
  Input,
  InputGroup,
  InputLeftElement,
  Stack,
  Text,
  useToast,
} from '@chakra-ui/react';

import { useState } from 'react';
import { AiOutlineUser } from 'react-icons/ai';
import { AiOutlineEyeInvisible } from 'react-icons/ai';
import { AiOutlineEye } from 'react-icons/ai';
import { BiLockAlt } from 'react-icons/bi';
import { Link as ReactLink, useNavigate } from 'react-router-dom';
import authenticate from 'utils/authentication/authenticateUser';
import { useAuth } from 'utils/authentication/useAuth';
import NavbarLoginTheme from './NavbarLoginTheme';
import ButtonComp from 'app/components/ButtonComp';
import ROUTES from 'app/constants/Routes';
//import features from '../../features.json';
interface userLoginDataType {
  userId: string;
  password: string;
}

function LoginView() {
  const [userId, setUserId] = useState('');
  const [password, setPassword] = useState('');
  const { login } = useAuth();
  const [show, setShow] = useState(false);
  let navigate = useNavigate();
  const toast = useToast();
  const handleClick = () => setShow(!show);

  const loginUserHandler = () => {
    if (userId == '' || password == '') {
      toast({
        title: 'Please enter UserName/Password',
        status: 'error',
        isClosable: true,
        position: 'bottom-right',
      });
    } else {
      const userLoginDetails: userLoginDataType = {
        userId,
        password,
      };
      authenticate(userLoginDetails.userId, userLoginDetails.password)
        .then(({ data, user, type }) => {
          // setTimeout(() => {

          if (type === 'changepassword') {
            login(data, user, type);
            setTimeout(() => {
              navigate(ROUTES.CHANGE_PASSWORD, { state: user });
            }, 1000);
          } else {
            login(data, user, type);
            navigate(ROUTES.LANDING_PAGE);
          }

          // }, 1000);
        })
        .catch(err => {
          //dispatch(actions.fetchLoginFailure(err));
          toast({
            title: `${err.message}`,
            status: 'error',
            isClosable: true,
            position: 'bottom-right',
          });
          // console.log(err.message);
        });
      setPassword('');
      setUserId('');
    }
  };

  const loginDelayHandler = () => {
    setTimeout(loginUserHandler, 500);
  };

  const keyPressHandler = event => {
    if (event.keyCode === 13) {
      loginDelayHandler();
    }
  };

  return (
    <Flex
      flexDirection="column"
      backgroundColor="#F3F3F3"
      //"white"

      width="100%"
    >
      <NavbarLoginTheme
        height="55px"
        title="Mobile App Testing"
      ></NavbarLoginTheme>

      <Flex width="100%" fontFamily="Montserrat" color="grey2">
        <Flex
          width="100%"
          align="center"
          justify="center"
          h="calc(100vh - 55px)"
        >
          <Flex
            width="574px"
            padding="10px"
            flexDirection="column"
            margin="50px auto"
          >
            <Stack
              width="100%"
              spacing="20px"
              border="0px solid"
              bg="white"
              padding="30px 20px"
              borderRadius="7px"
              boxShadow="0px 3px 6px #00000029"
            >
              <Stack spacing="0px">
                <Text fontWeight="600" fontSize="20px" color="grey15">
                  Sign-in for Automation Testing
                </Text>
                <Text fontSize="14px">
                  Please enter your credentials to proceed
                </Text>
              </Stack>

              <Stack marginTop="20px" spacing="10px">
                <InputGroup>
                  <InputLeftElement
                    marginTop="0px"
                    marginLeft="2px"
                    children={
                      <Icon w="20px" h="20" color="grey6" as={AiOutlineUser} />
                    }
                  ></InputLeftElement>
                  <Input
                    value={userId}
                    pattern=".+@globex\.com"
                    data-input="username-login-id"
                    size="20"
                    required
                    onChange={e => setUserId(e.target.value)}
                    type="email"
                    borderRadius="1px"
                    placeholder="User Email"
                    height="40px"
                    _focus={{
                      boxShadow: ' 0 0 0 white',
                      borderLeft: '5px solid ',
                      borderLeftColor: 'blue1',
                    }}
                  ></Input>
                </InputGroup>

                <InputGroup>
                  <InputLeftElement
                    marginTop="0px"
                    marginLeft="2px"
                    children={
                      <Icon w="20px" h="20" color="grey6" as={BiLockAlt} />
                    }
                  ></InputLeftElement>
                  <Input
                    value={password}
                    onChange={e => setPassword(e.target.value)}
                    type={show ? 'text' : 'password'}
                    borderRadius="1px"
                    height="40px"
                    data-input="password-login-id"
                    onKeyDown={e => keyPressHandler(e)}
                    placeholder="Password"
                    _focus={{
                      boxShadow: ' 0 0 0 white',
                      borderLeft: '5px solid ',
                      borderLeftColor: 'blue1',
                    }}
                  ></Input>
                  <InputRightElement width="4.5rem" marginTop="0px">
                    <Button
                      bg="white"
                      h="1.75rem"
                      size="sm"
                      _focus={{ boxShadow: ' 0 0 0 white' }}
                      onClick={handleClick}
                    >
                      {show ? (
                        <Icon as={AiOutlineEyeInvisible}></Icon>
                      ) : (
                        <Icon as={AiOutlineEye}></Icon>
                      )}
                    </Button>
                  </InputRightElement>
                </InputGroup>
              </Stack>
              <Stack spacing="40px">
                <ButtonComp
                  color="white"
                  padding="7px 14px"
                  backgroundColor="blue1"
                  width="100%"
                  buttonHandler={loginDelayHandler}
                  name="login-button-app-testing"
                >
                  Login
                </ButtonComp>
              </Stack>
            </Stack>
          </Flex>
        </Flex>
      </Flex>
    </Flex>
  );
}

export default LoginView;
