import { Text } from '@chakra-ui/layout'
import ButtonComp from 'app/components/ButtonComp'
import InputComponent from 'app/pages/Appium/components/InputComponent'
import SelectComponent from 'app/pages/Appium/components/SelectComponent'
import LabelFieldComponent from 'app/pages/AutomationTest/components/LabelFieldComponent'
import React from 'react'

function CreateEvent({type , eventListData , setEventName , eventName ,selectEvent , setSelectEvent , addEventHandler}) {
  return (
    <>
      <LabelFieldComponent label={`${type} Event Name`} isRequired={true}>
        <InputComponent
          setInputValue={setEventName}
          inputValue={eventName}
          title= {`Enter ${type} Event`}
        ></InputComponent>
        <Text padding="0px 5px">OR</Text>
        <SelectComponent
          name="xcui"
          type="project"
          selectValue={selectEvent}
          setSelectValue={setSelectEvent}
          selectOptions={eventListData}
          title= {`Select ${type} Event`}
        ></SelectComponent>
      </LabelFieldComponent>
      <LabelFieldComponent label="">
        <ButtonComp
          buttonHandler={()=> addEventHandler(type)}
          padding="7px 12px"
        >
          ADD Event
        </ButtonComp>
      </LabelFieldComponent>
    </>
  )
}

export default CreateEvent