import { PayloadAction } from '@reduxjs/toolkit';
import { createSlice } from 'utils/@reduxjs/toolkit';
import { useInjectReducer, useInjectSaga } from 'utils/redux-injectors';
import { mobileInteractSaga, lrInteractSaga } from './saga';

export const initialState: any = {
  devicesLoading: false,
  devices: [],
  selectedRemote: {},
  devicesError: null,

  deviceTimestamp: null,
  // session
  sessionLoading: false,
  session: null,
  sessionError: null,

  // upload screenshot
  screenshotUploading: false,
  screenshotUploadedResponse: {},
  screenshotUploadError: null,
  // upload screenshot
  screenshotDeleting: false,
  screenshotDeleted: {},
  screenshotDeleteError: null,

  // loading screenshots
  screenshotsLoading: false,
  screenshots: [],
  screenshotsError: null,
  // loading  appbuild
  appbuildsLoading: false,
  appbuilds: [],
  appbuildsLoadError: null,
  appbuildUploading: false,
  appbuildUpload: {},
  appbuildUploadError: null,
  // loading  project
  projectsLoading: false,
  projects: [],
  projectsLoadError: null,
  // create  project
  createProjectLoading: false,
  createProject: [],
  createProjectLoadError: null,

  responseTimes: {
    getDevices: 0,
    startSession: 0,
    stopSession: 0,
    sendStartDeviceStreamsMessage: 0,
    receiveDeviceStream: 0,
  },
  isConnected: false,
  isExit: false,
};

const createDataSlice = (name: string, saga: any) => {
  const slice = createSlice({
    //@ts-ignore
    name,
    initialState: {
      ...initialState,
    },
    reducers: {
      resetState: state => {
        console.log('Resetting state', initialState);
        /* state = initialState; */
        return initialState;
      },
      resetRemote: state => {
        state.selectedRemote = initialState.selectedRemote;
      },
      setSelectedRemote: (state, action) => {
        state.selectedRemote = action.payload;
      },
      setResponseTimes: (state, action) => {
        state.responseTimes = {
          ...state.responseTimes,
          ...action.payload,
        };
      },
      clearSession: state => {
        state.session = null;
      },
      loadDevices: (state, action) => {
        state.devicesLoading = true;
        state.devices = [];
        state.devicesError = null;
      },
      devicesLoaded: (state, action: PayloadAction<any>) => {
        state.devicesLoading = false;
        let devices = action.payload;
        state.devices = devices;
        state.devicesError = null;
      },
      devicesError: (state, action: PayloadAction<any>) => {
        state.devicesLoading = false;
        state.devicesError = action.payload;
      },
      updateDeviceTimestamp: (state, action: PayloadAction<any>) => {
        state.deviceTimestamp = action.payload;
      },
      //--- session
      startSession: (state, action: any) => {
        state.sessionLoading = true;
        state.session = null;
        state.sessionError = null;
      },
      sessionStarted: (state, action: PayloadAction<any>) => {
        state.sessionLoading = false;
        state.session = action.payload.id;
        state.sessionError = null;
      },
      sessionError: (state, action: PayloadAction<any>) => {
        state.sessionLoading = false;
        state.session = null;
        state.sessionError = action.payload;
      },
      stopSession: (state, action: any) => {
        state.sessionLoading = false;
        state.session = null;
        state.sessionError = null;
      },

      // --- screenshot upload
      screenshotUploading: (state, action: PayloadAction<any>) => {
        state.screenshotUploading = true;
        state.screenshotUploadedResponse = {};
        state.screenshotUploadError = null;
      },
      screenshotUploadedFile: (state, action: PayloadAction<any>) => {
        console.log(action.payload, 'uploaded response');
        state.screenshotUploading = false;
        //   state.screenshotUploadedResponse = action.payload;
        state.screenshotUploadError = null;
      },
      resetScreenshotResponse: (state, action: PayloadAction<any>) => {
        state.screenshotUploaded = {};
      },
      screenshotUploadingItem: (state, action: PayloadAction<any>) => {
        //console.log(action.payload, 'uploaded response');
        // state.screenshotUploading = false;
        state.screenshotUploadedResponse = action.payload;
        //state.screenshotUploadError = null;
      },
      screenshotUploadError: (state, action: PayloadAction<any>) => {
        state.screenshotUploading = false;
        // state.screenshotUploadedResponse = {};
        state.screenshotUploadError = action.payload;
      },
      // --- screenshot delete
      screenshotDeleting: (state, action: PayloadAction<any>) => {
        state.screenshotDeleting = true;
        state.screenshotDeleted = {};
        state.screenshotDeletError = null;
      },
      screenshotDeleted: (state, action: PayloadAction<any>) => {
        state.screenshotDeleting = false;
        state.screenshotUploadedResponse = {};
        state.screenshotDeleted = action.payload;
        state.screenshotDeletError = null;
      },
      screenshotDeletError: (state, action: PayloadAction<any>) => {
        state.screenshotDeleting = false;
        state.screenshotDeleted = {};
        state.screenshotDeletError = action.payload;
      },
      // app build loading
      appbuildLoading: (state, action: PayloadAction<any>) => {
        state.appbuildsLoading = true;
        state.appbuildsLoadError = null;
      },
      appbuildLoaded: (state, action: PayloadAction<any>) => {
        state.appbuildsLoading = false;
        state.appbuilds = action.payload;
        state.appbuildsLoadError = null;
      },
      appbuildLoadError: (state, action: PayloadAction<any>) => {
        state.appbuildsLoading = false;
        state.appbuildsLoadError = action.payload;
      },
      uploadAppBuild: (state, action: PayloadAction<any>) => {
        state.appbuildUploading = true;
        state.appbuildUploadError = null;
      },
      appbuildUploaded: (state, action: PayloadAction<any>) => {
        state.appbuildUploading = false;
        state.appbuildUpload = action.payload;
        state.appbuildUploadError = null;
      },
      appbuildUploadError: (state, action: PayloadAction<any>) => {
        state.appbuildUploading = false;
        state.appbuildUploadError = action.payload;
      },

      projectLoading: (state, action: PayloadAction<any>) => {
        state.projectsLoading = true;
        state.projectsLoadError = null;
      },
      projectLoaded: (state, action: PayloadAction<any>) => {
        state.projectsLoading = false;
        state.projects = action.payload;
        state.projectsLoadError = null;
      },
      projectLoadError: (state, action: PayloadAction<any>) => {
        state.projectsLoading = false;
        state.projectsLoadError = action.payload;
      },

      createProjectLoading: (state, action: PayloadAction<any>) => {
        state.createProjectLoading = true;
        state.createProjectLoadError = null;
      },
      createProjectLoaded: (state, action: PayloadAction<any>) => {
        state.createProjectLoading = false;
        state.createProject = action.payload;
        state.createProjectLoadError = null;
      },
      createProjectLoadError: (state, action: PayloadAction<any>) => {
        state.createProjectLoading = false;
        state.createProjectLoadError = action.payload;
      },

      // --- screenshot load
      screenshotsLoading: state => {
        state.screenshotsLoading = true;
        state.screenshotsError = false;
      },
      screenshotsLoaded: (state, action: PayloadAction<any>) => {
        state.screenshotsLoading = false;
        state.screenshots = [...state.screenshots, action.payload];
        state.screenshotsError = false;
      },
      screenshotsError: (state, action: PayloadAction<any>) => {
        state.screenshotsLoading = false;
        /* state.screenshots = []; */
        state.ListScreenShotError = action.payload;
      },
      stream: (state, action) => {
        state.isConnected = action.payload;
      },
      exitSession: (state, action) => {
        state.isExit = action.payload;
      },
    },
  });
  const { actions } = slice;

  const useDataSlice = () => {
    useInjectReducer({ key: slice.name, reducer: slice.reducer });
    useInjectSaga({ key: slice.name, saga });
    return { actions };
  };

  return { slice, useDataSlice };
};

export const mobileInteract = createDataSlice(
  'mobileInteract',
  mobileInteractSaga,
);
export const useMobileInteractSlice = mobileInteract.useDataSlice;
export const { actions: mobileInteractActions } = mobileInteract.slice;

export const lrInteract = createDataSlice('lrInteract', lrInteractSaga);
export const useLrInteractSlice = lrInteract.useDataSlice;
export const { actions: lrInteractActions } = lrInteract.slice;

export const allActions = {
  mobileInteractActions,
  lrInteractActions,
};
