import { useEffect, useRef, useState } from 'react';

import { getEmailId } from 'Authentication/configuration/GetEmail';
import { delay, Stream } from 'app/utils';

import { useInteractSlice } from '.';
import { addStartPrefix, retryStreamStart } from '../utils';
import { isDeviceSelfOwnedOrFree } from 'app/utils/device';

const useSendStartStreamMessage = (
  retryStart: number,
  isCleanExit: boolean,
) => {
  const {
    interactType,
    actions,
    dispatch,
    selector: { session, selectedRemote },
    controllerDetails: { controllerChannelName, deviceChannelNames },
    device,
  } = useInteractSlice();
  const [controllerState, setControllerState] = useState();
  const [controllerError, setControllerError] = useState();
  const stateRef = useRef<any>();
  const errorRef = useRef<any>();

  useEffect(() => {
    const start = async () => {
      try {
        stateRef.current = Stream();
        const startTime = performance.now(); // Record the start time
        await stateRef.current.startViewer(
          controllerChannelName,
          () => {},
          true,
        );
        await new Promise(resolve =>
          retryStreamStart(resolve, stateRef.current, true),
        );
        if (stateRef.current.failedToStartStream) {
          errorRef.current = 'failedToStartStream';
          //@ts-ignore
          setControllerError('failedToStartStream');
          setControllerState(undefined);
          console.log('Failed to open data channel');
        } else {
          let startMessage;
          if (
            interactType === 'lrInteract' &&
            selectedRemote &&
            Object.keys(selectedRemote).length
          ) {
            startMessage = addStartPrefix([
              session,
              ...deviceChannelNames,
              selectedRemote['device-uuid'],
              'MARK_BUSY',
            ]);
          } else {
            startMessage = addStartPrefix([
              session,
              ...deviceChannelNames,
              'MARK_BUSY',
            ]);
          }
          console.log(`Sending ${startMessage}`);

          stateRef.current.sendMessage(startMessage);
          const endTime = performance.now(); // Record the end time
          const timeElapsed = endTime - startTime; // Calculate the time difference
          console.log(`Time elapsed: ${timeElapsed} milliseconds`);
          dispatch(
            actions.setResponseTimes({
              sendStartDeviceStreamsMessage: timeElapsed,
            }),
          );
          /* stateRef.current.sendMessage(`Start ["${device.serial}"]`); */
          //TODO: remove delay and use remote message for confirmation
          await delay(1000);
          setControllerState(stateRef.current);
          setControllerError(undefined);
        }
      } catch (e) {
        console.log(e);
        errorRef.current = e;
        setControllerState(undefined);
        setControllerError(e);
      }
    };

    const email = getEmailId();
    const shouldStart =
      !isCleanExit &&
      session &&
      device &&
      Object.keys(device).length &&
      isDeviceSelfOwnedOrFree(device, email) &&
      controllerChannelName &&
      deviceChannelNames.length &&
      (retryStart || (!errorRef.current && !stateRef.current));

    if (shouldStart) {
      start();
    }
  }, [
    selectedRemote,
    actions,
    dispatch,
    controllerChannelName,
    deviceChannelNames,
    interactType,
    device,
    retryStart,
    session,
    isCleanExit,
  ]);

  return [controllerState, controllerError];
};

export default useSendStartStreamMessage;
