import { AuthenticationDetails } from 'amazon-cognito-identity-js';

import { getAuthUser } from './getAuthUser';
import { REACT_APP_LOGOUT_REDIRECTPAGE } from 'utils/enviroment';

const authenticate = async (Username, Password) => {
  const RedirectPage = REACT_APP_LOGOUT_REDIRECTPAGE;
  return await new Promise((resolve, reject) => {
    const authDetails = new AuthenticationDetails({
      Username,
      Password,
    });
    //@ts-ignore
    const user = getAuthUser(Username);

    if (!user) {
      return undefined;
    }
    user.authenticateUser(authDetails, {
      onSuccess: data => {
        user.getUserAttributes((err, result) => {
          if (err) {
            console.log('error', err);
            return;
          }
          if (
            result[2].getValue() === 'SystemAdmin' ||
            result[2].getValue() === 'TenantAdmin'
          ) {
            // dispatch(actions.updateIsLoading());
            // dispatch(actions.fetchUserDetailsSucess('admin'));
            console.log('----1----', { result });
          } else if (result[2].getValue() === 'TenantCare') {
            // dispatch(actions.updateIsLoading());
            // dispatch(actions.fetchUserDetailsSucess('user'));
            console.log('----2----', { result });
          } else {
            // dispatch(actions.updateIsLoading());
            // dispatch(actions.fetchUserDetailsSucess('u ser'));
            console.log('----3----', { result });
          }
        });
        resolve({ data, user, type: 'login' });
      },
      onFailure: err => {
        // console.log('on failure', err);
        reject(err);
      },
      newPasswordRequired: data => {
        console.log('newPasswordrequired', data);
        // setIsFirstLogin(true);
        // setUserAttr(data);
        resolve({ data, user, type: 'changepassword' });
      },
      totpRequired: () => {
        const token = prompt('Please enter your 6-digit token');
        user.sendMFACode(
          token,
          {
            // eslint-disable-next-line no-self-assign
            onSuccess: () => (window.location.href = `${RedirectPage}`),
            onFailure: () => alert('Incorrect code!'),
          },
          'SOFTWARE_TOKEN_MFA',
        );
      },
    });
  });
};

export default authenticate;
