class VideoEvents {
  SIGNALING_CLIENT_CONNECTED = "SIGNALING_CLIENT_CONNECTED"
  SIGNALING_CLIENT_CLOSE = "SIGNALING_CLIENT_CLOSE"
  SIGNALING_CLIENT_ERROR = "SIGNALING_CLIENT_ERROR"

  SDP_ANSWER_RECEIVED = "SDP_ANSWER_RECEIVED"
  ICE_CANDIDATES_RECEIVED = "ICE_CANDIDATES_RECEIVED"

  // ICE_CONNECTION_STATE
  VIDEO_REMOTE_TRACK = "VIDEO_REMOTE_TRACK"

  PEER_CONNECTION_FAILED = "PEER_CONNECTION_FAILED"
}

// ==================================================
class EventEmitter {
  constructor() {
    this.listeners = {};
  }

  on(event, callback) {
    if (!this.listeners[event]) {
      this.listeners[event] = [];
    }
    this.listeners[event].push(callback);
  }

  emit(event, data) {
    if (this.listeners[event]) {
      this.listeners[event].forEach(callback => callback(data));
    }
  }
  removeListener(event, callback) {
    if (this.listeners[event]) {
      this.listeners[event] = this.listeners[event].filter(
        listener => listener !== callback,
      );
    }
  }
}
export const eventEmitter = new EventEmitter();

let connectionState = 0;
function updateExternalValue(xyz) {
  if (connectionState !== xyz) {
    connectionState = xyz;
    eventEmitter.emit('externalValueChange', connectionState);
  }
}

// ================================

const onStatsReport = () => {
  console.log('stats report');
};

const onRemoteDataMessage = () => {
  console.log('remote data message');
};
function retrieveCachedICECandidates(channelName) {
  const cachedCandidatesJSON = localStorage.getItem(
    `IceCandidates-${channelName}`,
  );
  if (cachedCandidatesJSON) {
    return JSON.parse(cachedCandidatesJSON);
  }
  return [];
}

const getRandomClientId = () => {
  return Math.random().toString(36).substring(2).toUpperCase();
};

const Stream = () => {
  const state = {
    ...webrtcConfig,
    clientId: getRandomClientId(),
    accessKeyId: '',
    secretAccessKey: '',
    sessionToken: '',
    channelName: '',
    endpoint: null,
    messageToSend: '',
    failedToStartStream: false,
    startViewer: async (channelName, callback, onlyData) => {},
    stopViewer: () => {},
    sendMessage: msg => {},
    playerIsStarted: false,
    deviceResolution: { w: '', h: '' },
    scaleRatioWidth: 2,
    scaleRatioHeight: 2,
    streamWidth: '400px',
    streamHeight: '400px',
    screenRef: {},
    desiredHeight: '',
    handleKeyDown: () => ({}),
    remoteStream: null,
    combined: {},

    // These are set when user starts video; a few of them are only used when you start the stream as MASTER:
    signalingClient: null,
    remoteView: null,
    dataChannel: null,
    peerConnectionStatsInterval: null,
    peerConnectionByClientId: {},
    dataChannelByClientId: [],
    receivedMessages: '',
    connectionState: '',
  };

  async function startViewer(channelName, callback, eventsCallback, onlyData, remoteView) {
    try {
      state.channelName = channelName;
      const credentials: any = await getCredentials();
      state.accessKeyId = credentials.accessKeyId;
      state.secretAccessKey = credentials.secretAccessKey;

      state.sessionToken = credentials.sessionToken;

      // Create KVS client
      const kinesisVideoClient = new KinesisVideo({
        region: state.region,
        accessKeyId: state.accessKeyId,
        secretAccessKey: state.secretAccessKey,
        sessionToken: state.sessionToken,
        endpoint: state.endpoint,
        correctClockSkew: true,
      });

      // Get signaling channel ARN
      const describeSignalingChannelResponse = await kinesisVideoClient
        .describeSignalingChannel({
          ChannelName: state.channelName,
        })
        .promise();
      const channelARN =
        describeSignalingChannelResponse.ChannelInfo.ChannelARN;
      console.log('[VIEWER] Channel ARN: ', channelARN);

      // Get signaling channel endpoints
      const getSignalingChannelEndpointResponse = await kinesisVideoClient
        .getSignalingChannelEndpoint({
          ChannelARN: channelARN,
          SingleMasterChannelEndpointConfiguration: {
            Protocols: ['WSS', 'HTTPS'],
            Role: 'VIEWER',
          },
        })
        .promise();
      const endpointsByProtocol =
        getSignalingChannelEndpointResponse.ResourceEndpointList.reduce(
          (endpoints, endpoint) => {
            endpoints[endpoint.Protocol] = endpoint.ResourceEndpoint;
            return endpoints;
          },
          {},
        );
      /* console.log('[VIEWER] Endpoints: ', endpointsByProtocol); */

      const kinesisVideoSignalingChannelsClient =
        new KinesisVideoSignalingChannels({
          region: state.region,
          accessKeyId: state.accessKeyId,
          secretAccessKey: state.secretAccessKey,
          sessionToken: state.sessionToken,
          endpoint: endpointsByProtocol.HTTPS,
          correctClockSkew: true,
        });

      // Get ICE server configuration
      const getIceServerConfigResponse =
        await kinesisVideoSignalingChannelsClient
          .getIceServerConfig({
            ChannelARN: channelARN,
          })
          .promise();
      const iceServers = [];
      if (!state.natTraversalDisabled && !state.forceTURN) {
        iceServers.push({
          urls: `stun:stun.kinesisvideo.${state.region}.amazonaws.com:443`,
        });
      }
      if (!state.natTraversalDisabled) {
        getIceServerConfigResponse.IceServerList.forEach(iceServer =>
          iceServers.push({
            urls: iceServer.Uris,
            username: iceServer.Username,
            credential: iceServer.Password,
          }),
        );
      }
      console.log('[VIEWER] ICE servers: ', iceServers);

      // Create Signaling Client
      state.signalingClient = new SignalingClient({
        channelARN,
        channelEndpoint: endpointsByProtocol.WSS,
        clientId: state.clientId,
        role: 'VIEWER',
        region: state.region,
        credentials: {
          accessKeyId: state.accessKeyId,
          secretAccessKey: state.secretAccessKey,
          sessionToken: state.sessionToken,
        },
        systemClockOffset: kinesisVideoClient.config.systemClockOffset,
      });

      const resolution = state.widescreen
        ? { width: { ideal: 1280 }, height: { ideal: 720 } }
        : { width: { ideal: 640 }, height: { ideal: 480 } };

      const constraints = {
        video: state.sendVideo ? resolution : false,
        audio: state.sendAudio,
      };
      const configuration = {
        iceServers,
        iceTransportPolicy: state.forceTURN ? 'relay' : 'all',
      };
      state.peerConnection = new RTCPeerConnection(configuration);

      // Cache ICE candidates as they are gathered
      /* cacheICECandidates( */
      /*   state.peerConnection, */
      /*   state.channelName, */
      /*   state.useTrickleICE, */
      /*   state.signalingClient, */
      /* ); */

      // When reconnecting or initiating a new connection, retrieve the cached ICE candidates
      /* const cachedCandidates = retrieveCachedICECandidates(state.channelName); */
      /* cachedCandidates.forEach(candidate => { */
      /*   peerConnection.addIceCandidate(new RTCIceCandidate(candidate)); */
      /* }); */

      if (state.openDataChannel) {
        console.log('Creating data channel ...');
        state.dataChannel =
          state.peerConnection.createDataChannel('kvsDataChannel');
        state.dataChannel.onmessage = msg => {
          console.log('Received message: ', msg?.data);
          state.dataChannelReady = true;
          /* if (onlyData)  callback(state); */
        };
        state.peerConnection.ondatachannel = event => {
          // THIS LISTENER IS NOT BEING TRIGGERED
          event.channel.onmessage = onRemoteDataMessage;
        };
      }

      state.signalingClient.on('open', async () => {
        console.log('[VIEWER] Connected to signaling service');

        eventsCallback?.("SIGNALING_CLIENT", "open")

        // SIGNALING_CLIENT_CONNECTED

        // Create an SDP offer to send to the master
        console.log('[VIEWER] Creating SDP offer');
        await state.peerConnection.setLocalDescription(
          await state.peerConnection.createOffer({
            offerToReceiveAudio: true,
            offerToReceiveVideo: true,
          }),
        );

        // When trickle ICE is enabled, send the offer now and then send ICE candidates as they are generated. Otherwise wait on the ICE candidates.
        if (state.useTrickleICE) {
          /* console.log('[VIEWER] Sending SDP offer'); */
          state.signalingClient.sendSdpOffer(
            state.peerConnection.localDescription,
          );
        }
        /* console.log('[VIEWER] Generating ICE candidates'); */
      });

      state.signalingClient.on('sdpAnswer', async answer => {
        // Add the SDP answer to the peer connection

        // SDP_ANSWER_RECEIVED

        console.log('[VIEWER] Received SDP answer');
        try {
          await state.peerConnection.setRemoteDescription(answer);

          const cachedCandidatesFromLocalStorage =
            retrieveCachedICECandidates(channelName);
          console.log({
            fromLocalStorage: cachedCandidatesFromLocalStorage,
          });
          cachedCandidatesFromLocalStorage.forEach(candidate => {
            if (candidate) {
              state.peerConnection.addIceCandidate(
                new RTCIceCandidate(candidate),
              );
            }
          });
        } catch (error) {
          console.log(error);
          state.failedDueToInvalidPeerConnectionState = true;
          state.failedToStartStream = true;
          /* let count; */
          /* let peerConnectionInterval = setInterval( async () => { */
          /*   count++; */
          /*   if (count > 5) { */
          /*     clearInterval(peerConnectionInterval); */
          /*   } */
          /**/
          /*   console.log(state.peerConnection); */
          /*   await state.peerConnection.setRemoteDescription(answer); */
          /* }, 2000); */
        }
      });

      state.signalingClient.on('iceCandidate', candidate => {
        // Add the ICE candidate received from the MASTER to the peer connection
        console.log('[VIEWER] Received ICE candidate');
        state.peerConnection.addIceCandidate(candidate);

        // ICE_CANDIDATES_RECEIVED
      });

      state.signalingClient.on('close', () => {
        console.log('[VIEWER] Disconnected from signaling channel');
        eventsCallback?.("SIGNALING_CLIENT", "close")
        // SIGNALING_CLIENT_CLOSE
      });

      state.signalingClient.on('error', error => {
        console.error('[VIEWER] Signaling client error: ', error);
        eventsCallback?.("SIGNALING_CLIENT", "error")
        // SIGNALING_CLIENT_ERROR
      });

      // Send any ICE candidates to the other peer
      const cachedCandidates = [];

      state.peerConnection.addEventListener(
        'iceconnectionstatechange',
        event => {
          state.combined = {
            ...state.combined,
            [state.channelName]: state.peerConnection.iceConnectionState,
          };
          updateExternalValue(JSON.stringify(state.combined));
          console.log('iceConnectionState: newValue ==>', state.peerConnection.iceConnectionState);
          console.log('state icec', state, event);
          const iceConnectionState = state.peerConnection.iceConnectionState;

          state.connectionState = iceConnectionState;

          // ICE_CONNECTION_STATE
        },
      );
      state.peerConnection.addEventListener('icecandidate', ({ candidate }) => {
        if (candidate) {
          /* console.log('[VIEWER] Generated ICE candidate'); */
          console.log(`Caching ${candidate.toJSON()}`);
          cachedCandidates.push(candidate.toJSON());

          // When trickle ICE is enabled, send the ICE candidates as they are generated.
          if (state.useTrickleICE) {
            /* console.log('[VIEWER] Sending ICE candidate'); */
            state.signalingClient.sendIceCandidate(candidate);
          }
        } else {
          console.log('[VIEWER] All ICE candidates have been generated');
          localStorage.setItem(
            `IceCandidates-${channelName}`,
            JSON.stringify(cachedCandidates),
          );

          // When trickle ICE is disabled, send the offer now that all the ICE candidates have ben generated.
          if (!state.useTrickleICE) {
            console.log('[VIEWER] Sending SDP offer');
            state.signalingClient.sendSdpOffer(
              state.peerConnection.localDescription,
            );
          }
        }
      });

      // When reconnecting or initiating a new connection, retrieve the cached ICE candidates

      state.peerConnection.addEventListener('connectionstatechange', async event => {
        const rtcPeerConnection = event.target;
        console.debug("[VIEWER]", 'PeerConnection state:', rtcPeerConnection.connectionState);

        eventsCallback?.('PEER', rtcPeerConnection.connectionState);

        if (rtcPeerConnection.connectionState === 'connected') {
          console.log("[VIEWER]", 'Connection to peer successful!');
          const stats = await rtcPeerConnection.getStats();
          if (!stats) return;

          rtcPeerConnection.getSenders().map(sender => {
            const trackType = sender.track?.kind;
            if (sender.transport) {
              const iceTransport = sender.transport.iceTransport;
              if (iceTransport) {
                const logSelectedCandidate = () =>
                    console.debug(`Chosen candidate pair (${trackType || 'unknown'}):`, iceTransport.getSelectedCandidatePair());
                iceTransport.onselectedcandidatepairchange = logSelectedCandidate;
                logSelectedCandidate();
              }
            } else {
              console.error('Failed to fetch the candidate pair!');
            }
          });
        } else if (rtcPeerConnection.connectionState === 'failed') {

          console.error("[VIEWER]", `Connection to peer failed!`);
          // PEER_CONNECTION_FAILED
        }

      });

      // As remote tracks are received, add them to the remote view
      let tempArr = [];
      state.peerConnection.addEventListener('track', event => {
        console.log('[VIEWER] Received remote track');

        // VIDEO_REMOTE_TRACK

        if (onlyData) {
          /* callback(state); */
        } else {
          if (!state.remoteStream) {
            state.remoteStream = event.streams[0];
            console.log('calling callback', state.remoteStream);
            callback(state.remoteStream);
          }
        }
        /* remoteViewRef.current.srcObject = state.remoteStream; */

        /* remoteView.srcObject = state.remoteStream; */
      });

      console.log('[VIEWER] Starting state connection');
      state.signalingClient.open();
    } catch (error) {
      console.error(error);
      throw error;
      /* history.push(ROUTES.MOBILE_INTERACT); */
    }
  }

  function sendMessage(msg) {
    if (state.dataChannel) {
      try {
        state.dataChannel.send(msg);
        console.log(`Message sent to master: ${msg}`);
      } catch (e) {
        console.error('[VIEWER] Send DataChannel: ', e.toString());
      }
    }
  }

  function stopViewer() {
    try {
      console.log('[VIEWER] Stopping viewer connection');
      if (state.signalingClient) {
        state.signalingClient.close();
        state.signalingClient = null;
      }

      if (state.peerConnection) {
        state.peerConnection.close();
        state.peerConnection = null;
      }

      if (state.remoteStream) {
        state.remoteStream.getTracks().forEach(track => track.stop());
        state.remoteStream = null;
      }

      if (state.peerConnectionStatsInterval) {
        clearInterval(state.peerConnectionStatsInterval);
        state.peerConnectionStatsInterval = null;
      }

      if (state.remoteView) {
        state.remoteView.srcObject = null;
      }

      if (state.dataChannel) {
        state.dataChannel = null;
        state.dataChannelReady = false;
      }
      console.log(`[VIEWER] Stopped Connection`);
    } catch (e) {
      console.log(e);
    }
  }

  state.sendMessage = sendMessage;
  state.startViewer = startViewer;
  state.stopViewer = stopViewer;

  return state;
};

export default Stream;
