// @ts-nocheck
import {
  Flex,
  Radio,
  RadioGroup,
  Stack,
  Text,
  useToast,
} from '@chakra-ui/react';
import ButtonComp from 'app/components/ButtonComp';
import InputComponent from 'app/pages/Appium/components/InputComponent';
import SelectComponent from 'app/pages/Appium/components/SelectComponent';
import LabelFieldComponent from 'app/pages/AutomationTest/components/LabelFieldComponent';
import { GetLastAuthUser } from 'Authentication/configuration/UserName';
import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import CardComponent from '../../components/CardComponent';
import CardRow from '../../components/CardRow.tsx';
import { useSyntheticMonitoringSlice } from '../../slice';
import { selectSyntheticMonitoring } from '../../slice/selectors';
import CardTitleComponent from '../../components/CardTitleComponent';
import AddKpi from '../../components/AddKpi';
import AddKpiList from '../../components/AddKpiList';
import CreateKpi from '../../components/CreateKpi';
const selectKpiTypeOptions = [
  { name: 'state' },
  { name: 'latency' },
  { name: 'streaming' },
  { name: 'metric' },
];
const selectEventTypeOptions = [{ name: 'click' }, { name: 'swipe' }];
const selectOperationTypeOptions = [{ name: 'SUM' },
 { name: 'PERCENTAGE' },
 { name: 'COUNT' },
];

function KpiConfiguration() {
  const username = GetLastAuthUser();
  const dispatch = useDispatch();
  const { actions } = useSyntheticMonitoringSlice();
  const [kpiType, setKpiType] = useState('');
  const [selectExistKpi, setSelectExistKpi] = useState('');
  const [selectStartEvent, setSelectStartEvent] = useState('');
  const [selectEndEvent, setSelectEndEvent] = useState('');
  const [kpiMode, setKpiMode] = useState('1');
  const [kpiName, setKpiName] = useState('');
  const [kpiDescription, setKpiDescription] = useState('');
  const [startEventName, setStartEventName] = useState('');
  const [endEventName, setEndEventName] = useState('');
  const [startEventType, setStartEventType] = useState('');
  const [endEventType, setEndEventType] = useState('');
  const [operationType, setOperationType] = useState('');
  const [startEventDesc, setStartEventDesc] = useState('');
  const [endEventDesc, setEndEventDesc] = useState('');
  const {
    journeyPayload,
    kpiListData,
    eventListData,
    createEventError,
    createEventResponse,
    createKpiResponse,
    createKpiError,
    kpiPayload,
    updateItemfetch
  } = useSelector(selectSyntheticMonitoring);
  const toast = useToast();
const getEvent = (eventItems , event )=>{
    if(eventItems.length > 0){
  const eventObj =    eventItems.filter( item => item.name === event)
     return eventObj[0].uuid
    }
  return {}
}
  useEffect(() => {
    dispatch(actions.loadKpiListData());
    dispatch(actions.loadEventListData());
  }, []);
  useEffect(() => {
    if (createEventResponse.hasOwnProperty('status')) {
      if (createEventResponse.status === 200) {
        toast({
          title: createEventResponse.body.message,
          status: 'success',
          isClosable: true,
          position: 'bottom-right',
        });   
        setStartEventName('');
        setEndEventName('');
        dispatch(actions.loadEventListData());
   
      }else{
        toast({
          title: createEventResponse.body.message,
          status: 'error',
          isClosable: true,
          position: 'bottom-right',
        });
      }
      dispatch(actions.eventCreated({}));
    }
  }, [createEventResponse]);
  useEffect(() => {
    if (createEventError !== null && createEventError === 3) {
      toast({
        title: 'Something went wrong',
        status: 'error',
        isClosable: true,
        position: 'bottom-right',
      });
      dispatch(actions.eventCraeteError(null));
    }
  }, [createEventError]);
  useEffect(() => {
    if (createKpiResponse.hasOwnProperty('status')) {
      if (createKpiResponse.status === 200) {
        toast({
          title: createKpiResponse.body.message,
          status: 'success',
          isClosable: true,
          position: 'bottom-right',
        });
         //console.log(createKpiResponse)
        dispatch(actions.createKpiObject(createKpiResponse.body.response));
        setKpiName('');
        setKpiType('');
        setSelectStartEvent('');
        setSelectEndEvent('');
        dispatch(actions.loadKpiListData());
 
      }else{
        toast({
          title: createKpiResponse.body.message,
          status: 'error',
          isClosable: true,
          position: 'bottom-right',
        });
      }
      dispatch(actions.kpiCreated({}));
    }
  }, [createKpiResponse]);
  useEffect(() => {
    if (updateItemfetch.hasOwnProperty('status')) {
      if (updateItemfetch.status === 200) {
        toast({
          title: 'Kpi added successfully',
          status: 'success',
          isClosable: true,
          position: 'bottom-right',
        });
        dispatch(actions.createKpiObject(updateItemfetch.body));
      }else{
        toast({
          title: "can't add kpi",
          status: 'error',
          isClosable: true,
          position: 'bottom-right',
        });
      }
      dispatch(actions.itemUpdatedFetch({}));
    }
  }, [updateItemfetch]);

  useEffect(() => {
    if (createKpiError !== null && createKpiError === 3) {
      toast({
        title: 'Something went wrong',
        status: 'error',
        isClosable: true,
        position: 'bottom-right',
      });
      dispatch(actions.kpiCraeteError(null));
    }
  }, [createKpiError]);
 
  const addEventHandler = (eventType) => {
    if(eventType === 'Start' ){
      if (!startEventName) {
        toast({
        title: 'Please Enter Name',
          status: 'error',
          isClosable: true,
          position: 'bottom-right',
      });
        return;
    }
  }
    if(eventType === 'End' ){
      if (!endEventName) {
        toast({
        title: 'Please Enter Name',
          status: 'error',
          isClosable: true,
          position: 'bottom-right',
      });
        return;
    }
  }
    if (startEventName && eventType === 'Start') {
      const eventObj = {
        name: startEventName
      };
      ////console.log(eventObj);
      dispatch(actions.loadCreateEvent(eventObj));
    }
    if (endEventName && eventType === 'End') {
      const eventObj = {
        name: endEventName
      };
      ////console.log(eventObj);
      dispatch(actions.loadCreateEvent(eventObj));
    }
  };

  const addKpiHandler = () => {
    if (kpiMode === '1') {
      if(!kpiName){
        toast({
          title: 'Please Enter Name',
          status: 'error',
          isClosable: true,
          position: 'bottom-right',
        });
        return 
      }
      if(!kpiType){
        toast({
          title: 'Please Select Type',
          status: 'error',
          isClosable: true,
          position: 'bottom-right',
        });
        return 
      }
      if (kpiName !== '' && kpiType !== '') {
        if (kpiType === 'latency' || kpiType === 'streaming') {
          if(!selectStartEvent ||!selectEndEvent ){
            toast({
              title: 'Please Select Event',
              status: 'error',
              isClosable: true,
              position: 'bottom-right',
            });
            return 
          }
          if (selectStartEvent && selectEndEvent) {
          const startEventId = getEvent(eventListData , selectStartEvent)
          const endEventId =  getEvent(eventListData , selectEndEvent)
        if(kpiType === 'streaming'  ){
          if(!operationType){
            toast({
              title: 'Please Select operation Type',
              status: 'error',
              isClosable: true,
              position: 'bottom-right',
            });
            return 
          }
          const kpiObj = {
            name: kpiName,
            type: kpiType,
            operation : operationType,
            event: [
              { eventId : startEventId , sequenceId : 1},
              { eventId : endEventId , sequenceId : 2}
            ],
           
          };
     
          dispatch(actions.loadCreateKpi(kpiObj));
        }else{
          const kpiObj = {
            name: kpiName,
            type: kpiType,
            event: [
              { eventId : startEventId , sequenceId : 1},
              { eventId : endEventId , sequenceId : 2}
            ],
          };
     
          dispatch(actions.loadCreateKpi(kpiObj));
        }
          
            //dispatch(actions.createKpiObject(kpiObj));
          }
        } else if (kpiType === 'state') {
          if(!selectStartEvent){
            toast({
              title: 'Please Select Event',
              status: 'error',
              isClosable: true,
              position: 'bottom-right',
            });
            return 
          }
          const startEventId = getEvent(eventListData, selectStartEvent);
          if (selectStartEvent) {
            const kpiObj = {
              name: kpiName,
              type: kpiType,
              event: [
                { eventId : startEventId , sequenceId : 1}
              ],
            };
            dispatch(actions.loadCreateKpi(kpiObj));
          }
        } else if (kpiType === 'metric') {
          const kpiObj = {
            name: kpiName,
            type: kpiType,
            event: []
          };
          //console.log(kpiObj);
          dispatch(actions.loadCreateKpi(kpiObj));
          //dispatch(actions.createKpiObject(kpiObj));
        } else {
          //console.log('kpi not selected');
        }
      }
    } else if (kpiMode === '2') {
      if(!selectExistKpi){
        toast({
          title: 'Please Select Kpi',
          status: 'error',
          isClosable: true,
          position: 'bottom-right',
        });
        return 
      }
      if (selectExistKpi) {
       
        const filetrKpi = kpiListData.filter(
          kpi => kpi.name === selectExistKpi,
        );

        if (filetrKpi.length > 0) {
         // dispatch(actions.createKpiObject(filetrKpi[0]));
          dispatch(actions.loadUpdateFetchItem({id : filetrKpi[0].uuid , name : "kpi"}));
        }

        //console.log(filetrKpi);
      }
    }
  };

  const props = { setKpiName , kpiName , kpiType ,setKpiType , operationType , setOperationType , 
    setStartEventName , startEventName , selectStartEvent , setSelectStartEvent ,
    setSelectEndEvent , endEventName , eventListData , addEventHandler , setEndEventName ,selectEndEvent ,
};
  return (
    <Stack spacing="10px">
      <LabelFieldComponent label=" Journey Name">
        <Text fontWeight="600">{journeyPayload.name}</Text>
      </LabelFieldComponent>
      <LabelFieldComponent>
        <RadioGroup defaultValue="1" onChange={setKpiMode} value={kpiMode}>
          <Stack spacing={5} direction="row">
            <Radio colorScheme="green" value="1">
              Create New KPI
            </Radio>
            {Object.keys(journeyPayload).length !== 0 &&
              journeyPayload.hasOwnProperty('name') && (
                <Radio colorScheme="green" value="2">
              Choose Existing KPI
            </Radio>) }
          </Stack>
        </RadioGroup>
      </LabelFieldComponent>
    
      { kpiMode === '1' && (
        <>
      <CreateKpi {...props}></CreateKpi>
        </>
   
      )}
      { kpiMode === '2' && (
        <>
           <AddKpi selectExistKpi={selectExistKpi}
    setSelectExistKpi={setSelectExistKpi}
    kpiListData={kpiListData} ></AddKpi>
        </>
      )}
      <LabelFieldComponent>
        <ButtonComp
          buttonHandler={addKpiHandler}
          padding="7px 12px"
          backgroundColor="blue1"
          color="white"
        >
          Add KPI
        </ButtonComp>
      </LabelFieldComponent>
      <AddKpiList kpiPayload={kpiPayload} ></AddKpiList>
 
    </Stack>
  );
}

export default KpiConfiguration;
