import { dateTimeId } from 'utils/gernricfunction';

export const getSignedUrlPayload = (testId: any) => ({
  type: 'execution-screenshots',
  fileName: `${dateTimeId()}.jpg`,
  testId,
});

export const configForImageUpload = (buffer: any) => ({
  method: 'PUT',
  headers: {
    'Content-type': 'image/jpg',
  },
  body: buffer,
});

export const getBuffer = (capturedImage: any) =>
  Buffer.from(capturedImage.replace(/^data:image\/\w+;base64,/, ''), 'base64');
