import { PayloadAction } from '@reduxjs/toolkit';
import { createSlice } from 'utils/@reduxjs/toolkit';
import { useInjectReducer, useInjectSaga } from 'utils/redux-injectors';
import { testDashboardSaga } from './saga';
import { testDashboardState } from './types';

export const initialState: testDashboardState = {
  loading: false,
  error: null,
  testdashboardList: [],
  loadingFolderdash: false,
  errorFolderDash: null,
  testdashboardFolderList: [],
  loadingEmbedUrl: false,
  EmbedUrlData: '',
  EmbedUrlDataError: null,
};

const slice = createSlice({
  name: 'testDashboard',
  initialState,
  reducers: {
    loadtestDashboardData: state => {
      state.loading = true;
    },
    testDashboardDataLoaded: (state, action: PayloadAction<any>) => {
      state.loading = false;
      state.error = null;

      state.testdashboardList = action.payload;
    },
    testDashboardDataError: (state, action: PayloadAction<any>) => {
      state.loading = false;
      state.error = action.payload;
    },
    loadFoldertestDashboard: state => {
      state.loadingFolderdash = true;
    },
    testDashboardFolderLoaded: (state, action: PayloadAction<any>) => {
      state.loadingFolderdash = false;
      state.testdashboardFolderList = action.payload;
      state.errorFolderDash = null;
    },
    testDashboardFolderError: (state, action: PayloadAction<any>) => {
      state.loadingFolderdash = false;
      state.errorFolderDash = action.payload;
    },

    loadEmbedUrl: state => {
      state.loadingEmbedUrl = true;
    },
    EmbedUrlLoaded: (state, action: PayloadAction<any>) => {
      state.loadingEmbedUrl = false;
      state.EmbedUrlData = action.payload;
      state.EmbedUrlDataError = null;
    },
    EmbedUrlLoadError: (state, action: PayloadAction<any>) => {
      state.loadingEmbedUrl = false;
      state.EmbedUrlDataError = action.payload;
    },
  },
});

export const { actions: testDashboardActions } = slice;

export const useTestDashboardSlice = () => {
  useInjectReducer({ key: slice.name, reducer: slice.reducer });
  useInjectSaga({ key: slice.name, saga: testDashboardSaga });
  return { actions: slice.actions };
};

