import Cookies from 'js-cookie';

import cookieUserPool from './cookieUserPool';
import { GetLastAuthUser } from './getUserName';

export const getIdToken = () => {
  const username = GetLastAuthUser();
  const cookie_name = `CognitoIdentityServiceProvider.${cookieUserPool.getClientId()}.${username}.idToken`;
  try {
    return Cookies.get(cookie_name);
  } catch {
    return null;
  }
};
