// @ts-nocheck
import { PayloadAction } from '@reduxjs/toolkit';
import { createSlice } from 'utils/@reduxjs/toolkit';
import { useInjectReducer, useInjectSaga } from 'utils/redux-injectors';

import { HomePageState } from './types';
import { HomepageSaga } from './saga';

export const initialState: HomePageState = {
  paginationPageIndex: [],
  pageIndexValue: {},
  loadingMfatatus: false,
  mfaStausLoaded: {},
  mfaStatusFailed: null,
};

const getPageIndexData = (state, objectData) => {
  if (state.paginationPageIndex.length <= 0) {
    state.paginationPageIndex = [...state.paginationPageIndex, objectData];

    return objectData;
  }
  const findValue = state.paginationPageIndex.filter(
    item => item.key === objectData.key,
  );
  const findindex = state.paginationPageIndex.findIndex(
    item => item.key === objectData.key,
  );
  if (findValue.length <= 0) {
    state.paginationPageIndex = [...state.paginationPageIndex, objectData];
    return objectData;
  } else {
    state.paginationPageIndex[findindex] = objectData;

    return findValue[0];
  }
};

const slice = createSlice({
  name: 'HomePage',
  initialState,
  reducers: {
    updatePageIndex: (state, action: PayloadAction<any>) => {
      state.pageIndexValue = getPageIndexData(state, action.payload);
    },
    restePaginationIndex: (state, action: PayloadAction<any>) => {
      state.pageIndexValue = 0;
      state.paginationPageIndex = [];
    },
    getMfaStatus : (state, action: PayloadAction<any>) =>{
      state.loadingMfaStatus = true;
    },
    mfaStatusLoaded : (state, action: PayloadAction<any>) =>{
      console.log(action.payload)
     state.loadingMfaStatus = false;
     state.mfaStausLoaded = action.payload;
      state.mfaStatusFailed = null;
    },
    mfaStatusFailed : (state, action: PayloadAction<any>) =>{
     state.loadingMfaStatus = false;
     state.mfaStatusFailed = action.payload;
    }
  },
});

export const { actions: HomePageActions } = slice;

export const useHomePageSlice = () => {
  useInjectReducer({ key: slice.name, reducer: slice.reducer });
  useInjectSaga({ key: slice.name, saga: HomepageSaga });
  return { homePageActions: slice.actions };
};
