import { call, put } from 'redux-saga/effects';
import { DevicesErrorType } from '../types';

import { allActions } from '../';
import { request, postRequestConfigForStopSession } from 'utils/request';
import { stopSession as stopSessionUrl } from '../urls';
import { getSavedSessionId, removeSessionFromLocalStorage } from '../../utils';

import getAction from '../utils/getAction';

function* stopSession(action) {
  const { interactType, deviceId } = action.payload;
  const actionName = getAction(interactType);
  const actions = allActions[actionName];

  const sessionId = getSavedSessionId(interactType, deviceId);

  if (!sessionId) {
    console.log('No session id');
    yield undefined;
  }
  try {
    const response = yield call(
      request,
      stopSessionUrl(deviceId, sessionId),
      postRequestConfigForStopSession(),
    );
    if (response?.statusCode === 200) {
      const removesessionId = removeSessionFromLocalStorage(
        interactType,
        deviceId,
      );
    }

    yield put(actions.sessionStarted({ id: null }));
  } catch (e) {
    yield put(actions.sessionStarted({ id: null }));
    yield put(actions.sessionError(DevicesErrorType.RESPONSE_ERROR));
  }
}

export default stopSession;
