// @ts-nocheck
import { Flex, Img } from '@chakra-ui/react';
import React from 'react';
import loadingimage from '../../styles/Assets/load-loading.gif';

function LoadingComponent({
  height,
  width,
  background,
  isOverLay,
  position,
  top,
}) {
  return (
    <Flex
      background={isOverLay || background ? '#3F425429' : 'white'}
      height={height || '300px'}
      width={width || '100%'}
      justifyContent="center"
      alignItems="center"
      position={position || ''}
      zIndex="1"
      top={top}
    >
      <Img height="40px" width="40px" src={loadingimage}></Img>
    </Flex>
  );
}

export default LoadingComponent;
