import { useEffect, useState } from 'react';
import { NoContent, PopupComponent } from 'app/components';
import { StyledCenterWrapper, StyledTitleWrapper } from '../../styles';
import { useDisclosure } from '@chakra-ui/react';
import { useNavigate } from 'react-router-dom';
import ROUTES from 'app/constants/Routes';
import { useInteractSlice } from '../../hooks';

const ErrorMessageBox = ({
  devicesError,
  sessionError,
  deviceBusy,
  interactType,
}) => {
  const { isOpen, onOpen, onClose } = useDisclosure();
  const [message, setMessage] = useState('');
  const { route } = useInteractSlice();
  const navigate = useNavigate();

  function exitButton() {
    console.log('exit end :', new Date());
    navigate(route);
    console.log('navigate listing page :', new Date());
  }

  useEffect(() => {
    if (deviceBusy) {
      setMessage('The device is already in use. ');
    }

    if (devicesError) {
      setMessage('Devices failed to load. ');
    }

    if (sessionError) {
      setMessage('Oops. It looks like someone has taken the device in use. ');
    }
    if (!sessionError && !devicesError && !deviceBusy) {
      setMessage('');
    }
    return () => {
      setMessage('');
    };
  }, [devicesError, sessionError, setMessage, deviceBusy]);

  useEffect(() => {
    if (message) {
      onOpen();
    } else {
      onClose();
    }
  }, [message, onClose, onOpen]);
  useEffect(() => {
    if (isOpen) {
      console.log('exit start :', new Date());
      let timerId = setTimeout(exitButton, 5000);
      return () => clearTimeout(timerId);
    }
  }, [isOpen]);

  if (!message) {
    return null;
  }

  return (
    <>
      <PopupComponent
        title="Interact"
        width="450px"
        isOpen={isOpen}
        onClose={onClose}
        buttonTitle1="Cancel"
        buttonTitle2="Exit"
        buttonHandler1={onClose}
        buttonHandler2={exitButton}
        mainButtonColor="red"
      >
        <StyledCenterWrapper>
          <StyledTitleWrapper>
            {message} Please try after some time
          </StyledTitleWrapper>
        </StyledCenterWrapper>
      </PopupComponent>
      <NoContent message={message} />;
    </>
  );
  //return <NoContent message={message} />;
};

export default ErrorMessageBox;
