import { call, put } from 'redux-saga/effects';

import GetIdToken from 'Authentication/configuration/GetIdToken';
import { allActions } from '../';
import { DevicesErrorType } from '../types';
import { REACT_APP_APP_TESTING_BASE_URL } from 'utils/enviroment';
import { request } from 'utils/request';
import { getSessionType } from '../../utils';

import getAction from '../utils/getAction';

const BASE_URL = REACT_APP_APP_TESTING_BASE_URL;

function* loadDevices(action) {
  const { interactType } = action.payload;
  const testType = getSessionType(interactType);
  const platform = interactType === 'lrInteract' ? 'tv' : '';
  const actionName = getAction(interactType);
  const actions = allActions[actionName];
  const idToken = GetIdToken();
  const devicePlaform = platform ? `?platform=${platform}` : '';
  const testTypeString = testType ? `?testType=${testType}` : '';
  const testTypeParam =
    devicePlaform && testTypeString ? `&testType=${testType}` : testTypeString;
  // const requestUrl = `${BASE_URL}/testexecute/devices`;
  const requestUrl = `${BASE_URL}/testexecute/devices${devicePlaform}${testTypeParam}`;
  const config = {
    method: 'GET',
    headers: {
      'Content-type': 'application/json',
      Authorization: `Bearer ${idToken}`,
    },
  };

  try {
    const response = yield call(request, requestUrl, config);
    if (response.hasOwnProperty('data')) {
      let devices = response.data.list;
      if (!platform) {
        devices = devices.filter(device => device.platform != 'TV');
      }
      yield put(actions.devicesLoaded(devices));
    } else {
      yield put(actions.devicesError(DevicesErrorType.RESPONSE_ERROR));
    }
  } catch (e) {
    yield put(actions.devicesError(DevicesErrorType.TEST_NOT_FOUND));
  }
}

export default loadDevices;
