//@ts-nocheck
import { createSelector } from '@reduxjs/toolkit';

import { initialState } from '.';
import { RootState } from 'types';

const selectSlice = (state: RootState) => initialState;

export const selectLoading = createSelector(
  [selectSlice],
  state => state.isLoading,
);

export const selectUserAuth = createSelector(
  [selectSlice],
  state => state.isAuth,
);
export const selectUserRole = createSelector(
  [selectSlice],
  state => state.role,
);

export const selectError = createSelector([selectSlice], state => state.error);
export const selectCognitoResponse = createSelector(
  [selectSlice],
  state => state.cognitoResponse,
);
export const selectFeatureFlagConfigData = createSelector(
  [selectSlice],
  state => state.featuresFlagDataLoaded,
);
export const selectFeatureFlagConfigError = createSelector(
  [selectSlice],
  state => state.featuresFlagDataLoadingError,
);
export const selectFeatureFlagConfigLoading = createSelector(
  [selectSlice],
  state => state.loadingFeaturesFlag,
);
