import { Flex, HStack, Text } from '@chakra-ui/react';
import React from 'react';
interface propsType {
  label?: string;
  children?: any;
  infoIcon?: any;
  isRequired?: boolean;
}

function LabelFieldComponent({
  label,
  infoIcon,
  children,
  isRequired,
}: propsType) {
  return (
    <HStack>
      <Flex
        width="240px"
        align="center"
        height="35px"
        textTransform="capitalize"
        alignItems="center"
      >
        {label}
        {isRequired && (
          <Text padding="3px" color="red" as="span">
            {' '}
            *
          </Text>
        )}
        {infoIcon}
      </Flex>
      {children}
    </HStack>
  );
}

export default LabelFieldComponent;
