import { PayloadAction } from '@reduxjs/toolkit';
import { createSlice } from 'utils/@reduxjs/toolkit';
import { useInjectReducer, useInjectSaga } from 'utils/redux-injectors';
import { globalSaga } from './saga';
/* import { GlobalState } from './types'; */

export const initialState: any = {
  breadcrumb: [],
  buttonClicked: '',
};

const slice = createSlice({
  name: 'global',
  initialState,
  reducers: {
    resetState(state) {
      Object.assign(state, initialState);
    },
    clearButtonClicked(state) {
      state.buttonClicked = '';
    },
    setBreadcrumb(state, action) {
      state.breadcrumb = action.payload;
    },
    breadcrumbButtonClicked(state, action: PayloadAction<any>) {
      console.log('Breadcrumb button clicked');
      state.buttonClicked = action.payload.fullPath;
    },
  },
});

export const { actions: globalActions } = slice;

export const useGlobalSlice = () => {
  useInjectReducer({ key: slice.name, reducer: slice.reducer });
  useInjectSaga({ key: slice.name, saga: globalSaga });
  return { actions: slice.actions };
};

