// @ts-nocheck
import { Input } from '@chakra-ui/react';
import React, { useState } from 'react';
interface propsType {
  title?: string;
  width?: string | number;
  setInputValue?: any;
  inputValue?: string | number;
  type?: string;
  height?: string;
  padding?: string | number;
  isDisabled?: boolean;
}
function InputComponent({
  title,
  width,
  setInputValue,
  inputValue,
  type,
  height,
  padding,
  isDisabled,
}: propsType) {
  return (
    <Input
      zIndex="0"
      placeholder={title}
      data-input={title}
      size="sm"
      width={width || '230px'}
      color="grey15"
      h="34px"
      isDisabled={isDisabled}
      type={type}
      height={height}
      padding={padding}
      border="1px solid "
      borderColor="grey1"
      borderRadius="5px"
      value={inputValue}
      onChange={e => setInputValue(e.target.value)}
      _focus={{ boxShadow: ' 0 0 0 white', borderColor: 'grey1' }}
    ></Input>
  );
}

export default InputComponent;
