import {forwardRef, useEffect, useRef, useState} from 'react';
import {Box, Flex, useToast} from '@chakra-ui/react';

import {useInteractSlice} from 'app/pages/Interact/hooks';

import {Controls, Error, Loader} from '../../';
import {useStream} from './hooks';

import {
  addKeyListener,
  formatMessage,
  getScaledDimensions,
  mouseDownListener,
  mouseLeaveListener,
  mouseUpListener,
  removeKeyListener,
} from './utils';
import {useLocation} from 'react-router-dom';

interface PropsInterface {
  channelName: any;
  deviceParameters: any;
}

const VideoPlayer = forwardRef((props: PropsInterface, ref) => {
  const {
    interactType,
    selector,
    actions,
    dispatch,
    selector: { session },
  } = useInteractSlice();

  /* const dispatch = useDispatch(); */
  const {
    channelName,
    deviceParameters,
    dummy,
    platform,
    device,
    deviceState: iceConnectionState,
    deviceStates,
    identifier,
    setVideoState,
    setIsConnectedStatus,
    isConnectedStatus,
  } = props;
  const [retryStart, setRetryStart] = useState(0);
  const [retryLoader, setRetryLoader] = useState(false);
  const [portraitOrientedDeviceSize, setPortraitOrientedDeviceSize] = useState({
    width: 0,
    height: 0,
  });
  const [size, setSize] = useState({ width: 400, height: 400 });
  const [scalingFactor, setScalingFactor] = useState({ width: 1, height: 1 });
  const [isPortraitMode, setIsPortraitMode] = useState(true);
  /* const ref = useRef<HTMLElement | null>(null); */
  let sizeInterval;
  const isFirstRun = useRef(true);
  const deviceCategoryRef = useRef(null);
  deviceCategoryRef.current = deviceParameters?.deviceCategory;

  const [videoState, videoError] = useStream(channelName, retryStart);
  const [focusedElement, setFocusedElement] = useState(document.activeElement);
  const notSrcObject = !ref?.current?.srcObject;

  const [isConnected, setIsConnected] = useState(false);
  const [isStreamAvailable, setIsStreamAvailable] = useState(false);

  const location = useLocation().pathname;

  const [toastIds, setToastIds] = useState([]);

  const toast = useToast();

  useEffect(() => {
    const isConnected = location.includes('/lrtesting/automation')
      ? isConnectedStatus.main && isConnectedStatus.remote
      : isConnectedStatus.main;
    if (isConnected && isStreamAvailable) {
      dispatch(actions.stream(true));
    } else {
      dispatch(actions.stream(false));
    }
    return () => {
      dispatch(actions.stream(false));
    };
  }, [deviceStates, isStreamAvailable, isConnectedStatus]);

  useEffect(() => {
    ref.current.addEventListener('playing', () => {
      console.log('CONNECTION STATE: Video is now playing');
      setIsStreamAvailable(true);
    });
    ref.current.addEventListener('error', event => {
      console.error('CONNECTION STATE: Video error', event);
      setIsStreamAvailable(false);
      setIsConnectedStatus(prev => {
        return { ...prev, [identifier]: false };
      });
      if (!props.isCleanExit)
        toast({
          title:
            'Unfortunately the connection tot he device could not be established or was disconnected. Please press the retry button',
          status: 'error',
          isClosable: true,
          position: 'bottom-right',
        });
    });
    ref.current.addEventListener('loadedmetadata', () => {
      console.log('Video playback loadmetadata');
    });
    ref.current.addEventListener('canplay', () => {
      console.log('Video playback canplay');
      ref.current.play?.()
    });
    ref.current.addEventListener('ended', () => {
      setIsStreamAvailable(false);
      setIsConnectedStatus(prev => {
        return { ...prev, [identifier]: false };
      });
      console.log('CONNECTION STATE: Video playback ended');
    });

    const handleFocusChange = () => {
      setFocusedElement(document.activeElement);
    };

    document.addEventListener('focusin', handleFocusChange);

    console.log('ref.current setup listeners complete!', ref.current)

    return () => {
      console.log('Stopping video');
      document.removeEventListener('focusin', handleFocusChange);
      removeKeyListener();
      videoState && videoState.stopViewer();
    };
  }, []);

  useEffect(() => {
    // Run your desired function whenever the focusedElement changes
    console.log('Focused element changed:', focusedElement);
    // Perform any actions based on the focusedElement

    // Example: If the focusedElement is an input element, execute some code
    if (
      focusedElement &&
      (focusedElement.tagName.toLowerCase() === 'input' ||
        focusedElement.tagName.toLowerCase() === 'textarea')
    ) {
      console.log('The focused element is an input element.');
      // Perform actions specific to input elements
      removeKeyListener();
    } else {
      console.log('The focused element is NOT an input element.');
      addKeyListener(channelName, ref);
    }
  }, [focusedElement, channelName, ref]);

  useEffect(() => {
    const handleResize = () => {
      console.log('handleResize', { portraitOrientedDeviceSize });
      if (portraitOrientedDeviceSize.width) {
        const { width, height } = portraitOrientedDeviceSize;
        const {
          streamWidth,
          streamHeight,
          widthScalingFactor,
          heightScalingFactor,
        } = getScaledDimensions(width, height, 0.7);
        setScalingFactor({
          width: isPortraitMode ? widthScalingFactor : heightScalingFactor,
          height: isPortraitMode ? heightScalingFactor : widthScalingFactor,
        });
        ref.current.scalingFactor = {
          width: isPortraitMode ? widthScalingFactor : heightScalingFactor,
          height: isPortraitMode ? heightScalingFactor : widthScalingFactor,
        };
        ref.current.widthForGesture = isPortraitMode ? width : height;
        ref.current.heightForGesture = isPortraitMode ? height : width;
        console.log('Setting video size', streamWidth, streamHeight);
        setSize({
          width: isPortraitMode ? streamWidth : streamHeight,
          height: isPortraitMode ? streamHeight : streamWidth,
        });
      }
    };

    handleResize();
    window.addEventListener('resize', handleResize);

    const sizeInterval = setInterval(() => {
      if (!dummy) {
        if (ref?.current?.videoWidth > ref?.current?.videoHeight) {
          console.log('Setting Portrait Mode to false');
          setIsPortraitMode(false);
        } else {
          setIsPortraitMode(true);
        }
      }
    }, 2000);

    return () => {
      window.removeEventListener('resize', handleResize);
      clearInterval(sizeInterval);
    };
  }, [dummy, portraitOrientedDeviceSize, isPortraitMode, ref]);

  useEffect(() => {
    if (
      channelName &&
      Object.keys(deviceParameters).length &&
      // run only once
      isFirstRun.current
    ) {
      const { width, height } = deviceParameters;
      let portraitOrientedWidth = width;
      let portraitOrientedHeight = height;
      if (!dummy) {
        if (Number(width) > Number(height)) {
          portraitOrientedWidth = height;
          portraitOrientedHeight = width;
        }
      }

      console.log('Setting portrait width, height', {
        portraitOrientedWidth,
        portraitOrientedHeight,
      });
      setPortraitOrientedDeviceSize({
        width: portraitOrientedWidth,
        height: portraitOrientedHeight,
      });

      isFirstRun.current = false;
    }
  }, [dummy, channelName, deviceParameters]);

  console.log("RERENDER:: videoState --> ", videoState, "ref --> ", ref, "srcObject: ", ref?.current?.srcObject)

  useEffect(() => {
    if (videoState && videoState.remoteStream && !ref.current.srcObject) {
      setRetryLoader(false);
      console.log('Setting srcObject property for video element');

      window.myvideoState = videoState

      try {
        console.log({ scalingFactor });
        ref.current.addEventListener('mousedown', event => {
              console.log("event --> ", event, ref, videoState)
              mouseDownListener(
                  event,
                  scalingFactor,
                  channelName,
                  ref,
                  deviceCategoryRef,
              );
            },
        );
        ref.current.addEventListener('mouseup', event =>
          mouseUpListener(event, ref, channelName),
        );
        ref.current.addEventListener('mouseleave', event =>
          mouseLeaveListener(event, ref, channelName),
        );
        ref.current.videoState = videoState;
        ref.current.sendMessage = videoState.sendMessage;
        ref.current.srcObject = videoState.remoteStream;
      } catch (e) {
        console.log(e);
      }
      if (videoState && videoState.remoteStream) {
        ref.current.srcObject = videoState.remoteStream;
      }
    }
  }, [videoState, channelName, scalingFactor, ref?.current?.srcObject]);

  useEffect(() => {
    setIsConnected(false);
    // dispatch(actions.stream(false));
    if (iceConnectionState === 'checking' && !props.isCleanExit) {
      console.log('CONNECTION STATE: Ice Connecting');
      const newToast = toast({
        title:
          'Connecting to your device. Please wait, this can take a few seconds',
        status: 'success',
        isClosable: true,
        position: 'bottom-right',
      });
      setToastIds(prev => [...prev, newToast]);
      setIsConnectedStatus(prev => {
        return { ...prev, [identifier]: false };
      });
    } else if (
      iceConnectionState === 'connected' &&
      !isStreamAvailable &&
      !props.isCleanExit
    ) {
      console.log('CONNECTION STATE: Ice Connected, waiting for video stream');
      const newToast = toast({
        title:
          'Attempting to mirror your device screen now, Hold on.. we are almost there',
        status: 'success',
        isClosable: true,
        position: 'bottom-right',
      });
      setToastIds(prev => [...prev, newToast]);
      setIsConnectedStatus(prev => {
        return { ...prev, [identifier]: false };
      });
    } else if (iceConnectionState === 'connected' && isStreamAvailable) {
      console.log('CONNECTION STATE: Ice video stream Connected');

      toastIds.forEach(item => {
        toast.close(item);
      });

      setToastIds([]);
      setIsConnected(true);
      setIsConnectedStatus(prev => {
        return { ...prev, [identifier]: true };
      });
    } else if (
      (iceConnectionState === 'failed' ||
        iceConnectionState === 'disconnected') &&
      !props.isCleanExit
    ) {
      console.log('CONNECTION STATE: Ice connection failed/disconnected');
      const newToast = toast({
        title:
          'Unfortunately the connection to the device could not be established or was disconnected. Please press the retry button',
        status: 'error',
        isClosable: true,
        position: 'bottom-right',
      });
      setToastIds(prev => [...prev, newToast]);

      setIsConnectedStatus(prev => {
        return { ...prev, [identifier]: false };
      });
    } else if (
      iceConnectionState === 'closed' &&
      session &&
      !props.isCleanExit
    ) {
      console.log('CONNECTION STATE: Ice connection is closed');
      const newToast = toast({
        title:
          'The connection to the device was closed. In case you want to reconnect, please exit this session and start again',
        status: 'error',
        isClosable: true,
        position: 'bottom-right',
      });
      setToastIds(prev => [...prev, newToast]);

      setIsConnectedStatus(prev => {
        return { ...prev, [identifier]: false };
      });
    }
  }, [session, videoState, iceConnectionState, isStreamAvailable]);

  useEffect(() => {
    return () => {
      if (ref.current?.srcObject) {
        ref.current.srcObject = null
      }
    }
  }, []);

  const retryCallback = () => {
    setRetryLoader(true);
    setRetryStart(prev => prev + 1);
    ref.current.srcObject = null;
    if (identifier === 'remote') {
      setTimeout(() => {
        setRetryLoader(true);
        setRetryStart(prev => prev + 1);
        ref.current.srcObject = null;
      }, 5000);
    }
  };

  const onSpecialKeyPress = key => {
    let msg = ['input.keyDown', channelName, { key }];
    ref.current.sendMessage(formatMessage(msg));
  };

  return (
    <>
      <Error
        onExitSession={props.onExitSession}
        controllerError={videoError}
        controllerState={videoState}
        retryCallback={retryCallback}
      />
      <Flex direction="column">
        <Box position={'relative'}>
          <Loader
            style={{ position: 'absolute', zIndex: 1 }}
            overlay
            show={!isConnected}
            // message={setLoaderMessage(iceConnectionState)}
          />
          <video
            className="return-view"
            ref={ref}
            autoPlay
            playsInline
            disablePictureInPicture
            style={{
              width: size.width,
              height: size.height,
            }}
            /* $size={size} */
          />
        </Box>
        <Box>
          <Controls
            platform={platform}
            dummy={dummy}
            retryCallback={retryCallback}
            device={device}
            onSpecialKeyPress={onSpecialKeyPress}
            isConnected={isConnected}
          />
        </Box>
      </Flex>
    </>
  );
});

export default VideoPlayer;
