import { Flex, Text, useDisclosure } from '@chakra-ui/react';
import PopupComponent from 'app/components/PopupComponent';
import { useEffect } from 'react';

import { useInteractSlice } from '../../hooks';

const RemoteSelection = ({ showRemoteSelection, onSelection, isCleanExit }) => {
  const { isOpen, onOpen, onClose } = useDisclosure();
  const {
    interactType,
    selector: {
      devicesLoading,
      devicesError,
      responseTimes,
      sessionLoading,
      session,
      sessionError,
    },
    dispatch,
    device,
    globalSelector: { buttonClicked },
    globalActions,
  } = useInteractSlice();

  useEffect(() => {
    if (showRemoteSelection) {
      onOpen();
    }
  }, [showRemoteSelection, onOpen]);

  if (isCleanExit && !showRemoteSelection) {
    return null;
  }

  return (
    <PopupComponent
      title="Interact"
      width="450px"
      isOpen={isOpen}
      onClose={onClose}
      buttonTitle1="Cancel"
      buttonTitle2="Exit"
      buttonHandler1={onClose}
      buttonHandler2={onSelection}
    >
      <Flex justify="center" width="100%" align="center" justifyItems="center">
        <Text textAlign="center">Please select remote</Text>
      </Flex>
    </PopupComponent>
  );
};

export default RemoteSelection;
