// @ts-nocheck
import { Text, Flex, Box, Link, Tab } from '@chakra-ui/react';
import { NavLink } from 'react-router-dom';
import { useTestingContext } from 'app/hooks';

function SidebarTabComp({ link, icon, children, sidebarToggle }) {
  const { featureRoute } = useTestingContext();
  const isActive = featureRoute === link;

  return (
    <>
      <Flex height="50px" color="grey5" fontWeight="500" title={children}>
        <Link
          as={NavLink}
          data-link-tab={`data-side-tab-${children}`}
          width="100%"
          textDecoration="none"
          _hover={{ textDecoration: 'none' }}
          _focus={{ boxShadow: ' 0 0 0 white' }}
          to={link}
          style={
            isActive
              ? {
                  borderRight: '3px solid #F51C5E',
                  backgroundColor: '#1B1B28',
                  //color: '#FAFBFC',
                  color: '#3699FF',
                  fontWeight: 600,
                }
              : {}
          }
        >
          <Flex
            height="100%"
            alignItems="center"
            data-tab-id={children}
            className="activeTab"
          >
            <Box margin=" 0px 13px" className="activeTabIcon">
              {icon}
            </Box>
            {sidebarToggle && (
              <Text
                opacity={sidebarToggle ? 1 : 0}
                visible={sidebarToggle ? 'visible' : 'hidden'}
                title={children}
              >
                {children}
              </Text>
            )}
          </Flex>
        </Link>
      </Flex>
    </>
  );
}

export default SidebarTabComp;
