// @ts-nocheck
import { getEmailId } from 'Authentication/configuration/GetEmail';
import GetIdToken from 'Authentication/configuration/GetIdToken';
import { GetLastAuthUser } from 'Authentication/configuration/UserName';
import { call, put, takeLatest, delay } from 'redux-saga/effects';
import { request } from 'utils/request';
import { testDashboardActions as actions } from '.';
import { getCognitoAuthenticatedOpenId } from '../components/configUser';
import { testDashboardErrorType } from './types';
import { REACT_APP_APP_TESTING_BASE_URL, REACT_APP_AWS_QUICKSIGHT_FOLDER_ID } from 'utils/enviroment';
const username = GetLastAuthUser();
const email = getEmailId();

const accountId = process.env.REACT_APP_AWS_QUICKSIGHT_ACCOUNT_ID;
const quicksightfolderId = REACT_APP_AWS_QUICKSIGHT_FOLDER_ID;
const roleArnName = process.env.REACT_APP_AWS_QUICKSIGHT_ROLE_ARN_NAME;
const BASE_URL = REACT_APP_APP_TESTING_BASE_URL;

export function* getDashboardList() {
  const idToken = GetIdToken();
  const openIdToken = yield getCognitoAuthenticatedOpenId()
    .then(authtoken => {
      return authtoken.Token;
    })
    .catch(error => {
      return null;
    });

  const myQueryString = `?accountId=${accountId}
    &dashboardId='1'
    &openIdToken=${openIdToken}
  &authenticated='true' 
    &sessionName=${username}
    &resetDisabled='false'
    &undoRedoDisabled='false'       
    &roleArnName=${roleArnName}       
    &email=${email}       
    `;

  const requestUrl =
    'https://y5lbfasjq0.execute-api.ap-south-1.amazonaws.com/dev/getdashboardlist' +
    myQueryString;

  try {
    const getDashboardListData = yield call(request, requestUrl);

    if (getDashboardListData.hasOwnProperty('DashboardSummaryList')) {
      yield put(
        actions.testDashboardDataLoaded(
          getDashboardListData.DashboardSummaryList,
        ),
      );
    } else {
      yield put(
        actions.testDashboardDataError(testDashboardErrorType.RESPONSE_ERROR),
      );
    }
  } catch (e) {
    yield put(
      actions.testDashboardDataError(testDashboardErrorType.RESPONSE_ERROR),
    );
  }
}
export function* getFolderDashboardList(param) {
  const testTypeString = param.payload ? `&testType=${param.payload}` : '';
  const idToken = GetIdToken();
  const openIdToken = yield getCognitoAuthenticatedOpenId()
    .then(authtoken => {
      return authtoken.Token;
    })
    .catch(error => {
      return null;
    });

  const myQueryString = `?accountId=${accountId}
    &folderId=${quicksightfolderId}
    &openIdToken=${openIdToken}
  &authenticated='true' 
    &sessionName=${username}
    &resetDisabled='false'
    &undoRedoDisabled='false'       
    &roleArnName=${roleArnName}       
    &email=${email}       
    `;

  // const requestUrl =
  //   'https://y5lbfasjq0.execute-api.ap-south-1.amazonaws.com/dev/getlistfolderdashboard' +
  //   myQueryString;
  const requestUrl = `${BASE_URL}/quicksight/listdashboard?folderId=${quicksightfolderId}&openId=${openIdToken}${testTypeString}`;
  const config = {
    method: 'GET',
    headers: {
      'Content-type': 'application/json',
      Authorization: `Bearer ${idToken}`,
    },
  };
  // console.log(openIdToken);

  try {
    const getFolderDashboardListData = yield call(request, requestUrl, config);
    // console.log(getFolderDashboardListData);
    if (
      getFolderDashboardListData.hasOwnProperty('body') &&
      Array.isArray(getFolderDashboardListData.body)
    ) {
      yield put(
        actions.testDashboardFolderLoaded(getFolderDashboardListData.body),
      );
    } else if (
      getFolderDashboardListData.hasOwnProperty('body') &&
      getFolderDashboardListData.body === 'no dashboard found'
    ) {
      yield put(actions.testDashboardFolderLoaded([]));
    } else {
      yield put(
        actions.testDashboardFolderError(testDashboardErrorType.RESPONSE_ERROR),
      );
    }
  } catch (e) {
    yield put(
      actions.testDashboardFolderError(testDashboardErrorType.RESPONSE_ERROR),
    );
  }
}

export function* getDashboardEmbedUrl(param) {
  const idToken = GetIdToken();
  //console.log(param.payload.dashboradId);
  //console.log(param.payload.userRole);

  const openIdToken = yield getCognitoAuthenticatedOpenId()
    .then(authtoken => {
      return authtoken.Token;
      //dispatch(actions.loadtestDashboardData(authtoken.Token))
    })
    .catch(error => {
      return null;
    });

  const myQueryString = `?accountId=${accountId}
   &userRole=${param.payload.userRole}  
   &dashboardId=${param.payload.dashboradId}
    &openIdToken=${openIdToken}
    &authenticated='false'
    &sessionName=${username}
    &resetDisabled='false'
    &undoRedoDisabled='false'       
    &roleArnName=${roleArnName}       
    &email=${email}       
    `;
  // const requestUrl =
  //   'https://y5lbfasjq0.execute-api.ap-south-1.amazonaws.com/dev' +
  //   myQueryString;

  const requestUrl = `${BASE_URL}/quicksight/viewdashboard?dashboardId=${param.payload.dashboradId}&openId=${openIdToken}`;

  const config = {
    method: 'GET',
    headers: {
      'Content-type': 'application/json',
      Authorization: `Bearer ${idToken}`,
    },
  };

  try {
    const embedUrlResponse = yield call(request, requestUrl, config);
    if (
      embedUrlResponse.hasOwnProperty('body') &&
      embedUrlResponse.body.hasOwnProperty('EmbedUrl')
    ) {
      yield put(actions.EmbedUrlLoaded(embedUrlResponse.body.EmbedUrl));
    } else {
      yield put(
        actions.EmbedUrlLoadError(testDashboardErrorType.RESPONSE_ERROR),
      );
    }
  } catch (e) {
    yield put(actions.EmbedUrlLoadError(testDashboardErrorType.RESPONSE_ERROR));
  }
}

export function* testDashboardSaga() {
  yield takeLatest(actions.loadtestDashboardData.type, getDashboardList);
  yield takeLatest(
    actions.loadFoldertestDashboard.type,
    getFolderDashboardList,
  );
  yield takeLatest(actions.loadEmbedUrl.type, getDashboardEmbedUrl);
}
