import { useState, useEffect } from 'react';

import { Button, useDisclosure } from '@chakra-ui/react';

import {
  useSelectRemote,
  useLoadDevices,
  useStartSession,
  useRedirect,
  useSendStartStreamMessage,
  useInteractSlice,
  useStopSession,
  useSendStopStreamMessage,
  useVideoStreamsStarted,
} from '../../hooks';
import {
  Loader,
  InteractionItems,
  Error,
  ExitSessionConfirmation,
  ErrorMessageBox,
  RemoteSelection,
} from '../../components';

import config from 'app/components/SubHeaderItems/Breadcrumb/config';

import ROUTES from 'app/constants/Routes';
import { StyledBorderWrapper } from 'app/components/StyledScrollableContainer/styles';
import { useSelector } from 'react-redux';
import {
  isDeviceBusyAndNotSelfOwned,
  isDeviceSelfOwned,
} from 'app/utils/device';

const Interact = () => {
  const { isOpen, onOpen, onClose } = useDisclosure();
  const [showRemoteSelection, setShowRemoteSelection] = useState(false);
  const [retryStart, setRetryStart] = useState(0);
  const [videoStreamsStarted, setVideoStreamsStarted] = useState(false);
  const [isCleanExit, setIsCleanExit] = useState(false);
  const [deviceStopTried, setDeviceStopTried] = useState(false);
  const [isSessionStopped, setIsSessionStopped] = useState(false);

  const {
    interactType,
    selector: {
      devicesLoading,
      devicesError,
      sessionLoading,
      session,
      sessionError,
      selectedRemote,
    },
    dispatch,
    actions,
    device,
    globalSelector: { buttonClicked },
    globalActions,
    getEmailId,
  } = useInteractSlice();

  useEffect(() => {
    return () => {
      dispatch(actions.resetRemote());
      dispatch(actions.clearSession());
    };
  }, []);

  useLoadDevices();

  useEffect(() => {
    if (Object.keys(device)) {
      dispatch(
        globalActions.setBreadcrumb([
          ...config[interactType],
          { title: device.modelName },
        ]),
      );
    }
  }, [interactType, device, dispatch, globalActions]);

  useSelectRemote(setShowRemoteSelection);

  useEffect(() => {
    setShowRemoteSelection(false);
  }, [selectedRemote]);

  useStartSession(isCleanExit);

  const [controllerState, controllerError] = useSendStartStreamMessage(
    retryStart,
    isCleanExit,
  );

  useVideoStreamsStarted(
    controllerState,
    setVideoStreamsStarted,
    setRetryStart,
    isCleanExit,
  );

  useEffect(() => {
    dispatch(actions.exitSession(false));
    if (isOpen) {
      dispatch(globalActions.clearButtonClicked());
    }
  }, [isOpen, dispatch, globalActions]);

  useEffect(() => {
    if (
      !isOpen &&
      (buttonClicked === `${ROUTES.MOBILE_INTERACT}/${device.serial}` ||
        buttonClicked === `${ROUTES.LR_INTERACT}/${device.serial}`)
    ) {
      onOpen();
    }
  }, [buttonClicked, device, isOpen, onOpen]);

  useSendStopStreamMessage(isCleanExit, setDeviceStopTried);

  useStopSession(deviceStopTried, setIsSessionStopped);

  useRedirect(isSessionStopped);

  const onRemoteSelected = remote =>
    dispatch(actions.setSelectedRemote(remote));
  const retryCallback = () => setRetryStart(prev => prev + 1);
  const onExitSessionState = () => {
    dispatch(actions.exitSession(true));
  };
  const onExitSession = () => {
    setIsCleanExit(true);
  };
  console.log({ interactType, selectedRemote });

  return (
    <StyledBorderWrapper>
      <Loader
        show={
          devicesLoading ||
          sessionLoading ||
          isCleanExit ||
          (session && !controllerState && !controllerError)
        }
        overlay
        message={`${
          isCleanExit ? 'Exiting' : 'Starting'
        } your session, please hold`}
        // message={`Starting your session, please hold`}
      />
      <ErrorMessageBox
        devicesError={devicesError}
        sessionError={sessionError}
        interactType={interactType}
        deviceBusy={
          device &&
          Object.keys(device).length &&
          isDeviceBusyAndNotSelfOwned(device, getEmailId())
        }
      />
      <RemoteSelection
        showRemoteSelection={showRemoteSelection}
        onSelection={onRemoteSelected}
        isCleanExit={isCleanExit}
      />
      {session ? (
        <>
          <Error
            onExitSession={onExitSession}
            controllerError={controllerError}
            controllerState={controllerState}
            retryCallback={retryCallback}
          />
          <InteractionItems
            isCleanExit={isCleanExit}
            onExitSession={onExitSession}
            videoStreamsStarted={videoStreamsStarted}
            device={device || {}}
            remote={interactType === 'lrInteract' && selectedRemote}
          />
        </>
      ) : null}
      <ExitSessionConfirmation
        isCleanExit={isCleanExit}
        onExitSession={onExitSession}
        onExitSessionState={onExitSessionState}
        onClose={onClose}
        isOpen={isOpen}
      />
    </StyledBorderWrapper>
  );
};

export default Interact;
