import { getExtractMessage } from './getExtractMessage';

const getAppList = async ref => {
  console.log('get apps trigger');
  try {
    return await new Promise(resolve => {
      if (
        ref &&
        ref.current &&
        ref.current.videoState &&
        ref.current.videoState.dataChannel
      ) {
        let apps = [];

        const handleOnMessage = msg => {
          console.log('Received message  get app:', msg?.data);

          const app = getExtractMessage(msg?.data, 'list.packages')
            .trim()
            .replace(/[\n\u0000]/g, '');
          //console.log(app, 'app');
          apps.push({ name: app });
        };

        ref.current.videoState.dataChannel.onmessage = handleOnMessage;

        setTimeout(() => {
          // Remove the onmessage handler
          ref.current.videoState.dataChannel.onmessage = null;

          // Resolve the promise with the collected apps
          resolve(apps);
        }, 1700);
      }
      //  else {
      //   // Reject the promise if the reference is invalid
      //   throw new Error('Invalid reference');
      // }
    });
  } catch (error) {
    console.error(error.message); // Handle the error
    return []; // Provide a default value or handle the error as needed
  }
};

export { getAppList };
