import { useEffect } from 'react';
import { useNavigate } from 'react-router-dom';

import { useInteractSlice } from '.';

const useRedirect = isSessionStopped => {
  const navigate = useNavigate();
  const { route } = useInteractSlice();

  useEffect(() => {
    if (isSessionStopped) {
      navigate(route);
    }
  }, [isSessionStopped, route, navigate]);
};

export default useRedirect;
