import { Flex, Stack, Text } from '@chakra-ui/react';
import LoadingComponent from 'app/components/LoadingComponent';
import SubContainer from 'app/components/SubContainer';
import Subheader from 'app/components/Subheader';
import { selectUserRole } from 'Authentication/slice/selectors';
import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import {
  Routes,
  BrowserRouter,
  Route,
  useParams,
  useLocation,
} from 'react-router-dom';
//import TableauReport from 'tableau-react';
import { getCognitoAuthenticatedOpenId } from './components/configUser';
import DashboardListingComp from './components/DashboardListingComp';
import DashboardListingCompGridView from './components/DashboardListingCompGridView';
import DashboardTiles from './components/DashboardTiles';
//import TableauComponent from './components/TableauComponent';
import TestQuickSight from './components/TestQuickSight';
import { useTestDashboardSlice } from './slice';
import {
  selectLoadingDashboardFolderList,
  selectLoadingDashboardList,
  selectTestDashboardDFolderList,
  selectTestDashboardList,
} from './slice/selectors';
import ROUTES from '../../constants/Routes';

const getFilterDahsboardList = (dashbord, dashboardListDataResponse) => {
  const filterdash = dashboardListDataResponse.filter(
    item => item.DashboardId === dashbord.MemberId,
  );

  return filterdash[0];
};

function TestDashboard() {
  const { actions } = useTestDashboardSlice();
  const [tabroute, setTabroute] = useState(['Session Barometer']);
  const [tabpath, setTabpath] = useState(`${ROUTES.MOBILE_SESSION_BAROMETER}`);
  const [openIdToken, setOpenIdToken] = useState('');
  const [dashboardListData, setDashboardListData] = useState([]);
  const dispatch = useDispatch();

  const loadingList = useSelector(selectLoadingDashboardList);
  const loadingListFolder = useSelector(selectLoadingDashboardFolderList);
  const location = useLocation();
  const dashboardListDataResponse = useSelector(selectTestDashboardList);
  const dashboardListDataFolderResponse = useSelector(
    selectTestDashboardDFolderList,
  );

  useEffect(() => {
    const pathname = location.pathname;
    if (pathname.startsWith('/motesting')) {
      dispatch(
        actions.loadFoldertestDashboard(
          'Mobile Device Availability,Mobile Device Usage,Mobile Test Observability',
        ),
      );
      setTabroute(['Session Barometer']);
      setTabpath(`${ROUTES.MOBILE_SESSION_BAROMETER}`);
    } else if (pathname.startsWith('/lrtesting')) {
      dispatch(
        actions.loadFoldertestDashboard(
          'LR Device Availability,LR Device Usage,LR Test Observability',
        ),
      );
      setTabroute(['Session Barometer']);
      setTabpath(`${ROUTES.LR_SESSION_BAROMETER}`);
    } else {
      dispatch(
        actions.loadFoldertestDashboard(
          'Mobile App Barometer,LR App Barometer',
        ),
      );
      setTabroute(['App Barometer']);
      setTabpath(`${ROUTES.MONITORING_APPBAROMETER}`);
    }
  }, [location]);

  return (
    <Flex flexDirection="column" width="100%" align="center">
      <Subheader path={tabpath} tabroute={tabroute}></Subheader>
      <SubContainer>
        <Stack
          bg="white"
          minHh="80vh"
          display="flex"
          align="center"
          padding="10px 0px"
        //  paddingTop="50px"
        >
          {loadingList || loadingListFolder ? (
            <LoadingComponent height="80vh"></LoadingComponent>
          ) : (
            <DashboardListingCompGridView
              dashboardListData={dashboardListDataFolderResponse}
            ></DashboardListingCompGridView>
          )}
        </Stack>
      </SubContainer>
    </Flex>
  );
}

export default TestDashboard;
