import { useEffect, useRef, useState } from 'react';

import { NoContent } from 'app/components';

import VideoPlayer from './VideoPlayer';
import Sidebar from './Sidebar';

import { StyledVideoPlayer, StyledContainer, StyledSidebar } from './styles';
import { eventEmitter } from 'app/utils/Stream';
import MessageBox from './MessageBox';

const InteractionItems = ({
  device,
  remote,
  videoStreamsStarted,
  onExitSession,
  isCleanExit,
}) => {
  console.log({ remote }, 'change remote');
  const videoRef = useRef();
  const remoteRef = useRef();
  const [errorMessage, setErrorMessage] = useState('');
  const [deviceStates, setDeviceStates] = useState({});
  const [videoState, setVideoState] = useState(null);
  const [isConnectedStatus, setIsConnectedStatus] = useState({});

  useEffect(() => {
    const devices = { [device.serial]: 'checking' };
    if (remote) {
      devices[remote['device-uuid']] = 'checking';
    }
    setDeviceStates(devices);
  }, [device, remote]);

  useEffect(() => {
    const handleExternalValueChange = newValue => {
      setDeviceStates(prev => {
        return { ...prev, ...JSON.parse(newValue) };
      });
    };
    eventEmitter.on('externalValueChange', handleExternalValueChange);

    return () => {
      eventEmitter.removeListener(
        'externalValueChange',
        handleExternalValueChange,
      );
    };
  }, []);

  useEffect(() => {
    if (
      device &&
      device.deviceParameters &&
      (!device.deviceParameters.height || !device.deviceParameters.width)
    ) {
      setErrorMessage('Device Configuration Error');
    }
  }, [device, setErrorMessage]);

  if (!device || !Object.keys(device).length) {
    return null;
  }

  if (errorMessage) {
    return <NoContent message={errorMessage} />;
  }

  if (!videoStreamsStarted) {
    return null;
  }

  return (
    <StyledContainer>
      <MessageBox
        ref={videoRef}
        onExitSession={onExitSession}
        device={device}
      ></MessageBox>
      <StyledVideoPlayer>
        <VideoPlayer
          identifier="main"
          isCleanExit={isCleanExit}
          onExitSession={onExitSession}
          dummy={remote}
          ref={videoRef}
          channelName={device.serial}
          platform={device?.platform}
          deviceParameters={device.deviceParameters}
          deviceStates={deviceStates}
          deviceState={deviceStates[device.serial] || 'checking'}
          setVideoState={setVideoState}
          setIsConnectedStatus={setIsConnectedStatus}
          isConnectedStatus={isConnectedStatus}
        />
      </StyledVideoPlayer>
      <StyledSidebar>
        <Sidebar ref={videoRef} device={device} videoState={videoState}>
          {remote && remote.width && remote.height ? (
            <VideoPlayer
              identifier="remote"
              onExitSession={onExitSession}
              isCleanExit={isCleanExit}
              ref={remoteRef}
              platform={device?.platform}
              channelName={remote['device-uuid']}
              deviceParameters={remote}
              deviceStates={deviceStates}
              deviceState={deviceStates[remote['device-uuid']] || 'checking'}
              setIsConnectedStatus={setIsConnectedStatus}
              isConnectedStatus={isConnectedStatus}
            />
          ) : null}
        </Sidebar>
      </StyledSidebar>
    </StyledContainer>
  );
};

export default InteractionItems;
