//@ts-nocheck
import { Flex, Image, Text } from '@chakra-ui/react';
import React from 'react';
import mozarkIcon from '../../../styles/Assets/mozark-logo.png';
import iciciLogo from '../../../styles/Assets/icici-logo.png';
import { Link } from 'react-router-dom';
import { useSelector } from 'react-redux';
import { selectFeatureFlagConfigData } from 'Authentication/slice/selectors';
import { getBrandIcon } from 'utils/getBrandIcon';
import CenterLogo from 'app/components/CenterLogo';
const tenant = process.env.REACT_APP_BASE_URL_TITLE;
function NavbarLoginTheme({ title, height }) {
  const features = useSelector(selectFeatureFlagConfigData);

  return (
    <Flex
      fontFamily="Montserrat"
      background="white1"
      height={height || '60px'}
      alignItems="center"
      boxSizing="border-box"
      position="sticky"
      zIndex="2"
      justifyContent="space-between"
      boxShadow="0px 3px 6px #00000029"
      backgroundColor="white1"
      color="black2"
    >
      <Flex alignItems="center" height="100%">
        <Flex
          w="170px"
          alignItems="center"
          marginBottom="10px"
          paddingLeft="10px"
          height="40px"
        >
          <Link to="/">
            <Image
              src={
                features.features.enable['client-org-logo-enable']
                  ? iciciLogo
                  : mozarkIcon
              }
              alt="Brand"
            />
          </Link>
        </Flex>
      </Flex>

      {features.features.enable['client-org-center-logo-enable'] &&
      getBrandIcon(tenant) ? (
        <CenterLogo />
      ) : (
        <Text fontSize="22px" fontWeight="600">
          {title}
        </Text>
      )}

      <Flex w="170px" alignItems="center" marginRight="10px" height="40px">
        {features.features.enable['client-org-logo-enable'] && (
          <Image src={mozarkIcon} alt="Brand" />
        )}
      </Flex>
    </Flex>
  );
}

export default NavbarLoginTheme;
