import SparkMD5 from 'spark-md5';

export const dateTimeConverter = dateStringValue => {
  // console.log(dateStringValue);
  if (
    dateStringValue === null ||
    dateStringValue === undefined ||
    dateStringValue === ''
  ) {
    return '';
  }

  const date = new Date(dateStringValue).toString().slice(4, 24).split(' ');
  const day = date[1];
  const month = date[0];
  date[0] = day;
  date[1] = month;
  const convertedDate = date.join(' ');
  //console.log(convertedDate);

  // const dateSplit = new Date(dateStringValue)
  //   .toLocaleString('en-ca', { hour12: false })
  //   .split(', ')[0];
  // const timeSplit = new Date(dateStringValue)
  //   .toLocaleString('en-GB')
  //   .split(', ')[1];

  // const convertedDateTimeLocal = dateSplit + 'T' + timeSplit;
  return convertedDate;
};

export const dateTimeConverterWithTimeZone = dateStringValue => {
  const dateSplit = new Date(dateStringValue)
    .toLocaleString('en-ca', { hour12: false })
    .split(', ')[0];
  //start datestring formate code converter 2/10/2023 -> 2023-02-10
  const datestringSplite = new Date(dateSplit);

  const year = datestringSplite.getFullYear();
  const month = (datestringSplite.getMonth() + 1).toString().padStart(2, '0');
  const day = datestringSplite.getDate().toString().padStart(2, '0');

  const formattedDate = `${year}-${month}-${day}`;
  //end formater
  const timeSplit = new Date(dateStringValue)
    .toLocaleString('en-GB')
    .split(', ')[1];
  const gmtString = new Date(dateStringValue).toString().split(' ')[5];
  let gmtFormat = gmtString.split('').splice(3, 5, ':');
  gmtFormat.splice(3, 0, ':');
  const gmtValue = gmtFormat.join('');
  const convertedDateTime = formattedDate + 'T' + timeSplit + gmtValue;
  return convertedDateTime;
};

export const getMd5SumFilesTest = files => {
  var blobSlice =
    File.prototype.mozSlice ||
    File.prototype.webkitSlice ||
    File.prototype.slice;

  const file = files;
  let chunkSize = 2097152;
  let chunks = Math.ceil(file.size / chunkSize);
  let currentChunk = 0;
  const spark = new SparkMD5.ArrayBuffer();
  var fileReader = new FileReader();
  if (chunks > 4) {
    chunks = 4;
  }

  fileReader.onload = function (e) {
    spark.append(e.target.result);

    currentChunk++;

    if (currentChunk < chunks) {
      loadNext();
    } else {
      // console.log( 'MD5 hash:' + spark.end());
      // const md5sum = spark.end();
      //  console.log(md5sum);
    }
  };

  function loadNext() {
    let start = 0;
    let end = 0;
    start = currentChunk * chunkSize;
    end = start + chunkSize >= file.size ? file.size : start + chunkSize;

    fileReader.readAsArrayBuffer(blobSlice.call(file, start, end));
  }

  loadNext();
};

export const dateTimeId = () => {
  let dd = new Date().toString().split(' ');
  let year = dd[3];
  let month = ('0' + new Date().getMonth()).slice(-2);
  let day = dd[2];
  let time = dd[4].replaceAll(':', '-');
  // let zone ='+' + dd[5].split('+')[1].slice(0, 2) + ':' + dd[5].split('+')[1].slice(2);
  let ms = new Date().getMilliseconds();
  let result = year + '-' + month + '-' + day + 'T' + time + ms;

  // console.log(result);
  return result;
};

export const getMd5SumFiles = (files, setMd5Sum) => {
  var blobSlice =
    File.prototype.mozSlice ||
    File.prototype.webkitSlice ||
    File.prototype.slice;

  const file = files.files[0];
  let chunkSize = 2097152;
  let chunks = Math.ceil(file.size / chunkSize);
  let currentChunk = 0;
  const spark = new SparkMD5.ArrayBuffer();
  var fileReader = new FileReader();
  if (chunks > 4) {
    chunks = 4;
  }

  fileReader.onload = function (e) {
    spark.append(e.target.result);

    currentChunk++;

    if (currentChunk < chunks) {
      loadNext();
    } else {
      /// console.log( 'MD5 hash:' + spark.end());
      const md5sum = spark.end();
      setMd5Sum(md5sum);
    }
  };

  function loadNext() {
    let start = 0;
    let end = 0;
    start = currentChunk * chunkSize;
    end = start + chunkSize >= file.size ? file.size : start + chunkSize;

    fileReader.readAsArrayBuffer(blobSlice.call(file, start, end));
  }

  loadNext();
};

