export const stringSaprater = string => {
  if (string) {
    let str = string.split('/');

    return str[str.length - 1];
  }
  return '';
};

export const dateTimeConverterWithOffset = dateObj => {
  function Formatter(number) {
    return ('0' + number).slice(-2);
  }
  function ZoneOffset(seconds) {
    var sec, min, hour;
    if (seconds < 3600) {
      var a = Math.floor(seconds / 60); //minutes
      var b = seconds % 60; //seconds

      if (b != 1) {
        sec = 'seconds';
      } else {
        sec = 'second';
      }

      if (a != 1) {
        min = 'minutes';
      } else {
        min = 'minute';
      }

      return `${Formatter(0)}${Formatter(a)}`;
    } else {
      var a = Math.floor(seconds / 3600); //hours
      var x = seconds % 3600;
      var b = Math.floor(x / 60); //minutes
      var c = seconds % 60; //seconds

      if (c != 1) {
        sec = 'seconds';
      } else {
        sec = 'second';
      }

      if (b != 1) {
        min = 'minutes';
      } else {
        min = 'minute';
      }

      if (c != 1) {
        hour = 'hours';
      } else {
        hour = 'hour';
      }
      return `${Formatter(a)}${Formatter(b)}`;
    }
  }

  return `${dateObj.dateTime.date.year}-${Formatter(
    dateObj.dateTime.date.month,
  )}-${Formatter(dateObj.dateTime.date.day)}T${Formatter(
    dateObj.dateTime.time.hour,
  )}:${Formatter(dateObj.dateTime.time.minute)}:${Formatter(
    dateObj.dateTime.time.second,
  )}+${ZoneOffset(dateObj.zone.totalSeconds)}`;
};

export const dateConvertToFormat = date => {
  if (date === '' || date === null) {
    return null;
  }

  //const date  = '26-10-2022'
  let datevalue = date.toString().split('-');
  const day = datevalue[0];
  const year = datevalue[2];
  datevalue[0] = year;
  datevalue[2] = day;
  //console.log(datevalue.join('-'))
  const dateval = new Date(datevalue.join('-'));
  const dateformat = dateval.toDateString().slice(4);
  // console.log(dateformat)
  return dateformat;
};

export const getStatusColorDevice = deviceParameters => {
  if (deviceParameters?.deviceStatus) {
    if (deviceParameters.deviceStatus === 'busy') {
      return 'red2';
    } else if (deviceParameters.deviceStatus === 'disconnected') {
      return 'grey5';
    } else {
      return 'green2';
    }
  } else {
    return 'red2';
  }
};
