const getViewportHeight = () => {
  // Get the viewport height
  // The value of innerHeight is taken from the height of the window's layout viewport.
  return window.innerHeight;
};

function roundToFourDecimals(number) {
  return Number(number.toFixed(4));
}

const getScaledDimensions = (width, height, scalingFactor = 0.7) => {
  const streamHeight = roundToFourDecimals(getViewportHeight() * scalingFactor);

  const aspectRatio = width / height;

  const streamWidth = roundToFourDecimals(streamHeight * aspectRatio);
  const widthScalingFactor = roundToFourDecimals(width / streamWidth);
  const heightScalingFactor = roundToFourDecimals(height / streamHeight);

  console.log('dimensions', {
    width,
    height,
    streamWidth,
    streamHeight,
    widthScalingFactor,
    heightScalingFactor,
  });

  return { streamWidth, streamHeight, widthScalingFactor, heightScalingFactor };
};

function getScaledCoordinates(x, y, scaleRatioX, scaleRatioY) {
  return { scaledX: x * scaleRatioX, scaledY: y * scaleRatioY };
}

export { getScaledDimensions, getScaledCoordinates };
