import { ChevronRightIcon } from '@chakra-ui/icons';
import {
  Breadcrumb,
  BreadcrumbItem,
  BreadcrumbLink,
  BreadcrumbSeparator,
  Flex,
  Text,
  Box,
  Container,
  Icon,
} from '@chakra-ui/react';
import SearchBar from 'app/pages/TestAnalytics/components/SearchBar';
import TestTpyeTabComp from 'app/pages/TestAnalytics/components/TestTpyeTabComp';
import { useTestAnalyticsSlice } from 'app/pages/TestAnalytics/testAnalyticsSlice';
import { selectSeacrhKey } from 'app/pages/TestAnalytics/testAnalyticsSlice/selectors';
import React, { useState } from 'react';
import { BiSearch } from 'react-icons/bi';
import { useDispatch, useSelector } from 'react-redux';
import { Link } from 'react-router-dom';
import InfoComp from '../pages/TestSession/components/SubheaderSessionId';
import ButtonComp from './ButtonComp';

function Subheader({
  infoStatusData,
  tabroute,
  children,
  searchItemValue,
  setSearchItemValue,

  resetSearchHandler,
  path,
  isTestType,
}) {
  // const [searchItemValue, setSearchItemValue] = useState();
  const dispatch = useDispatch();
  const { testAnalyticsactions } = useTestAnalyticsSlice();
  // const getSearchKey = useSelector(selectSeacrhKey);
  const searchItemsHandler = () => {
    const regexSearchValidation = /[^a-zA-Z0-9_-]/;
    if (
      searchItemValue === '' ||
      searchItemValue.match(regexSearchValidation)
    ) {
      return;
    }
    dispatch(testAnalyticsactions.triggerSerachKey(searchItemValue));
    dispatch(testAnalyticsactions.resetPageIndex({ tabType: '', value: 0 }));
    //console.log(searchItemValue)
  };

  const enterKeyHandler = event => {
    if (event.keyCode === 13) {
      searchItemsHandler();
    }
  };

  // useSelector(()=>{
  //  if(getSearchKey ===''){
  //   setSearchItemValue('')
  //  }
  // },[getSearchKey])

  return (
    <Flex
      fontFamily="Montserrat"
      fontWeight="500"
      backgroundColor="white1"
      justifyContent="space-between"
      width="100%"
      alignItems="center"
      paddingLeft="20px"
      height="60px"
    >
      <Flex fontSize="16px">
        {tabroute && (
          <Breadcrumb
            separator={<ChevronRightIcon color="black2" />}
            textDecoration="none"
            color="black2"
          >
            {tabroute &&
              tabroute.map((route, index) => (
                <BreadcrumbItem title={route} key={index}>
                  <BreadcrumbLink
                    //  path={path}
                    as={Link}
                    to={path}
                    onClick={resetSearchHandler}
                    _hover={{ textDecoration: 'none', color: 'blue1' }}
                    _focus={
                      false
                        ? { boxShadow: ' 0px 0px 6px #008FE0' }
                        : { boxShadow: '0px 0px 0px transparent' }
                    }
                  >
                    {/* <Link to={path ? path : ''}> */}
                    {route}
                    {/* </Link> */}
                  </BreadcrumbLink>
                </BreadcrumbItem>
              ))}
          </Breadcrumb>
        )}
        {/* {children} */}
      </Flex>

      {isTestType && <TestTpyeTabComp></TestTpyeTabComp>}
      {setSearchItemValue && (
        <Flex paddingRight="20px" width="40%" align="center">
          <SearchBar
            width="70%"
            enterKeyHandler={enterKeyHandler}
            searchItem={searchItemValue}
            setSearchItem={setSearchItemValue}
          ></SearchBar>
          <ButtonComp
            marginLeft="0px"
            padding="9px  10px"
            name="search-testanalytic-key-button"
            buttonHandler={searchItemsHandler}
          >
            <Icon as={BiSearch}></Icon>
          </ButtonComp>
        </Flex>
      )}

      {/* {infoStatusData && (
        <Flex
          height="44px"
          width="269px"
          alignItems="center"
          flexDirection="row"
          justifyContent="space-between"
          padding="5px"
          marginRight="7"
        >
          <Flex marginLeft="0px">
            <Text as="b" paddingRight="7px" fontWeight="500">
              5
            </Text>
            <Text color="grey13">Running </Text>
          </Flex>
          <Flex marginLeft="0px" fontWeight="500">
            <Text as="b" paddingRight="7px">
              2
            </Text>
            <Text color="grey13">Queued </Text>
          </Flex>
          <Flex marginLeft="0px">
            <Text as="b" paddingRight="7px" fontWeight="500">
              1
            </Text>
            <Text color="grey13">Scheduled</Text>
          </Flex>
        </Flex>
      )} */}
    </Flex>
  );
}

export default Subheader;
