// @ts-nocheck
import AWS from 'aws-sdk';
import {
  AuthenticationDetails,
  CognitoUser,
  CognitoUserPool,
  CookieStorage,
} from 'amazon-cognito-identity-js';
import { apiGatewayGetDashboardEmbedUrl } from './TestQuickSight';
import GetIdToken from 'Authentication/configuration/GetIdToken';
import UserPool from 'Authentication/configuration/UserPool';
import { GetLastAuthUser } from 'Authentication/configuration/UserName';
import { getEmailId } from 'Authentication/configuration/GetEmail';
import { getUpdatedToken } from './getUpdatedToken';
const poolId = process.env.REACT_APP_AWS_COGNITO_USERPOOLID;
const awsRegion = process.env.REACT_APP_AWS_REGION;
const awsConnitoIdentyPoolId =
  process.env.REACT_APP_AWS_QUICKSIGHT_COGNITO_IDENTITY_POOL_ID;

export const getCognitoAuthenticatedOpenId = async () => {
  try {
    let idToken = await getUpdatedToken(); // Wait for token update
    AWS.config.update({ region: awsRegion });
    const cognitoIdentity = new AWS.CognitoIdentity();
    const getIdParams = {
      IdentityPoolId: awsConnitoIdentyPoolId,
      Logins: {
        [`cognito-idp.ap-south-1.amazonaws.com/${poolId}`]: idToken,
      },
    };

    return new Promise((resolve, reject) => {
      cognitoIdentity.getId(getIdParams, (err, data) => {
        if (err) {
          reject(err);
        } else {
          data.Logins = {
            [`cognito-idp.ap-south-1.amazonaws.com/${poolId}`]: idToken,
          };

          cognitoIdentity.getOpenIdToken(data, (err, openIdToken) => {
            if (err) {
              reject(err);
            } else {
              resolve(openIdToken);
            }
          });
        }
      });
    });
  } catch (err) {
    throw err; // Propagate error if token update fails
  }
};
