import React, { useEffect, useState } from 'react';
import {
  Box,
  BreadcrumbItem as ChakraBreadcrumbItem,
  Tooltip,
} from '@chakra-ui/react';
import { Link } from 'react-router-dom';
import { ChevronRightIcon } from '@chakra-ui/icons';
import { useDispatch, useSelector } from 'react-redux';

import { SubHeaderItems } from 'app/components';
import { BreadcrumbProps } from './types';
import { useTestingContext } from 'app/hooks';
import { useGlobalSlice } from 'app/slice';
import { selectGlobal } from 'app/slice/selectors';
import { useLocation } from 'react-router-dom';

import { isBreadcrumbPath, findMatchingDetailPath } from './utils';
import { StyledBreadcrumb, StyledFlex, StyledBreadcrumbLink } from './styles';

const Breadcrumb: React.FC<BreadcrumbProps> = () => {
  const { RightContent, ExitButton } = SubHeaderItems;
  const location = useLocation().pathname;
  const isConnected = useSelector(state =>
    location.includes('/motesting/interact')
      ? state?.mobileInteract?.isConnected
      : state?.lrInteract?.isConnected,
  );
  const isExit = useSelector(state =>
    location.includes('/motesting/interact')
      ? state?.mobileInteract?.isExit
      : state?.lrInteract?.isExit,
  );
  const dispatch = useDispatch();
  const { actions } = useGlobalSlice();
  const { breadcrumb } = useSelector(selectGlobal);
  const { fullPath } = useTestingContext();
  const [hide, setHide] = useState(true);

  useEffect(() => {
    const pathConfig = isBreadcrumbPath(fullPath);
    if (pathConfig) {
      setHide(false);
      dispatch(actions.setBreadcrumb(pathConfig));
    } else if (findMatchingDetailPath(fullPath)) {
      setHide(false);
    } else {
      setHide(true);
    }
  }, [fullPath, setHide, actions, dispatch]);

  if (hide || !breadcrumb.length) {
    return null;
  }

  return (
    <StyledFlex>
      <StyledBreadcrumb separator={<ChevronRightIcon color="black2" />}>
        {breadcrumb &&
          breadcrumb.length &&
          breadcrumb.map((item, index) => (
            <ChakraBreadcrumbItem title={item.title} key={index}>
              <StyledBreadcrumbLink as={Link} to={item.path} $isFocused={false}>
                {item.title}
              </StyledBreadcrumbLink>
            </ChakraBreadcrumbItem>
          ))}
      </StyledBreadcrumb>
      <RightContent>
        {findMatchingDetailPath(fullPath) ? (
          <>
            <Tooltip label={isConnected ? 'Connected' : 'Disconnected'}>
              <Box
                width={'10px'}
                height={'10px'}
                borderRadius={'50%'}
                backgroundColor={
                  isExit ? 'red2' : isConnected ? 'green2' : 'red2'
                }
              ></Box>
            </Tooltip>
            <ExitButton
              clickHandler={() =>
                dispatch(actions.breadcrumbButtonClicked({ fullPath }))
              }
            />
          </>
        ) : null}
      </RightContent>
    </StyledFlex>
  );
};

export default Breadcrumb;
