// @ts-nocheck
import { Button, Flex, Text } from '@chakra-ui/react';
import React from 'react';

function NoContent({ message, setAgainHandler, getRefresh, height, width }) {
  return (
    <Flex
      paddingTop="10px"
      background="white"
      height={height || '250px'}
      width={width || '100%'}
      justifyContent="center"
      alignItems="center"
    >
      <Text
        textAlign="center"
        height="40px"
        fontFamily="Montserrat"
        fontWeight="600"
      >
        {message}
        {setAgainHandler && (
          <Text
            textAlign="center"
            color="blue1"
            as="span"
            onClick={() => setAgainHandler(getRefresh++)}
            cursor="pointer"
          >
            Try Again
          </Text>
        )}
      </Text>
    </Flex>
  );
}

export default NoContent;
