import cookieUserPool from './cookieUserPool';
export const GetLastAuthUser = () => {
  const last_auth = `CognitoIdentityServiceProvider.${cookieUserPool.getClientId()}.LastAuthUser`;
  //console.log(auth);
  const reg = new RegExp(`(?<=${last_auth}=)[^;]*`);
  try {
    return document.cookie.match(reg)[0];
  } catch {
    return null;
  }
};
