// @ts-nocheck
import React, { useContext } from 'react';
import { Flex, Icon, Image, Img } from '@chakra-ui/react';
import starticon from '../../../../styles/Assets/file-cog-outline.svg';
import starticon2 from '../../../../styles/Assets/Group 1776.svg';
import notification from '../../../../styles/Assets/notifications.svg';
import userIcon from '../../../../styles/Assets/user profile.svg';
import mozarkIcon from '../../../../styles/Assets/mozark-logo.png';
import { Link } from 'react-router-dom';
import iciciLogo from '../../../../styles/Assets/icici-logo.png';
import { useOutletContext } from 'react-router-dom';
import { useSelector } from 'react-redux';
import { selectFeatureFlagConfigData } from 'Authentication/slice/selectors';
import UserPool from 'Authentication/configuration/UserPool';
import UserInfoCard from './UserInfoCard';
import PopoverComponent from 'app/components/PopoverComponent';
import { AiOutlineHome } from 'react-icons/ai';
import { REACT_APP_LOGOUT_REDIRECTPAGE } from 'utils/enviroment';
import { useAuth } from 'utils/authentication/useAuth';
import { getBrandIcon } from 'utils/getBrandIcon';
import CenterLogo from 'app/components/CenterLogo';

const RedirectPage = REACT_APP_LOGOUT_REDIRECTPAGE;

const StartIcon = () => {
  return (
    <>
      <img src={starticon} alt="start"></img>
    </>
  );
};
const StartIcon2 = () => {
  return (
    <>
      <img src={starticon2} alt="start"></img>
    </>
  );
};

function Navbar({
  startActionHandler,
  stopActionHandler,
  testNotificationData,
  isLoading,
}) {
  //const userRole = ''; //useSelector(selectUserRole);
  const features = useSelector(selectFeatureFlagConfigData);
  const { isAuthenticated, userRole } = useAuth();
  const GetLastAuthUser = () => {
    const last_auth = `CognitoIdentityServiceProvider.${UserPool.getClientId()}.LastAuthUser`;
    const reg = new RegExp(`(?<=${last_auth}=)[^;]*`);
    try {
      return document.cookie.match(reg)[0];
    } catch {
      return null;
    }
  };
  const tenant = process.env.REACT_APP_BASE_URL_TITLE;
  const username = GetLastAuthUser();

  const { logout } = useAuth();
  const userHandler = () => {
    const userlogout = document.getElementById('user');
    if (userlogout.style.display == 'none') {
      userlogout.style.display = 'block';
    } else {
      userlogout.style.display = 'none';
    }
  };
  return (
    <Flex
      fontFamily="Montserrat"
      background="white1"
      height="60px"
      alignItems="center"
      boxSizing="border-box"
      position="sticky"
      zIndex="2"
      justifyContent="space-between"
      boxShadow="0px 3px 6px #00000029"
      backgroundColor="white1"
      color="black2"
    >
      <Flex alignItems="center" height="100%">
        <Flex w="170px" alignItems="center" paddingLeft="10px" height="40px">
          <Link to="/">
            <Image
              src={
                features.features.enable['client-org-logo-enable']
                  ? iciciLogo
                  : mozarkIcon
              }
              alt="Brand"
            />
          </Link>
        </Flex>
      </Flex>
      {features.features.enable['client-org-center-logo-enable'] &&
        getBrandIcon(tenant) && <CenterLogo />}
      <Flex alignItems="center" justifyContent="space-between" marginRight="4">
        <Flex w="170px" alignItems="center" padding="0px 10px" height="40px">
          {features.features.enable['client-org-logo-enable'] && (
            <Image src={mozarkIcon} alt="Brand" />
          )}
        </Flex>
        <Link to="/">
          <Icon
            as={AiOutlineHome}
            margin="0px 20px"
            w="25px"
            h="25px"
            title="Home"
            cursor="pointer"
            color="grey1"
          ></Icon>
        </Link>
        <PopoverComponent
          height={
            features.features.enable['user-management-authentication-2fa']
              ? '260px'
              : '260px'
          }
          width="280px"
          top="5px"
          right="-10px"
          //title="ADD USER"
          button={
            <Img title="User" w="25px" h="25px" src={userIcon} alt="usericon" />
          }
        >
          <UserInfoCard
            userHandler={userHandler}
            RedirectPage={RedirectPage}
            logout={logout}
            username={username}
            userRole={userRole}
          ></UserInfoCard>
        </PopoverComponent>
      </Flex>
    </Flex>
  );
}

export default Navbar;
