import { CognitoUser, CookieStorage } from 'amazon-cognito-identity-js';
import { REACT_APP_AWSCOOKIESTORAGEDOMAIN } from 'utils/enviroment';
import cookieUserPool from './cookieUserPool';

const awsCookieStorageDomain = REACT_APP_AWSCOOKIESTORAGEDOMAIN;
export const getAuthUser = UserName => {
  const userInfo = new CognitoUser({
    Username: UserName,
    Pool: cookieUserPool,
    Storage: new CookieStorage({ domain: awsCookieStorageDomain }),
  });

  return userInfo;
};
