//@ts-nocheck
import { CognitoUserPool, CookieStorage } from 'amazon-cognito-identity-js';
import { REACT_APP_AWSCOOKIESTORAGEDOMAIN } from 'utils/enviroment';

const awsCookieStorageDomain = REACT_APP_AWSCOOKIESTORAGEDOMAIN;
const poolData = {
  UserPoolId: process.env.REACT_APP_AWS_COGNITO_USERPOOLID,
  ClientId: process.env.REACT_APP_AWS_COGNITO_CLIENTID,
  Storage: new CookieStorage({ domain: awsCookieStorageDomain }),
};
export default new CognitoUserPool(poolData);
