import { call, put, select } from 'redux-saga/effects';

import { allActions } from '../';
import { request, postRequestConfig } from 'utils/request';
import { generatePresignedUrl } from '../urls';

import getAction from '../utils/getAction';
import {
  configForImageUpload,
  getBuffer,
  getSignedUrlPayload,
} from '../utils/screenshotUpload';

function* uploadScreenshot(action: any) {
  const { capturedImage, type } = action.payload;

  const actionName = getAction(type);
  const actions = allActions[actionName];

  // testId is sessionId
  const sessionId = yield select(state => state?.[type]?.session);
  const fileconfig = getSignedUrlPayload(sessionId);
  try {
    // get pre-signed url
    yield put(
      actions.screenshotUploadingItem({ ...fileconfig, capturedImage }),
    );
    const presignedUrlResponse = yield call(
      request,
      generatePresignedUrl,
      postRequestConfig(getSignedUrlPayload(sessionId)),
      //fileconfig,
    );

    if (!presignedUrlResponse.hasOwnProperty('url')) {
      yield put(
        actions.screenshotUploadError('Failed to generate presigned url'),
      );
      return undefined;
    }

    // upload
    const uploadResponse = yield call(
      fetch,
      presignedUrlResponse.url,
      configForImageUpload(getBuffer(capturedImage)),
    );
    console.log({ uploadResponse });
    if (uploadResponse.status === 200) {
      yield put(
        actions.screenshotUploadedFile({
          status: 200,
          fileconfig: { ...fileconfig, capturedImage },
          message: 'screenshots uploaded',
        }),
      );
    } else {
      yield put(
        actions.screenshotUploadError(
          'error uploading screenshot',
          uploadResponse,
        ),
      );
    }
  } catch (e) {
    console.log(e);
    yield put(
      actions.screenshotUploadError('error uploading screenshot final', e),
    );
  }
}

export default uploadScreenshot;
