import {Overlay} from './styles';
import RotatingLines from './RotatingLines';
import {Box, Flex} from '@chakra-ui/react';

const Loader = ({show, overlay = false, message, style, messageStyle}) => {
  if (!show) {
    return null;
  }

  if (overlay) {
    if (message) {
      return (
        <Overlay style={style}>
          <Flex direction="column" alignItems="center">
            <RotatingLines />
            {message ? (
                <Box marginTop="10px" fontWeight="600" style={messageStyle}>
                {message}
              </Box>
            ) : null}
          </Flex>
        </Overlay>
      );
    } else {
      return (
        <Overlay style={style}>
          <RotatingLines />
        </Overlay>
      );
    }
  }

  return <RotatingLines />;
};

export default Loader;
