// @ts-nocheck

import { Box } from '@chakra-ui/react';

const Log = ({ log }) => {
  // console.log('log Component', log);
  return (
    <Box fontSize={'13px'} marginBottom={'10px'}>
      <span>{log.timeStamp}</span>{' '}
      <span style={{ color: '#6D8693' }}>{log.pid}</span>
      {log?.tid && (
        <>
          {' '}
          <span style={{ color: '#6D8693' }}>{log?.tid}</span>
        </>
      )}
      <span> {log.level}</span>
      <span style={{ color: '#9D744E' }}> {log.message}</span>
    </Box>
  );
};

export default Log;
