import { useState, useEffect, forwardRef } from 'react';
import { Button, HStack, Spacer, Stack, Img, Flex } from '@chakra-ui/react';
import { useScreenshot } from 'use-react-screenshot';

import LoadingComponent from 'app/components/LoadingComponent';
import ScreenshotList from '../ScreenshotList';

import TabHeader from '../TabHeader';
import { RiScreenshot2Fill } from 'react-icons/ri';

import { useInteractSlice } from 'app/pages/Interact/hooks';
import { NoContent } from 'app/components';

//@ts-ignore
const Screenshots = forwardRef(({}, ref) => {
  const {
    interactType: type,
    actions,
    selector,
    dispatch,
  } = useInteractSlice();
  const { screenshotsLoading, screenshotUploadedResponse, screenshots } =
    selector;
  const [image, takeScreenShot] = useScreenshot('');
  const [screenshotsList, setScreenshotsList] = useState<any>([]);

  //@ts-ignore
  const captureImage = () => takeScreenShot(ref?.current);

  const clearImages = () => {
    setScreenshotsList([]);
  };

  useEffect(() => {
    dispatch(actions.screenshotUploadingItem({}));
    setScreenshotsList([]);

    return () => {
      dispatch(actions.screenshotUploadingItem({}));
      setScreenshotsList([]);
    };
  }, []);

  useEffect(() => {
    if (screenshotUploadedResponse?.hasOwnProperty('capturedImage')) {
      setScreenshotsList(prevState => [
        ...prevState,
        screenshotUploadedResponse,
      ]);
    }
  }, [screenshotUploadedResponse]);

  useEffect(() => {
    let timeoutId;
    if (image) {
      if (timeoutId) {
        clearTimeout(timeoutId);
      }
      timeoutId = setTimeout(() => {
        dispatch(actions.screenshotUploading({ capturedImage: image, type }));
        // setScreenshotsList(prevState => [...prevState, image]);
      }, 800);
    }
    return () => {
      if (timeoutId) {
        clearTimeout(timeoutId);
      }
    };
  }, [dispatch, image, setScreenshotsList]);
  const takeScreenHandler = () => {
    captureImage();
  };

  if (screenshotsLoading) {
    //@ts-ignore
    return <LoadingComponent height="100vh"></LoadingComponent>;
  }

  return (
    //@ts-ignore
    <TabHeader heading="Gallery">
      <Stack padding="5px">
        <HStack>
          <Button
            h="30px"
            color="blue1"
            backgroundColor="transparent"
            fontSize="14px"
            _focus={{ backgroundColor: 'white' }}
            fontWeight="500"
            leftIcon={<RiScreenshot2Fill size={18} color="gray" />}
            onClick={takeScreenHandler}
          >
            Take Screenshot
          </Button>
          <Spacer />
          <Button
            h="30px"
            color="grey9"
            backgroundColor="transparent"
            fontSize="14px"
            _focus={{ backgroundColor: 'white' }}
            fontWeight="500"
            onClick={clearImages}
          >
            Clear
          </Button>
        </HStack>
        <Flex minHeight="60vh" padding="10px" overflow="hidden">
          {screenshotsList && screenshotsList.length > 0 ? (
            <ScreenshotList
              setScreenshotsList={setScreenshotsList}
              screenshots={screenshotsList}
              takeScreenShot={takeScreenShot}
            />
          ) : (
            <NoContent width="100%" height="100%" message="No Media Found" />
          )}
        </Flex>
      </Stack>
    </TabHeader>
  );
});

export default Screenshots;
