import { useEffect, useState } from 'react';
import { ErrorMessageBox } from '.';
import { RetryStartStream } from '.';
import { NoContent, PopupComponent } from 'app/components';
import { useDisclosure } from '@chakra-ui/react';
import { StyledCenterWrapper, StyledTitleWrapper } from '../styles';

const Error = ({
  controllerState,
  controllerError,
  retryCallback,
  onExitSession,
}) => {
  const [countOfAutoRetry, setCountOfAutoRetry] = useState(0);
  const { isOpen, onOpen, onClose } = useDisclosure();
  useEffect(() => {
    if (
      !countOfAutoRetry &&
      controllerState &&
      Object.keys(controllerState).length &&
      controllerState.failedDueToInvalidPeerConnectionState
    ) {
      retryCallback();
      setCountOfAutoRetry(prev => prev + 1);
    }
    if (
      countOfAutoRetry ||
      controllerError ||
      (controllerState &&
        (controllerState.failedToStartStream ||
          controllerState.failedDueToInvalidPeerConnectionState))
    ) {
      // console.log('on failed');
      onOpen();
    } else {
      onClose();
    }

    return () => {
      setCountOfAutoRetry(0);
    };
  }, [controllerState, countOfAutoRetry, retryCallback]);

  useEffect(() => {
    if (isOpen) {
      const timerId = setTimeout(onExitSession, 8000); // 30,000 milliseconds = 30 seconds
      return () => clearTimeout(timerId);
    }
  }, [isOpen]);

  return countOfAutoRetry ||
    controllerError ||
    (controllerState &&
      (controllerState.failedToStartStream ||
        controllerState.failedDueToInvalidPeerConnectionState)) ? (
    <>
      <PopupComponent
        title="Interact"
        width="450px"
        isOpen={isOpen}
        onClose={onClose}
        buttonTitle1="Cancel"
        buttonTitle2="Exit"
        buttonHandler1={onClose}
        buttonHandler2={onExitSession}
        mainButtonColor="red"
      >
        <StyledCenterWrapper>
          <StyledTitleWrapper>
            Sorry, we are unable to connect to the device at this time. Please
            retry in some time.
          </StyledTitleWrapper>
        </StyledCenterWrapper>
      </PopupComponent>
      {/* <NoContent message={'Failed to connect'} /> */}
    </>
  ) : //<RetryStartStream retryStart={retryCallback} />
  null;
};

export default Error;
