//@ts-nocheck

import UserPool from './UserPool';
import Cookies from 'js-cookie';

const getAccessToken = () => {
    const getLastAuthUser = () => {
    const last_auth = `CognitoIdentityServiceProvider.${UserPool.getClientId()}.LastAuthUser`;

    const reg = new RegExp(`(?<=${last_auth}=)[^;]*`);

    try {
      return document.cookie.match(reg)[0];
    } catch {
      return null;
    }
  };

    const username = getLastAuthUser();

  const cookie_name = `CognitoIdentityServiceProvider.${UserPool.getClientId()}.${username}.accessToken`;

  try {
    return Cookies.get(cookie_name);
  } catch {
    return null;
  }
};

export default getAccessToken;
