const extractStringByKey = (inputString, key) => {
  const pattern = new RegExp(`^"${key}", "(.+)"$`, 'gm');
  const matches = inputString.matchAll(pattern);
  let lastMatch = null;

  for (const match of matches) {
    const item = match[1];
    if (item) {
      const arr = item.split(',');
      if (arr.length > 1 && arr[0] === key) {
        return arr[1];
      }
    }
  }

  return lastMatch;
};

const getExtractMessageShouldNotKey = (item, key) => {
  // 'list.packages, 0b059e1de05633f7223682a0f046360404f03fe5, com.netflix.Netflix'
  //upload.app, 306PLSY152974, Successfully installed
  // console.log(item);
  if (item) {
    const arr = item.trim().split(',');
    // console.log(arr);
    if (arr.length > 1) {
      if (arr[0] !== key) {
        // console.log(key, arr[0]);
        return item;
      }
    }
  }
  return '';
};

const extractDeviceLog = contollerMsg => {
  const startIndex = contollerMsg.indexOf('log": "') + 'log": "'.length;
  const endIndex = contollerMsg.lastIndexOf('"');
  const logString = contollerMsg.substring(startIndex, endIndex - 2);
  return logString;
};

export {
  getExtractMessage,
  getExtractMessageShouldNotKey,
  extractStringByKey,
  extractDeviceLog,
};
