export const mobileControllerChannel = device => {
  // mobile interact controller channel
  return device?.deviceParameters?.channelName;
};

export const mobileChannel = device => {
  // mobile interact channel
  return device?.serial;
};

export const lrControllerChannel = device => {
  return device?.deviceParameters?.['interact-controller']?.['channelName'];
};

export const lrChannel = device => {
  return device?.deviceParameters?.['channelName'];
};

/* export const lrRemoteChannel = device => { */
/*   return device?.deviceParameters?.['interact-controller']?.['device-uuid']; */
/* }; */
export const lrRemoteChannel = device => {
  const interactController = device?.deviceParameters?.['interact-controller'];

  if (Array.isArray(interactController)) {
    return interactController;
  }

  return [interactController];
};
