import { Flex, Text } from '@chakra-ui/react';
import PopupComponent from 'app/components/PopupComponent';

const ExitSessionConfirmation = ({
  onExitSession,
  onClose,
  isOpen,
  isCleanExit,
  onExitSessionState,
}) => {
  if (isCleanExit) {
    return null;
  }

  return (
    <PopupComponent
      title="Interact"
      width="450px"
      isOpen={isOpen}
      onClose={onClose}
      buttonTitle1="Cancel"
      buttonTitle2="Exit"
      buttonHandler1={onClose}
      buttonHandler2={() => {
        onExitSession();
        onExitSessionState();
      }}
      mainButtonColor="red"
    >
      <Flex justify="center" width="100%" align="center" justifyItems="center">
        <Text textAlign="center">
          Are you sure that you want to exit the session?
        </Text>
      </Flex>
    </PopupComponent>
  );
};

export default ExitSessionConfirmation;
