import InputComponent from 'app/pages/Appium/components/InputComponent'
import SelectComponent from 'app/pages/Appium/components/SelectComponent'
import LabelFieldComponent from 'app/pages/AutomationTest/components/LabelFieldComponent'
import React from 'react'
import CreateEvent from './CreateEvent';

const selectKpiTypeOptions = [
    { name: 'state' },
    { name: 'latency' },
    { name: 'streaming' },
    { name: 'metric' },
  ];
  const selectOperationTypeOptions = [{ name: 'SUM' },
 { name: 'PERCENTAGE' },
 { name: 'COUNT' },
];

function CreateKpi({setKpiName , kpiName , kpiType ,setKpiType , operationType , setOperationType , 
    setStartEventName , startEventName , selectStartEvent , setSelectStartEvent ,
    setSelectEndEvent , endEventName , eventListData , addEventHandler , setEndEventName ,selectEndEvent 
}) {
    const startEventProps =  {type : 'Start' , eventListData ,addEventHandler : addEventHandler  , setEventName : setStartEventName , eventName : startEventName , selectEvent : selectStartEvent ,setSelectEvent : setSelectStartEvent };
    const endEventProps = {type : 'End' , eventListData , addEventHandler : addEventHandler , setEventName : setEndEventName , eventName : endEventName , selectEvent : selectEndEvent ,setSelectEvent : setSelectEndEvent };
  return (
    <>
    <LabelFieldComponent label="KPI Name" isRequired={true}>
    <InputComponent
      setInputValue={setKpiName}
      inputValue={kpiName}
      title="Enter Kpi Name"
    ></InputComponent>
  </LabelFieldComponent>
  <LabelFieldComponent label="KPI Type" isRequired={true}>
    <SelectComponent
      name="xcui"
      type="project"
      selectValue={kpiType}
      setSelectValue={setKpiType}
      selectOptions={selectKpiTypeOptions}
      title="Kpi Type"
    ></SelectComponent>
  </LabelFieldComponent>
  { kpiType === 'streaming' && <LabelFieldComponent isRequired={true} label="Operation">
  <SelectComponent
          name="xcui"
          type="project"
          selectValue={operationType}
          setSelectValue={setOperationType}
          selectOptions={selectOperationTypeOptions}
          title="Select Type"
        ></SelectComponent>
  </LabelFieldComponent> }
  {(kpiType === 'latency' ||
    kpiType === 'streaming' ||
    kpiType === 'state') && (
        <CreateEvent {...startEventProps}></CreateEvent>
  )}
  {(kpiType === 'latency' || kpiType === 'streaming') && (
   <CreateEvent {...endEventProps}></CreateEvent>
  )}
</>
  )
}

export default CreateKpi