// @ts-nocheck
import React, {useCallback, useState} from "react";
import _isEmpty from "lodash/isEmpty";
import {Flex, Input, InputGroup, Stack, Text} from "@chakra-ui/react";
import ButtonComp from "../../components/ButtonComp";

const ChannelCodeInput = (props) => {
    const {onStartConnect: onStartConnectProp, channelName: channelNameProp} = props;

    console.log("channelNameProp --> ", channelNameProp)

    const [channelName, setChannelName] = useState(channelNameProp)

    const onConnect = useCallback(() => {
        if (_isEmpty(channelName)) {
            alert("ViewerCode can't be empty!")
            return
        }
        onStartConnectProp?.(channelName)
    }, [channelName]);

    const onChannelInput = useCallback((e) => {
        setChannelName(e?.target?.value);
    }, []);

    const onKeyboardKeyPress = useCallback((event) => {
        if (event.keyCode === 13) {
            setChannelName(event?.target?.value);
            onConnect()
        }
    }, [onConnect]);

    return (
        <Flex
            flexDirection="column"
            backgroundColor="#F3F3F3"
            width="100%"
        >
            <Flex width="100%" fontFamily="Montserrat" color="grey2">
                <Flex
                    width="100%"
                    align="center"
                    justify="center"
                    h="calc(100vh - 55px)"
                >
                    <Flex
                        width="574px"
                        padding="10px"
                        flexDirection="column"
                        margin="50px auto"
                    >
                        <Stack
                            width="100%"
                            spacing="20px"
                            border="0px solid"
                            bg="white"
                            padding="30px 20px"
                            borderRadius="7px"
                            boxShadow="0px 3px 6px #00000029"
                        >
                            <Stack spacing="0px">
                                <Text fontWeight="600" fontSize="20px" color="grey15">
                                    Enter Viewer code
                                </Text>
                            </Stack>

                            <Stack marginTop="20px" spacing="10px">
                                <InputGroup>
                                    <Input
                                        value={channelName}
                                        onChange={onChannelInput}
                                        // type={show ? 'text' : 'password'}
                                        borderRadius="1px"
                                        height="40px"
                                        data-input="password-login-id"
                                        onKeyDown={onKeyboardKeyPress}
                                        placeholder="Code"
                                        _focus={{
                                            boxShadow: ' 0 0 0 white',
                                            borderLeft: '5px solid ',
                                            borderLeftColor: 'blue1',
                                        }}
                                    ></Input>
                                </InputGroup>
                            </Stack>
                            <Stack spacing="40px">
                                <ButtonComp
                                    color="white"
                                    padding="7px 14px"
                                    backgroundColor="blue1"
                                    width="100%"
                                    buttonHandler={onConnect}
                                    name="login-button-app-testing"
                                    isDisabled={_isEmpty(channelName)}
                                >
                                    Connect
                                </ButtonComp>
                            </Stack>
                        </Stack>
                    </Flex>
                </Flex>
            </Flex>
        </Flex>
    );
};

export default React.memo(ChannelCodeInput)
