import { PayloadAction } from '@reduxjs/toolkit';
import { createSlice } from 'utils/@reduxjs/toolkit';
import { useInjectReducer, useInjectSaga } from 'utils/redux-injectors';
import { userDetailsSaga } from './saga';
import { UserDetailsState } from './types';

export const initialState: UserDetailsState = {
  isLoading: false,
  error: null,
  isAuth: false,
  role: '',
  cognitoResponse: {},
  loadingFeaturesFlag: false,
  featuresFlagDataLoaded: {
    version: '0.1',
    features: {
      enable: {
        'default-landing-page': '',
        'login-theme-with-app-interact': false,
        'client-org-logo-enable': false,
        'client-org-center-logo-enable': true,
        'automation-testing-login-card-url-disablead': false,
        'user-management-features': false,
        'user-management-authentication-2fa': true,
        'user-management-invite-user': true,
        'user-management-delete-user': true,
        'user-management-update-user-role': true,
        'user-management-search-user': false,
        'user-management-signup-features': false,
        'settings-manage-project': true,
        'test-analytics-features': true,
        'test-manage-schedule-fetures': false,
        'test-analytics-list-test-executions': false,
        'test-analytics-display-test-execution-summary': true,
        'test-analytics-display-test-execution-har-viewer': true,
        'test-analytics-display-test-execution-har-analytics': true,
        'test-analytics-display-test-execution-device-profiling': true,
        'test-analytics-display-test-execution-app-profiling': false,
        'test-analytics-display-test-execution-download-artifacts': true,
        'test-execution-features': false,
        'test-execution-framework-support-uiautomator': true,
        'test-execution-framework-support-xcuitest': true,
        'test-execution-create-project': true,
        'test-execution-upload-app-build': true,
        'test-execution-upload-script': true,
        'test-execution-create-device-group': true,
        'test-execution-configure-test-execution': true,
        'test-execution-schedule-test-execution': true,
        'newtwork-automate-feature': false,
        'browser-automate-feature': false,
        'test-tv-automate-feature': true,
        'test-app-automate-feature': true,
        'test-dashboard-quicksight-feature': true,
        'lr-interact-features': false,
        'mydevice-tab': true,
        'performancetest-tab': false,
        'diagnostictest-tab': false,
        'features-schedule-calendar': true,
        'local-testing-features': false,
        'synthetic-monitoring-features': true,
      },
    },
  },
  featuresFlagDataLoadingError: null,
};

const slice = createSlice({
  name: 'userDetails',
  initialState: initialState,
  reducers: {
    updateIsLoading: state => {
      state.isLoading = true;
    },
    fetchUserDetailsSucess: (state, action: PayloadAction<any>) => {
      state.role = action.payload;
      state.isAuth = true;
      state.isLoading = false;
    },
    fetchLoginFailure: (state, action: PayloadAction<any>) => {
      state.isLoading = false;
      state.isAuth = false;
      state.error = action.payload;
    },
    getCongnitoResponse: (state, action: PayloadAction<any>) => {
      state.cognitoResponse = action.payload;
    },
    loadFeaturesFlag: state => {
      state.loadingFeaturesFlag = true;
    },
    feturesFlagResponseLoaded: (state, action: PayloadAction<any>) => {
      state.loadingFeaturesFlag = false;
      state.featuresFlagDataLoaded = action.payload;
      state.featuresFlagDataLoadingError = null;
    },
    feturesFlagDataError: (state, action: PayloadAction<any>) => {
      state.loadingFeaturesFlag = false;
      state.featuresFlagDataLoadingError = action.payload;
    },
  },
});

export const { actions: userDetailsAction } = slice;

export const useUserDetailsSlice = () => {
  useInjectReducer({ key: slice.name, reducer: slice.reducer });
  useInjectSaga({ key: slice.name, saga: userDetailsSaga });

  return { actions: slice.actions };
};
