import { useEffect, useState } from 'react';
import {
  extractStringByKey,
  getExtractMessage,
  getExtractMessageShouldNotKey,
} from '../slice/utils/getExtractMessage';

const useReceivedMessageApp = (ref, setMessage) => {
  const [log, setLog] = useState('');
  useEffect(() => {
    //@ts-ignore
    console.log('trigger effect', { log });
    setLog('');
    if (
      ref &&
      ref.current &&
      ref.current.videoState &&
      ref.current.videoState.dataChannel
    ) {
      ref.current.videoState.dataChannel.onmessage = msg => {
        console.log('Received message effect: ', msg?.data);
        setMessage('');
        const received = getExtractMessageShouldNotKey(
          msg?.data,
          'list.packages',
        );
        console.log('received', received);
        setMessage(received);
      };
    }
    console.log('trigger');
  }, [ref, ref?.current?.videoState?.dataChannel]);
};
export default useReceivedMessageApp;
