import { Flex } from '@chakra-ui/react';
import ScreenshotImage from './ScreenshotImage';
import ScrollerList from 'app/components/ScrollerList';
import ActionComponent from './ActionComponent';
import { useState } from 'react';
import { useInteractSlice } from 'app/pages/Interact/hooks';
import { getDownloadFile } from 'app/pages/Interact/slice/utils/downloadScreen';

const ScreenshotList = ({
  screenshots,
  setScreenshotsList,
  takeScreenShot,
}) => {
  const [selectedScreen, setSelectedScreen] = useState([]);
  const {
    interactType: type,
    actions,
    selector,
    dispatch,
  } = useInteractSlice();
  // const { screenshotsLoading, screenshotUploadedResponse, screenshots } =
  //   selector;
  const removeSelectedFromList = (
    setScreenshotsList,
    screenshots,
    selectedScreen,
  ) => {
    if (selectedScreen.length > 0) {
      const updatedScreenshotsList = screenshots.filter(
        screen =>
          !selectedScreen.some(
            selected => selected.fileName === screen.fileName,
          ),
      );

      // Update the screenshots list using setScreenshotsList
      setScreenshotsList(updatedScreenshotsList);
    }
  };
  const deleteItemHandler = () => {
    //console.log('delete handler');
    if (!selectedScreen) {
      return;
    }
    const screenIds = selectedScreen.map(screen => screen.fileName);
    dispatch(actions.screenshotDeleting({ screenIds, type }));
    removeSelectedFromList(setScreenshotsList, screenshots, selectedScreen);
    setSelectedScreen([]);
    // takeScreenShot(<></>);
  };

  const downloadItemHandler = () => {
    //console.log('delete handler');
    if (!selectedScreen) {
      return;
    }
    const screenIds = selectedScreen.map(screen => screen.fileName);
    console.log({ selectedScreen });
    for (let i = 0; i < screenIds.length; i++) {
      getDownloadFile(
        selectedScreen[i].capturedImage,
        selectedScreen[i].fileName,
      );
    }
    setSelectedScreen([]);
  };

  //console.log({ screenshots, selectedScreen });
  if (!screenshots.length) return null;
  return (
    <Flex flexDirection="column" width="100%">
      {selectedScreen && selectedScreen.length > 0 && (
        <ActionComponent
          downloadItemHandler={downloadItemHandler}
          selectedItems={selectedScreen}
          deleteItemHandler={deleteItemHandler}
        ></ActionComponent>
      )}
      <ScrollerList>
        {screenshots.map((item, index) => (
          <ScreenshotImage
            //handleCheckboxChange={handleCheckboxChange}
            setSelectedItems={setSelectedScreen}
            selectedItems={selectedScreen}
            key={index}
            index={index}
            item={item}
          />
        ))}
      </ScrollerList>
    </Flex>
  );
};

export default ScreenshotList;
