import React from 'react';

import arrowExpand from 'app/Assets/arrow-expand.png';

import { StyledArrowExpandBox, StyledArrowExpandImage } from './styles';

const ArrowExpand = ({ sidebarToggle, handleSidebar }) => {
  return (
    <StyledArrowExpandBox onClick={handleSidebar}>
      <StyledArrowExpandImage
        src={arrowExpand}
        alt={sidebarToggle ? 'left' : 'right'}
        sidebarToggle={sidebarToggle}
      />
    </StyledArrowExpandBox>
  );
};

export default ArrowExpand;
