import { PayloadAction } from '@reduxjs/toolkit';
import { createSlice } from 'utils/@reduxjs/toolkit';
import { useInjectReducer, useInjectSaga } from 'utils/redux-injectors';
import { syntheticMonitoringSaga } from './saga';
import { SyntheticMonitoringState } from './types';

export const initialState: SyntheticMonitoringState = {
  projectData: [],
  loadingProject: false,
  projectDataError: null,
  kpiListData: [],
  loadingKpi: false,
  kpiDataError: null,
  journeyListData: [],
  loadingJourney: false,
  journeyDataError: null,
  eventListData: [],
  loadingEvent: false,
  eventDataError: null,
  projectResponse: {},
  loadingCreateProject: false,
  createProjectError: null,
  journeyPayload: {},
  kpiPayload: [],
  loadingCreateEvent: false,
  createEventResponse: {},
  createEventError: null,
  loadingCreateKpi: false,
  createKpiResponse: {},
  createKpiError: null,
  loadingCreateJourney: false,
  createJourneyResponse: {},
  createJourneyError: null,
  loadingDeleteJourney: false,
  deleteJourneyResponse: {},
  deleteJourneyError: null,
  loadingfetchItem : false,
  updateItemfetchError : null,
  updateItemfetch : {},
  loadingUpdateJourney: false,
  updateJourneyResponse: {},
  updateJourneyError: null,
  loadingKpiJourney: false,
  updateKpiResponse: {},
  updateKpiError: null,
  loadingEventJourney: false,
  updateEventResponse: {},
  updateEventError: null,
  editItemState: [],
  journeyId :'',
  kpiEditPayloadState:{},
  eventEditPayloadState:{}
};

const updateKpiPayload = (list, kpiData) => {
  const filterData = list.filter(kpi => kpi.name === kpiData.name);
  if (filterData.length > 0) {
    return list;
  }
  return [...list, kpiData];
};
const updateItems = (list, insertkey) => {
 // console.log({list, insertkey})
  const filterData = list.filter(item => item.uuid === insertkey.uuid);
  if (filterData.length > 0) {
    return list;
  }
  return [...list, insertkey];
};
const removeItemPayload = (list, removeKey) => {
  const removedKpiData = list.filter(item => item.name !== removeKey);
  return removedKpiData;
};
const removeKpiPayload = (list, kpiName) => {
  const removedKpiData = list.filter(kpi => kpi.name !== kpiName);
  return removedKpiData;
};
const slice = createSlice({
  name: 'syntheticMonitoring',
  initialState,
  reducers: {
    loadProjectData: state => {
      state.loadingProject = true;
    },
    projectDataLoaded: (state, action: PayloadAction<any>) => {
      state.projectData = action.payload;
      state.loadingProject = false;
      state.projectDataError = null;
    },
    projectDataError: (state, action: PayloadAction<any>) => {
      state.loadingProject = false;
      state.projectDataError = action.payload;
    },
    loadcreateProject: state => {
      state.loadingCreateProject = true;
    },
    projectCreatedResponse: (state, action: PayloadAction<any>) => {
      state.loadingCreateProject = false;
      state.projectResponse = action.payload;
      state.createProjectError = null;
    },
    createProjectError: (state, action: PayloadAction<any>) => {
      state.loadingCreateProject = false;
      state.createProjectError = action.payload;
    },
    createJourneyPayload: (state, action: PayloadAction<any>) => {
      state.journeyPayload = action.payload;
    },
    resetJourneyPayload: state => {
      state.journeyPayload = {};
    },
    loadJourneyListData: state => {
      state.loadingJourney = true;
    },
    journeyDataLoaded: (state, action: PayloadAction<any>) => {
      state.loadingJourney = false;
      state.journeyListData = action.payload;
      state.journeyDataError = null;
    },
    journeyDataLoadedError: (state, action: PayloadAction<any>) => {
      state.loadingJourney = false;
      state.journeyDataError = action.payload;
    },
    loadKpiListData: state => {
      state.loadingKpi = true;
    },
    kpiDataLoaded: (state, action: PayloadAction<any>) => {
      state.loadingKpi = false;
      state.kpiListData = action.payload;
      state.kpiDataError = null;
    },
    kpiDataLoadedError: (state, action: PayloadAction<any>) => {
      state.loadingKpi = false;
      state.kpiDataError = action.payload;
    },
    loadEventListData: state => {
      state.loadingEvent = true;
    },
    eventDataLoaded: (state, action: PayloadAction<any>) => {
      state.loadingEvent = false;
      state.eventListData = action.payload;
      state.eventDataError = null;
    },
    eventDataLoadedError: (state, action: PayloadAction<any>) => {
      state.loadingEvent = false;
      state.eventDataError = action.payload;
    },
    createKpiObject: (state, action: PayloadAction<any>) => {
      state.kpiPayload = updateKpiPayload(state.kpiPayload, action.payload);
    },
    createEditItems : (state , action : PayloadAction<any>) => {
      state.editItemState = updateItems(state.editItemState , action.payload)
    },
    insertEditItems : (state , action : PayloadAction<any>) => {
      state.editItemState = action.payload;
    },
    
    insertKpiPayload: (state, action: PayloadAction<any>) => {
      state.kpiPayload = [...action.payload];
    },
    resetKpiPayload: state => {
      state.kpiPayload = [];
    },
    removeKpiItem: (state, action: PayloadAction<any>) => {
      state.kpiPayload = removeKpiPayload(state.kpiPayload, action.payload);
    },
    removeItemPayload: (state, action: PayloadAction<any>) => {
      state.editItemState = removeItemPayload(state.editItemState , action.payload);
    },
    loadCreateJourney: state => {
      state.loadingCreateJourney = true;
    },
    createdJourney: (state, action: PayloadAction<any>) => {
      state.loadingCreateJourney = false;
      state.createJourneyResponse = action.payload;
      state.createJourneyError = null;
    },
    createJourneyError: (state, action: PayloadAction<any>) => {
      state.loadingCreateJourney = false;
      state.createJourneyError = action.payload;
    },
    loadUpdateJourney: state => {
      state.loadingUpdateJourney = true;
    },
    updatedJourney: (state, action: PayloadAction<any>) => {
      state.loadingUpdateJourney = false;
      state.updateJourneyResponse = action.payload;
      state.updateJourneyError = null;
    },
    updateJourneyError: (state, action: PayloadAction<any>) => {
      state.loadingUpdateJourney = false;
      state.updateJourneyError = action.payload;
    },
    loadUpdateKpi: state => {
      state.loadingUpdateKpi = true;
    },
    updatedKpi: (state, action: PayloadAction<any>) => {
      state.loadingUpdateKpi = false;
      state.updateKpiResponse = action.payload;
      state.updateKpiError = null;
    },
    updateKpiError: (state, action: PayloadAction<any>) => {
      state.loadingUpdateKpi = false;
      state.updateKpiError = action.payload;
    },
    loadUpdateEvent: state => {
      state.loadingUpdateEvent = true;
    },
    updatedEvent: (state, action: PayloadAction<any>) => {
      state.loadingUpdateEvent = false;
      state.updateEventResponse = action.payload;
      state.updateEventError = null;
    },
    updateEventError: (state, action: PayloadAction<any>) => {
      state.loadingUpdateEvent = false;
      state.updateEventError = action.payload;
    },
    getJourneyId : (state , action: PayloadAction<any>) =>{
      state.journeyId = action.payload;
    },
    loadCreateEvent: state => {
      state.loadingCreateEvent = true;
    },
    eventCreated: (state, action: PayloadAction<any>) => {
      state.loadingCreateEvent = false;
      state.createEventResponse = action.payload;
      state.createEventError = null;
    },
    eventCraeteError: (state, action: PayloadAction<any>) => {
      state.loadingCreateEvent = false;
      state.createEventError = action.payload;
    },
    loadCreateKpi: state => {
      state.loadingCreateKpi = true;
    },
    kpiCreated: (state, action: PayloadAction<any>) => {
      state.loadingCreateKpi = false;
      state.createKpiResponse = action.payload;
      state.createKpiError = null;
    },
    kpiCraeteError: (state, action: PayloadAction<any>) => {
      state.loadingCreateKpi = false;
      state.createKpiError = action.payload;
    },
    KpiEditPayload : (state, action : PayloadAction<any>) => {
      state.kpiEditPayloadState = action.payload;
    },
    EventEditPayload : (state, action : PayloadAction<any>) => {
      state.eventEditPayloadState = action.payload;
    },
    loadDeleteItem : state => {
      state.loadingDeleteJourney = true;
    },
    itemDeleted: (state, action: PayloadAction<any>)=>{
     state.deleteJourneyResponse = action.payload;
     state.loadingDeleteJourney = false;
     state.deleteJourneyError = null;
    },
    itemDeleteError: (state, action: PayloadAction<any>) => {
      state.loadingDeleteJourney = false;
      state.deleteJourneyError = action.payload;
    },
    loadUpdateFetchItem : state => {
      state.loadingfetchItem = true;
    },
    itemUpdatedFetch: (state, action: PayloadAction<any>)=>{
     state.updateItemfetch = action.payload;
     state.loadingfetchItem = false;
     state.updateItemfetchError = null;
    },
    itemUpdatedFetchError: (state, action: PayloadAction<any>) => {
      state.loadingfetchItem = false;
      state.updateItemfetchError = action.payload;
    }
    

  },
});

export const { actions: syntheticMonitoringActions } = slice;

export const useSyntheticMonitoringSlice = () => {
  useInjectReducer({ key: slice.name, reducer: slice.reducer });
  useInjectSaga({ key: slice.name, saga: syntheticMonitoringSaga });
  return { actions: slice.actions };
};

