import GetIdToken from 'Authentication/configuration/GetIdToken';
import { isTokenExpired } from 'utils/IsTokenExpire';
import { getRefreshSession } from 'utils/authentication/getRefreshSession';

const getUpdatedToken = async () => {
  const idToken = GetIdToken();
  const isExpire = isTokenExpired();
  if (isExpire) {
    const sessionData = await getRefreshSession();
    const idToken =
      sessionData.hasOwnProperty('idToken') && sessionData?.idToken?.jwtToken;
    if (idToken) {
      return idToken;
    }
  }
  return idToken;
};
export { getUpdatedToken };
