// @ts-nocheck
import React, {useCallback, useState} from "react";
import {useSearchParams} from "react-router-dom";


import ChannelCodeInput from "./ChannelCodeInput";
import VideoPlayer from "./VidePlayer/VideoPlayer";

const LiveStreamPage = (props) => {
    console.log("LiveStreamPage: props: ", props)
    let [searchParams, setSearchParams] = useSearchParams();

    const viewerCode = searchParams.get("viewerCode")
    const [channelName, setChannelName] = useState(viewerCode)
    const [shouldStartStream, setShouldStartStream] = useState(viewerCode != undefined)

    const onStartConnect = useCallback((viewerCode) => {
        setSearchParams({
            viewerCode
        });
        setChannelName(viewerCode);
        setShouldStartStream(true);
    }, []);

    const onDisconnectClicked = useCallback(() => {
        setSearchParams({});
        setShouldStartStream(false);
    }, []);

    if (channelName && shouldStartStream) {
        return <VideoPlayer channelName={channelName} onDisconnectClicked={onDisconnectClicked}/>
    }
    return <ChannelCodeInput channelName={channelName} onStartConnect={onStartConnect}/>
};

export default React.memo(LiveStreamPage);
