import { Text, Box, Flex, Icon, Td, Tr } from '@chakra-ui/react';
import { BsStopCircle } from 'react-icons/bs';
import { FaTelegramPlane } from 'react-icons/fa';

import { getEmailId } from 'Authentication/configuration/GetEmail';

import {
  isDeviceSelfOwned,
  isDeviceBusyAndNotSelfOwned,
  isDeviceFree,
} from 'app/utils/device';

const ListTableRow = ({ item: device, index, onRowSelection, stopAction }) => {
  const email = getEmailId();
  const {
    serial,
    brand,
    deviceParameters: {
      channelName,
      deviceStatus,
      user,
      city,
      network,
      mobile,
      screenSize,
    },
  } = device;

  const setDeviceStatusColor = isDeviceFree(device)
    ? 'green2'
    : isDeviceSelfOwned(device, email)
    ? 'orange1'
    : 'red2';

  const onSelection = (event, device) => {
    /* if (isDeviceBusyAndNotSelfOwned(device, email)) { */
    /*   event?.preventDefault(); */
    /*   return undefined; */
    /* } */
    if (
      isDeviceBusyAndNotSelfOwned(device, email) ||
      isDeviceSelfOwned(device, email)
    ) {
      return undefined;
    }
    onRowSelection(device);
    return undefined;
  };

  return (
    <>
      <Tr
        cursor={
          isDeviceBusyAndNotSelfOwned(device, email) ||
          isDeviceSelfOwned(device, email)
            ? 'not-allowed'
            : 'pointer'
        }
        role="button"
        data-row-id={`lr-row-id-${index}-${serial}`}
        _hover={isDeviceFree(device) ? { background: 'grey14' } : {}}
        onClick={event => onSelection(event, device)}
        className="hoverRow"
        color={isDeviceBusyAndNotSelfOwned(device, email) ? '#808080' : 'black'}
      >
        <Td minWidth="20px" paddingLeft="0px" paddingRight="0px" padding="5px">
          <Box
            borderRadius="50%"
            width="10px"
            h="10px"
            title={deviceStatus}
            bg={setDeviceStatusColor}
          ></Box>
        </Td>
        <Td color="inherit" paddingLeft="5px" padding="5px">
          {device.brand}
        </Td>
        <Td color="inherit" paddingLeft="5px" padding="5px">
          {device.modelName}
        </Td>
        <Td color="inherit" paddingLeft="5px" padding="5px">
          {device.osVersion}
        </Td>
        <Td color="inherit" paddingLeft="5px" padding="5px">
          {network}
        </Td>
        <Td color="inherit" paddingLeft="5px" padding="5px">
          {mobile}
        </Td>
        <Td color="inherit" paddingLeft="5px" padding="5px">
          {screenSize}
        </Td>
        <Td color="inherit" padding="5px">
          {city}
        </Td>
        <Td color="inherit" padding="5px">
          {serial}
        </Td>
        <Td color="inherit" padding="5px" width="100px" paddingRight="10px">
          <Flex>
            {isDeviceFree(device) ? (
              <Icon
                w="18px"
                cursor="pointer"
                title="start"
                h="18px"
                color="grey1"
                role={'button'}
                as={FaTelegramPlane}
              ></Icon>
            ) : (
              isDeviceSelfOwned(device, email) && (
                <Icon
                  w="18px"
                  cursor="pointer"
                  title="stop"
                  h="18px"
                  role={'button'}
                  onClick={event => stopAction(event, device)}
                  color="grey1"
                  as={BsStopCircle}
                ></Icon>
              )
            )}
          </Flex>
        </Td>
      </Tr>
    </>
  );
};

export default ListTableRow;
