// @ts-nocheck
import { Flex, Stack, Text } from '@chakra-ui/react';
import SubContainer from 'app/components/SubContainer';

import React, { useState, useEffect, useRef } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useLocation, useOutletContext, useParams } from 'react-router-dom';
import { useTestDashboardSlice } from '../slice';
import {
  selectLoadingDashboard,
  selectTestDashboardEmbedUrl,
} from '../slice/selectors';
import * as QuickSightEmbedding from 'amazon-quicksight-embedding-sdk';
import { selectUserRole } from 'Authentication/slice/selectors';
import LoadingComponent from 'app/components/LoadingComponent';
import SubheaderTestdash from './SubheaderTestdash';
import ROUTES from 'app/constants/Routes';

function DashboardComponent() {
  const { dashId } = useParams();
  const { actions } = useTestDashboardSlice();
  const dispatch = useDispatch();
  const dashboardUrl = useSelector(selectTestDashboardEmbedUrl);
  const containerRef = useRef();
  //const userRole = useSelector(selectUserRole);
  const { isAuthenticated, userRole } = useOutletContext();
  const loadingDashboardUrl = useSelector(selectLoadingDashboard);
  const [tabroute, setTabroute] = useState([
    'Session Barometer',
    localStorage.getItem('dashboard_name'),
  ]);
  const [tabpath, setTabpath] = useState([
    `${ROUTES.MOBILE_SESSION_BAROMETER}`,
    `${ROUTES.MOBILE_SESSION_BAROMETER}/${dashId}`,
  ]);

  const location = useLocation();

  useEffect(() => {
    function loadDashboard() {
      dispatch(actions.EmbedUrlLoaded(''));
      dispatch(
        actions.loadEmbedUrl({ dashboradId: dashId, userRole: userRole }),
      );
    }
    const pathname = location.pathname;
    if (pathname.startsWith('/motesting')) {
      setTabroute([
        'Session Barometer',
        localStorage.getItem('dashboard_name'),
      ]);
      setTabpath([
        `${ROUTES.MOBILE_SESSION_BAROMETER}`,
        `${ROUTES.MOBILE_SESSION_BAROMETER}/${dashId}`,
      ]);
    } else if (pathname.startsWith('/lrtesting')) {
      setTabroute([
        'Session Barometer',
        localStorage.getItem('dashboard_name'),
      ]);
      setTabpath([
        `${ROUTES.LR_SESSION_BAROMETER}`,
        `${ROUTES.LR_SESSION_BAROMETER}/${dashId}`,
      ]);
    } else {
      setTabroute(['App Barometer', localStorage.getItem('dashboard_name')]);
      setTabpath([
        `${ROUTES.MONITORING_APPBAROMETER}`,
        `${ROUTES.MONITORING_APPBAROMETER}/${dashId}`,
      ]);
    }
    loadDashboard();
    const intervalId = setInterval(loadDashboard, 300000);
    return () => clearInterval(intervalId);
  }, [dashId, location]);

  return (
    <Flex flexDirection="column" width="100%" align="center">
      <SubheaderTestdash path={tabpath} tabroute={tabroute}></SubheaderTestdash>
      <SubContainer>
        {loadingDashboardUrl ? (
          <LoadingComponent height="80vh"></LoadingComponent>
        ) : (
          <Stack
            bg="white"
            h="120vh"
            display="flex"
            width="100%"
            paddingBottom="0px"
            //  paddingTop="50px"
            //position="relative"
          >
            {/* <Flex
              height="35px"
              width="100.1%"
              bg="white"
              position="absolute"
              alignContent="center"
              alignItems="center"
              justifyContent="center"
            >
              <Text
                padding="5px 20px"
                fontSize="16px"
                fontFamily="Montserrat"
                fontWeight="600"
              >
                
              </Text>
            </Flex> */}

            <iframe
              src={dashboardUrl}
              // width="100%"
              // height="1200"
              style={{ width: '100%', height: '1200px' }}
              frameBorder="0"
            ></iframe>
            {/* <Flex
              height="23px"
              bg="red"
              position="absolute"
              alignContent="center"
              alignItems="center"
              justifyContent="center"
              bottom="0px"
              right="0px"
              width="131px"
            ></Flex> */}
          </Stack>
        )}
      </SubContainer>
    </Flex>
  );
}

export default DashboardComponent;
