// import GetIdToken from 'Authentication/configuration/GetIdToken';
import { call, delay, put, takeLatest } from 'redux-saga/effects';
import { request } from 'utils/request';
import { testAnalyticsActions as actions } from '.';
import { testAnalyticsErrorType } from './types';
import { isTokenExpired } from 'utils/IsTokenExpire';
import GetIdToken from 'Authentication/configuration/GetIdToken';
import { REACT_APP_TEST_ANALYTICS_BASE_URL } from 'utils/enviroment';

const BASE_URL = REACT_APP_TEST_ANALYTICS_BASE_URL;
const getTestTypeKey = (testType, tabIndex) => {
  let testTypeString = '';
  if (testType === 'lr-interact') {
    if (tabIndex === 1) {
      testTypeString = 'lr-interact';
    } else {
      testTypeString = 'lr-automation';
    }
  } else {
    if (tabIndex === 1) {
      testTypeString = 'mobile-interact';
    } else {
      testTypeString = 'app-automation,local-testing,synthetic-monitoring';
    }
  }
  return testTypeString;
};

function* gettestAnalyticsData(param) {
  const idToken = GetIdToken();
  const dateRageString =
    param.payload.fromDate !== '' && param.payload.toDate !== ''
      ? `${param.payload.fromDate}/${param.payload.toDate}`
      : '';
  const testType =
    param.payload.testType !== 'lr-interact'
      ? 'app-automation,mobile-interact,local-testing'
      : 'lr-interact,lr-automation';
  getTestTypeKey(param.payload.testType, param.payload.getTestTypeTab);
  const requestURL = `${BASE_URL}/tests?testType=${getTestTypeKey(
    param.payload.testType,
    param.payload.getTestTypeTab,
  )}&testStatus=${param.payload.testStatus}&search=${
    param.payload.searchKey
  }&dateRange=${dateRageString}&pageNo=${param.payload.page}&pageSize=${
    param.payload.pageSize
  }`;
  const token = isTokenExpired();

  const config = {
    method: 'GET',
    headers: {
      'Content-type': 'application/json',
      Authorization: `Bearer ${idToken}`,
    },
  };

  try {
    const testAnalyticsData = yield call(request, requestURL, config);
    //{ message : "The incoming token has expired"}
    // console.log(testAnalyticsData);
    if (
      testAnalyticsData.hasOwnProperty('statusCode') &&
      testAnalyticsData.statusCode === 200
    ) {
      //  console.log(testAnalyticsData);
      yield put(actions.testAnalyticsDataLoaded(testAnalyticsData.body));
      yield put(actions.getPageCountfromApi(testAnalyticsData.count));
    } else {
      if (
        testAnalyticsData.hasOwnProperty('message') &&
        testAnalyticsData.message === 'The incoming token has expired'
      ) {
        yield put(
          actions.testAnalyticsDataError(
            testAnalyticsErrorType.TOKEN_EXP_ERROR,
          ),
        );
      } else {
        yield put(
          actions.testAnalyticsDataError(testAnalyticsErrorType.RESPONSE_ERROR),
        );
      }
    }
  } catch (err) {
    yield put(
      actions.testAnalyticsDataError(testAnalyticsErrorType.RESPONSE_ERROR),
    );
  }
}

export function* testAnalyticsSaga() {
  yield takeLatest(actions.loadtestAnalyticsData.type, gettestAnalyticsData);
}
