export const getLocationSplits = location => {
  const testingType = location.pathname.split('/')[1] || '';
  const featureRoute =
    '/' + location.pathname.split('/').slice(1, 3).join('/') || '';
  const fullPath =
    '/' + location.pathname.split('/').slice(1, 4).join('/') || '';
  return { testingType, featureRoute, fullPath };
};
