import {useEffect, useRef, useState} from 'react';
import {Stream} from 'app/utils';

import {useInteractSlice} from 'app/pages/Interact/hooks';
import {retryStreamStart} from 'app/pages/Interact/utils';

const useStream = (channelName: string, retryStart: number) => {
  const { actions, dispatch } = useInteractSlice();
  const [videoState, setVideoState] = useState();
  const [videoError, setVideoError] = useState();
  const stateRef = useRef<any>();
  const errorRef = useRef<any>();

  const callback = videoStream => {
    // using callback because stream has event listeners which we do not track reactively
    console.log('callback', videoStream);
    if (!stateRef.current.remoteStream) {
      stateRef.current.remoteStream = videoStream;
    }
  };

  useEffect(() => {
    const start = async () => {
      try {
        stateRef.current = Stream();

        const startTime = performance.now(); // Record the start time
        await stateRef.current.startViewer(channelName, callback, false);
        await new Promise(resolve =>
          retryStreamStart(resolve, stateRef.current, false),
        );

        if (stateRef.current.failedToStartStream) {
          errorRef.current = 'failedToStartStream';
          //@ts-ignore
          setVideoError('failedToStartStream');
          setVideoState(undefined);
          console.log('Failed to open channel');
        } else {
          const endTime = performance.now(); // Record the end time
          const timeElapsed = endTime - startTime; // Calculate the time difference
            console.log(`Time elapsed: ${timeElapsed} milliseconds, state --> `, stateRef.current);
          dispatch(
            actions.setResponseTimes({
              receiveDeviceStream: timeElapsed,
            }),
          );
          setVideoState(stateRef.current);
          setVideoError(undefined);
        }
      } catch (e) {
        console.log(e);
        if (e.message === 'Invalid State') {
          console.log('retry');
        }
      }
    };

    if (channelName) {
      start();
    }
  }, [channelName, retryStart]);

  return [videoState, videoError];
};

export default useStream;
