export const filterDevice = (device, pathname) => {
  return device.serial === pathname.split('/').pop();
};

export const isDeviceSelfOwned = (device, email) => {
  return (
    device?.deviceParameters?.user &&
    device?.deviceParameters?.user?.email &&
    device?.deviceParameters?.user?.email === email &&
    (device?.deviceParameters?.deviceStatus === 'busy' ||
      device?.deviceParameters?.deviceStatus === 'MARK_BUSY')
  );
};

export const isDeviceBusy = device => {
  return (
    device.deviceParameters.deviceStatus === 'busy' ||
    device.deviceParameters.deviceStatus === 'MARK_BUSY'
  );
};

export const isDeviceFree = device => {
  return device.deviceParameters.deviceStatus === 'free';
};
export const isDeviceMarkBusyFree = device => {
  return (
    device.deviceParameters.deviceStatus === 'MARK_FREE'
    // ||
    // device.deviceParameters.deviceStatus === 'MARK_BUSY'
  );
};

export const isDeviceBusyAndNotSelfOwned = (device, email) => {
  return (
    !!(isDeviceBusy(device) && !isDeviceSelfOwned(device, email)) ||
    isDeviceMarkBusyFree(device)
  );
};

export const isDeviceSelfOwnedOrFree = (device, email) => {
  return isDeviceFree(device) || isDeviceSelfOwned(device, email);
};
