import React, { useState, useRef, useEffect } from 'react';
import {
  Button,
  Modal,
  ModalBody,
  ModalCloseButton,
  ModalContent,
  ModalFooter,
  ModalHeader,
  ModalOverlay,
  Text,
} from '@chakra-ui/react';
import styled from 'styled-components';

const TimerText = styled(Text)`
  font-size: 24px;
  text-align: center;
`;

const initialTime = 120;

const IdleTimeModal = ({ isOpen, onClose, onExit, onContinue }) => {
  const [remainingTime, setRemainingTime] = useState(initialTime); 
  const intervalRef = useRef<any>();

  useEffect(() => {
    if (isOpen) {
      setRemainingTime(initialTime);
      intervalRef.current = setInterval(() => {
        setRemainingTime(prevTime => {
          if (prevTime === 0) {
            clearInterval(intervalRef.current);
            onExit(); // Call the callback when the timer expires
          }
          return prevTime - 1;
        });
      }, 1000);
    }

    return () => {
      clearInterval(intervalRef.current); // Clean up the interval on modal close or unmount
    };
  }, [isOpen, onExit]);

  const handleButtonPress = () => {
    clearInterval(intervalRef.current); // Stop the timer
    onContinue();
    onClose(); // Close the modal
  };

  return (
    <Modal isOpen={isOpen} onClose={onClose}>
      <ModalOverlay />
      <ModalContent>
        <ModalHeader>Are you there?</ModalHeader>
        <ModalBody>
          <TimerText>Your session will exit in {remainingTime}s</TimerText>
        </ModalBody>
        <ModalFooter>
          <Button colorScheme="teal" onClick={handleButtonPress}>
            Continue Session
          </Button>
        </ModalFooter>
      </ModalContent>
    </Modal>
  );
};

export default IdleTimeModal;
