// @ts-nocheck
import SubLayout from 'app/components/SubLayout';
import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import LoaderOverlay from '../../components/LoaderOverlay';
import Wizard from '../../components/Wizard';
import { selectSyntheticMonitoring } from '../../slice/selectors';
import EventConfiguration from '../EventConfiguration';
import JourneyConfiguration from '../JourneyConfiguration';
import KpiConfiguration from '../KpiConfiguration';
import { useSyntheticMonitoringSlice } from '../../slice';

import Subheader from 'app/components/Subheader';
import SubContainer from 'app/components/SubContainer';

function KpiBuilder() {
  const tabpath = ['/journey'];
  const tabroute = ['Create Journey'];
  const {
    loadingCreateJourney,
    loadingCreateKpi,
    loadingfetchItem,
    loadingCreateEvent,
    loadingUpdateJourney,
  } = useSelector(selectSyntheticMonitoring);
  const { actions } = useSyntheticMonitoringSlice();
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(actions.itemUpdatedFetch({}));
  }, []);

  return (
    <LoaderOverlay
      isloading={
        loadingCreateJourney ||
        loadingfetchItem ||
        loadingCreateKpi ||
        loadingCreateEvent ||
        loadingUpdateJourney
      }
    >
      <Subheader path={tabpath} tabroute={tabroute}></Subheader>

      <SubContainer isCenter={true}>
        <SubLayout>
          <Wizard
            journeyConfiguration={<JourneyConfiguration></JourneyConfiguration>}
            kpiConfiguration={<KpiConfiguration></KpiConfiguration>}
            eventConfiguration={<EventConfiguration></EventConfiguration>}
          ></Wizard>
        </SubLayout>
      </SubContainer>
    </LoaderOverlay>
  );
}

export default KpiBuilder;
