// @ts-nocheck
import { Flex } from '@chakra-ui/layout';
import TabHeader from '../TabHeader';
import { Input } from '@chakra-ui/input';
import { Checkbox, CheckboxGroup } from '@chakra-ui/react';
import { Button } from '@chakra-ui/react';
import { Select } from '@chakra-ui/react';
import { Box } from '@chakra-ui/react';
import pauseIcon from '../../../../../Assets/pause-icon.png';
import playIcon from '../../../../../Assets/play-icon.png';
import stopIcon from '../../../../../Assets/stop-icon.png';
import downloadIcon from '../../../../../Assets/download-icon.png';
import LogViewer from './logViewer';
import { forwardRef, useEffect, useState } from 'react';
import useReceivedMessage from 'app/pages/Interact/hooks/useReceivedMessage';

const DeviceLogs = forwardRef(({ videoState }, ref) => {
  const [isStreamStarted, setIsStreamStarted] = useState(false);
  const [streamControl, setStreamControl] = useState('play');
  const [deviceLog, setDeviceLog] = useState('');
  const [timer, setTimer] = useState(0);
  useReceivedMessage(ref, setDeviceLog, setTimer, streamControl);

  // console.log('device log123', deviceLog);
  const levelOptions = [
    { option: 'All Levels' },
    { option: 'Emergency', androidKey: 'E', iphoneKey: '<Emergency>' },
    { option: 'Alert', androidKey: 'A', iphoneKey: '<Alert>' },
    { option: 'Critical', androidKey: 'C', iphoneKey: '<Critical>' },
    { option: 'Error', androidKey: 'E', iphoneKey: '<Error>' },
    { option: 'Warning', androidKey: 'W', iphoneKey: '<Warning>' },
    { option: 'Notice', androidKey: 'N', iphoneKey: '<Notice>' },
    { option: 'Info', androidKey: 'I', iphoneKey: '<Info>' },
    { option: 'Debug', androidKey: 'D', iphoneKey: '<Debug>' },
    { option: 'Verbose', androidKey: 'V', iphoneKey: '<Verbose>' },
  ];
  const [filters, setFilters] = useState({
    isExclude: false,
    pid: '',
    tid: '',
    tag: '',
    package: '',
    search: '',
  });
  const inputChangeHandler = (event, name) => {
    if (name === 'isExclude') {
      return setFilters(prev => {
        return { ...prev, [name]: event.target.checked };
      });
    }

    setFilters(prev => {
      const updatedFilters = { ...prev, [name]: event.target.value };
      const filterMessageString = `input.devicelog.Filter, ${
        ref.current.videoState.channelName
      }, ${JSON.stringify(filters)}`;
      // sendMessageToController(filterMessageString);
      return updatedFilters;
    });
  };

  const handleStreamControl = eventType => {
    console.log('handleStreamControl', ref.current.videoState);
    const messageString = `input.devicelog.${
      eventType === 'play' ? 'Resume' : 'Pause'
    }, ${ref.current.videoState.channelName}, ${
      eventType === 'play' ? JSON.stringify(filters) : 'None'
    }`;
    // sendMessageToController(messageString);
    setStreamControl(eventType);
  };

  const sendMessageToController = message => {
    ref.current.videoState.sendMessage(message);
  };

  // console.log('streamControl =====', streamControl);

  return (
    <TabHeader heading="Device Log" padding={'10px'}>
      <Box padding={'10px'}>
        <Flex alignItems={'center'} marginBottom={'10px'}>
          <Input
            _focus={{ boxShadow: ' 0 0 0 white' }}
            placeholder="Search Logs"
            onChange={event => {
              inputChangeHandler(event, 'search');
            }}
            color="black"
            border="1px solid #C8C8C8"
            borderRadius="5px"
            width={'60%'}
            //   value={url}
            type="text"
            //   onChange={e => setUrl(e.target.value)}
          ></Input>
          <Checkbox
            width={'40%'}
            marginLeft={'20px'}
            colorScheme="blue"
            onChange={event => {
              inputChangeHandler(event, 'isExclude');
            }}
          >
            Exclude
          </Checkbox>
        </Flex>
        <Flex gridGap={'10px'} marginBottom={'10px'}>
          <Input
            _focus={{ boxShadow: ' 0 0 0 white' }}
            placeholder="PID"
            onChange={event => {
              inputChangeHandler(event, 'pid');
            }}
            color="black"
            border="1px solid #C8C8C8"
            borderRadius="5px"
            width={'25%'}
            //   value={url}
            type="text"
            //   onChange={e => setUrl(e.target.value)}
          ></Input>
          <Input
            _focus={{ boxShadow: ' 0 0 0 white' }}
            placeholder="TID"
            onChange={event => {
              inputChangeHandler(event, 'tid');
            }}
            color="black"
            border="1px solid #C8C8C8"
            borderRadius="5px"
            width={'25%'}
            //   value={url}
            type="text"
            //   onChange={e => setUrl(e.target.value)}
          ></Input>
          <Input
            _focus={{ boxShadow: ' 0 0 0 white' }}
            placeholder="Tag"
            onChange={event => {
              inputChangeHandler(event, 'tag');
            }}
            color="black"
            border="1px solid #C8C8C8"
            borderRadius="5px"
            width={'25%'}
            //   value={url}
            type="text"
            //   onChange={e => setUrl(e.target.value)}
          ></Input>
          <Input
            _focus={{ boxShadow: ' 0 0 0 white' }}
            placeholder="Package"
            onChange={event => {
              inputChangeHandler(event, 'package');
            }}
            color="black"
            border="1px solid #C8C8C8"
            borderRadius="5px"
            width={'25%'}
            //   value={url}
            type="text"
          ></Input>
        </Flex>
        <Flex gridGap={'10px'}>
          <Button
            maxWidth={'100px'}
            padding={0}
            type={'button'}
            colorScheme="gray"
            onClick={() => {
              handleStreamControl(streamControl === 'play' ? 'pause' : 'play');
            }}
          >
            <img
              width={'60%'}
              src={streamControl === 'play' ? pauseIcon : playIcon}
              alt="pause"
            />
          </Button>
          <Button
            maxWidth={'100px'}
            padding={0}
            type={'button'}
            colorScheme="gray"
            onClick={() => {
              handleStreamControl('stop');
            }}
          >
            <img width={'60%'} src={stopIcon} alt="stop" />
          </Button>
          <Button
            maxWidth={'100px'}
            padding={0}
            type={'button'}
            colorScheme="gray"
          >
            <img width={'60%'} src={downloadIcon} alt="download" />
          </Button>
          <Select
            onChange={event => {
              inputChangeHandler(event, 'level');
            }}
          >
            {levelOptions.map(level => (
              <option key={level.option} value={level.option}>
                {level.option}
              </option>
            ))}
          </Select>
        </Flex>
      </Box>
      <Box>
        <LogViewer
          filters={filters}
          levelOptions={levelOptions}
          isStreamStarted={isStreamStarted}
          setIsStreamStarted={setIsStreamStarted}
          log={deviceLog}
        />
      </Box>
    </TabHeader>
  );
});
export default DeviceLogs;
