import { formatMessage, getScaledCoordinates } from './index';

const state = {
  counter: 0,
  isDown: false,
  mouseMove: null,
  scaleRatioWidth: 1,
  scaleRatioHeight: 1,
  prevX: null,
  prevY: null,
};

const mouseDownListener = (event, scalingFactor, serial, videoRef, deviceCategoryRef) => {
  try {
    state.scaleRatioWidth = videoRef.current.scalingFactor.width;
    state.scaleRatioHeight = videoRef.current.scalingFactor.height;

    state.isDown = true;
    // Skip secondary click
    if (event.which === 3) {
      return;
    }

    event.preventDefault();

    startMousing(videoRef, serial, scalingFactor, deviceCategoryRef);

    const rect = event.target.getBoundingClientRect();
    var x = event.clientX - rect.left;
    var y = event.clientY - rect.top;
    const { scaledX, scaledY } = getScaledCoordinates(
      x,
      y,
      state.scaleRatioWidth,
      state.scaleRatioHeight,
    );
    state.counter += 1;
    let msg = [
      'input.touchDown',
      serial,
      {
        seq: state.counter,
        contact: 0,
        x: scaledX,
        y: scaledY,
        pressure: 0.5,
      },
    ];
    console.log(msg);

    videoRef.current.sendMessage(formatMessage(msg));

    state.counter += 1;
    msg = ['input.touchCommit', serial, { seq: state.counter }];
    console.log(msg);
    videoRef.current.sendMessage(formatMessage(msg));
  } catch (e) {
    console.log(e);
  }
};

const startMousing = (videoRef, serial, scalingFactor, deviceCategoryRef) => {
  try {
    state.counter += 1;
    const msg = [
      'input.gestureStart',
      serial,
      {
        seq: state.counter,
        width: videoRef.current.widthForGesture,
        height: videoRef.current.heightForGesture,
        deviceCategory: deviceCategoryRef?.current,
      },
    ];
    console.log(msg);
    videoRef.current.sendMessage(formatMessage(msg));
    state.mouseMove = videoRef.current.addEventListener('mousemove', event =>
      mouseMoveListener(event, serial, videoRef),
    );
  } catch (e) {
    console.log(e);
  }
};

function stopMousing(videoRef, serial, left = true) {
  try {
    videoRef.current.removeEventListener('mousemove', state.mouseMove);

    if (left) {
      state.counter += 1;
      const msg = ['input.gestureStop', serial, { seq: state.counter }];
      console.log(msg);
      videoRef.current.sendMessage(formatMessage(msg));
    }
    state.counter = 0;
  } catch (e) {
    console.log(e);
  }
}

function mouseMoveListener(event, serial, videoRef) {
  try {
    state.scaleRatioWidth = videoRef.current.scalingFactor.width;
    state.scaleRatioHeight = videoRef.current.scalingFactor.height;

    // Skip secondary click
    if (event.which === 3) {
      return;
    }
    event.preventDefault();

    if (state.isDown) {
      const rect = event.target.getBoundingClientRect();
      var x = event.clientX - rect.left;
      var y = event.clientY - rect.top;
      const { scaledX, scaledY } = getScaledCoordinates(
        x,
        y,
        state.scaleRatioWidth,
        state.scaleRatioHeight,
      );

      if (state.prevX === scaledX && state.prevY === scaledY) {
        return;
      }

      state.prevX = scaledX;
      state.prevY = scaledY;
      state.counter += 1;
      let msg = [
        'input.touchMove',
        serial,
        {
          seq: state.counter,
          contact: 0,
          x: scaledX,
          y: scaledY,
          pressure: 0.5,
        },
      ];
      console.log(msg);
      videoRef.current.sendMessage(formatMessage(msg));

      state.counter += 1;
      msg = ['input.touchCommit', serial, { seq: state.counter }];
      console.log(msg);
      videoRef.current.sendMessage(formatMessage(msg));
    }
  } catch (e) {
    console.log(e);
  }
}

function mouseLeaveListener(event, videoRef, serial) {
  try {
    if (state.isDown) {
      state.counter += 1;
      const msg = ['input.gestureStop', serial, { seq: state.counter }];
      console.log(msg);
      videoRef.current.sendMessage(formatMessage(msg));
    }
    state.isDown = false;
    event.preventDefault();
    stopMousing(videoRef, serial, false);
  } catch (e) {
    console.log(e);
  }
}

function mouseUpListener(event, videoRef, serial) {
  try {
    state.isDown = false;
    // Skip secondary click
    if (event.which === 3) {
      return;
    }
    event.preventDefault();

    state.counter += 1;

    let msg = ['input.touchUp', serial, { seq: state.counter, contact: 0 }];
    console.log(msg);
    videoRef.current.sendMessage(formatMessage(msg));
    state.counter += 1;
    msg = ['input.touchCommit', serial, { seq: state.counter }];
    console.log(msg);
    videoRef.current.sendMessage(formatMessage(msg));

    stopMousing(videoRef, serial);
  } catch (e) {
    console.log(e);
  }
}

export {
  mouseDownListener,
  stopMousing,
  mouseMoveListener,
  startMousing,
  mouseUpListener,
  mouseLeaveListener,
};
