import { REACT_APP_APP_TESTING_BASE_URL } from 'utils/enviroment';

const BASE_URL = REACT_APP_APP_TESTING_BASE_URL;

export const startSession = (deviceId: string) =>
  `${BASE_URL}/tv/device/${deviceId}/session/start`;
export const generatePresignedUrl = `${BASE_URL}/testexecute/generate-presigned-url`;
export const stopSession = (deviceId: string, sessionId: string) =>
  `${BASE_URL}/tv/device/${deviceId}/session/stop?id=${sessionId}`;
