import {getEmailId} from 'Authentication/configuration/GetEmail';
import {getCognitoAuthenticatedOpenId} from 'app/pages/TestDashboard/components/configUser';
import AWS from 'aws-sdk';

const roleName = process.env.REACT_APP_AWS_QUICKSIGHT_ROLE_ARN_NAME;
const accountId = process.env.REACT_APP_AWS_QUICKSIGHT_ACCOUNT_ID;

const getCredentials = async () => {
  const email = getEmailId();
  const openIdToken = await getCognitoAuthenticatedOpenId().then(token => {
    return token;
  });
  const stsClient = new AWS.STS();
    // @ts-ignore
  let stsParams = {
    RoleSessionName: email,
      // @ts-ignore
    WebIdentityToken: openIdToken.Token,
    RoleArn: `arn:aws:iam::${accountId}:role/${roleName}`,
  };
  return new Promise((resolve, reject) => {
    stsClient.assumeRoleWithWebIdentity(stsParams, function (err, data) {
      console.log('data=====', data);
      if (err) {
        reject(err);
      } else {
        const credentials = {
            // @ts-ignore
          accessKeyId: data.Credentials.AccessKeyId,
            // @ts-ignore
          secretAccessKey: data.Credentials.SecretAccessKey,
            // @ts-ignore
          sessionToken: data.Credentials.SessionToken,
        };
        resolve(credentials);
      }
    });
  });
};

export default getCredentials;
