export function addStartPrefix(inputArray) {
  const formattedArr = inputArray.map(item => `"${item}"`);
  return `Start [${formattedArr.join(',')}]`;
}

export function addStopPrefix(inputArray) {
  const formattedArr = inputArray.map(item => `"${item}"`);
  return `Stop [${formattedArr.join(',')}]`;
}
