import {useCallback, useRef, useState} from "react";
import {Stream} from "../../../utils";

const useVideoPlayer = ({channelName, eventCallback}) => {
    const streamStateRef = useRef<any>(Stream());

    const [remoteVideo, setRemoteVideo] = useState<MediaStream>();
    const [remoteVideoError, setRemoteVideoError] = useState<string>();

    const callback = useCallback((videoStream: MediaStream) => {
        // using callback because stream has event listeners which we do not track reactively
        console.log('callback', videoStream, streamStateRef.current.remoteStream);
        if (!streamStateRef.current.remoteStream) {
            streamStateRef.current.remoteStream = videoStream;
        }
        setRemoteVideo(videoStream)
    }, []);

    const startStream = useCallback(async () => {
        try {
            streamStateRef.current = Stream();
            console.log("streamStateRef.current --> ", streamStateRef.current)

            const startTime = performance.now(); // Record the start time
            await streamStateRef.current.startViewer(channelName, callback, eventCallback, false);

            if (streamStateRef.current.failedToStartStream) {
                setRemoteVideoError('failedToStartStream');
                setRemoteVideo(undefined);
                console.log('Failed to open channel');
            } else {
                const endTime = performance.now(); // Record the end time
                const timeElapsed = endTime - startTime; // Calculate the time difference
                console.log(`Time elapsed: ${timeElapsed} milliseconds, state --> `, streamStateRef.current);

                setRemoteVideoError(undefined);
            }
        } catch (e) {
            console.log(e);
        }
    }, [])

    const stopStream = useCallback(async () => {
        if (streamStateRef.current?.stopViewer) {
            streamStateRef.current.stopViewer()
        } else {
            console.warn("Failed to stop stream, Stream is empty!")
        }
    }, []);

    return {
        startStream,
        stopStream,
        remoteVideo,
        remoteVideoError,
    };
};

export default useVideoPlayer;
