import { useEffect } from 'react';

const useVideoStreamsStarted = (
  controllerState,
  setVideoStreamsStarted,
  setRetryStart,
  isCleanExit,
) => {
  useEffect(() => {
    if (controllerState) setRetryStart(0);
    //@ts-ignore
    if (
      !isCleanExit &&
      controllerState &&
      controllerState.dataChannel?.readyState
    ) {
      setVideoStreamsStarted(true);
      //@ts-ignore
      controllerState.stopViewer();
    }
  }, [controllerState, setVideoStreamsStarted, setRetryStart, isCleanExit]);
};

export default useVideoStreamsStarted;
