import { useEffect, useRef } from 'react';
import { useInteractSlice } from '.';

const useStopSession = (deviceStopTried, setIsSessionStopped) => {
  const {
    actions,
    selector: { session },
    dispatch,
    device,
  } = useInteractSlice();
  const firstRun = useRef(true);

  useEffect(() => {
    const stop = async () => {
      if (session) {
        dispatch(
          actions.stopSession({
            deviceId: device.serial,
          }),
        );
        dispatch(actions.clearSession());
      }
      setIsSessionStopped(true);
      firstRun.current = false;
    };
    if (deviceStopTried && firstRun.current) {
      stop();
    }
  }, [
    setIsSessionStopped,
    actions,
    dispatch,
    device,
    deviceStopTried,
    session,
  ]);

  return null;
};

export default useStopSession;
