const retryStreamStart = (resolve, state, onlyData) => {
  let count = 0;
  let retryTimeout;

  const connect = () => {
    try {
      count++;

      if (count >= 60) {
        console.error('Failed to connect');
        state.stopViewer();
        state.failedToStartStream = true;
        clearTimeout(retryTimeout);
        resolve();
        return undefined;
      }

      if (onlyData) {
        console.log(`#${count} Checking for Data Channel Ready State`);
        if (state.dataChannel && state.dataChannel.readyState === 'open') {
          clearTimeout(retryTimeout);
          resolve();
          return undefined;
        }
      } else {
        console.log(`#${count} Checking for Remote Video Track`);
        if (state.remoteStream) {
          console.log('Found Remote Video Track');
          clearTimeout(retryTimeout);
          resolve();
          return undefined;
        }
      }
      retryTimeout = setTimeout(connect, 1000);
    } catch (e) {
      console.log(e);
    }
  };

  connect();
};

export default retryStreamStart;
