import { delay, Stream } from 'app/utils';
import { addStopPrefix, getSavedSessionId, retryStreamStart } from '.';

import { getControllerMessageParameters } from '../utils';

const controllerStopMessage = async (device, interactType, deviceId) => {
  const { controllerChannelName, deviceChannelNames, remoteChannelNames } =
    getControllerMessageParameters(device, interactType);
  // const deviceId = window.location.href.split('/').pop() || '';
  const savedSessionId = getSavedSessionId(interactType, deviceId);
  const state = Stream();
  await state.startViewer(controllerChannelName, () => {}, true);
  await new Promise(resolve => retryStreamStart(resolve, state, true));
  if (state.failedToStartStream) {
    console.log('Failed to open data channel');
  } else {
    if (!savedSessionId) {
      console.log('NO session ID');
      return;
    }
    let stopMessage;
    if (
      interactType === 'lrInteract' &&
      remoteChannelNames &&
      remoteChannelNames.length > 0
    ) {
      stopMessage = addStopPrefix([
        savedSessionId,
        ...deviceChannelNames,
        remoteChannelNames[0]['device-uuid'],
        'MARK_FREE',
      ]);
      console.log(
        `Sending ${stopMessage}`,
        remoteChannelNames[0]['device-uuid'],
      );
    } else {
      stopMessage = addStopPrefix([
        savedSessionId,
        ...deviceChannelNames,
        'MARK_FREE',
      ]);
      console.log(`Sending ${stopMessage}`);
    }
    //addStopPrefix(deviceChannelNames);

    /* await delay(1000); */

    state.sendMessage(stopMessage);
    //TODO: remove delay and use remote message for confirmation
    await delay(1000);
    state.stopViewer();
  }
};

export default controllerStopMessage;
