import { useEffect } from 'react';
import { useNavigate } from 'react-router-dom';

import { isDeviceBusyAndNotSelfOwned } from 'app/utils/device';
import { useInteractSlice } from '.';

const useSelectRemote = (setShowRemoteSelection: any) => {
  const navigate = useNavigate();
  const {
    interactType,
    actions,
    dispatch,
    selector: { session, selectedRemote },
    controllerDetails: {
      controllerChannelName,
      deviceChannelNames,
      remoteChannelNames,
    },
    device,
    getEmailId,
  } = useInteractSlice();

  useEffect(() => {
    const numberOfRemotes = remoteChannelNames.length || 0;
    if (
      interactType === 'lrInteract' &&
      device &&
      Object.keys(device).length &&
      !isDeviceBusyAndNotSelfOwned(device, getEmailId()) &&
      selectedRemote &&
      !Object.keys(selectedRemote).length &&
      numberOfRemotes
    ) {
      if (numberOfRemotes > 1) {
        setShowRemoteSelection(true);
      } else {
        /* const remoteDevice = getRemoteDetails(remoteChannelNames[0], device); */
        dispatch(actions.setSelectedRemote(remoteChannelNames[0]));
      }
    }
  }, [
    getEmailId,
    interactType,
    actions,
    dispatch,
    device,
    remoteChannelNames,
    selectedRemote,
    setShowRemoteSelection,
  ]);
};

export default useSelectRemote;
