import { forwardRef } from 'react';
import Screenshots from 'app/pages/Interact/components/InteractionItems/Screenshots';
import DeviceInfo from 'app/pages/Interact/components/InteractionItems/DeviceInfo';
import Tabs from './Tabs';
import {
  RiRemoteControl2Fill,
  RiScreenshot2Fill,
  RiUploadFill,
} from 'react-icons/ri';
import { MdPermDeviceInformation } from 'react-icons/md';
import { Flex } from '@chakra-ui/layout';
import { FaNetworkWired } from 'react-icons/fa';
import { VscDebugConsole } from 'react-icons/vsc';

import AppUpload from '../AppUpload';
import { addTabIf } from './utils';
import WebBrowsing from '../WebBrowsing';
import NetworkLogs from '../NetworkLogs';
import DeviceLogs from '../DeviceLogs';
import deviceLogIcon from '../../../../../Assets/device-logs.png';
import networkLogIcon from '../../../../../Assets/network-logs.png';

const Sidebar = forwardRef(({ children, device, videoState }, ref) => {
  const screenshots = {
    icon: RiScreenshot2Fill,
    content: <Screenshots ref={ref} />,
  };

  const deviceInfo = {
    icon: MdPermDeviceInformation,
    content: <DeviceInfo device={device} />,
  };

  const appUpload = {
    icon: RiUploadFill,
    content: <AppUpload ref={ref} />,
  };

  const remote = {
    icon: RiRemoteControl2Fill,
    content: <Flex justifyContent="center">{children}</Flex>,
  };

  const networkLogs = {
    icon: FaNetworkWired,
    content: <NetworkLogs />,
  };

  const deviceLogs = {
    icon: VscDebugConsole,
    content: <DeviceLogs ref={ref} videoState={videoState} />,
  };

  let tabContents = [
    ...addTabIf(children, remote),
    screenshots,
    //...addTasbIf(!children,
    appUpload, //),
    //  ...addTabIf(!children && platform !== 'ios', webBrowsing),
    deviceInfo,

    ...addTabIf(!children, deviceLogs),
    // ...addTabIf(!children, networkLogs),
  ];

  const itemList = tabContents.map(({ icon: Icon, content }) => ({
    tab: { icon: <Icon size={24} color="gray" /> },
    component: content,
  }));

  return <Tabs itemList={itemList} />;
});

export default Sidebar;
