import { createSelector } from '@reduxjs/toolkit';
import { RootState } from 'types';
import { initialState } from '.';
import { getControllerMessageParameters } from '../utils';
import { filterDevice } from 'app/utils';

export const selectMobileInteractSlice = (state: any) => state.mobileInteract;
export const selectLrInteractSlice = (state: any) => state.lrInteract;

const createSliceSelectors = (stateSelector: (state: any) => any) => ({
  selectAll: (state: any) => stateSelector(state),
  selectResponseTimes: (state: any) => stateSelector(state.responseTimes),
  selectDevices: (state: any) => stateSelector(state.devices),
  selectDevicesLoading: (state: any) => stateSelector(state.devicesLoading),
  selectDevicesError: (state: any) => stateSelector(state.devicesError),
  selectSelectedDevice: (state: any, interactType, pathname) => {
    const sliceName = interactType;
    const devices = state[sliceName].devices;
    if (devices && Object.keys(devices).length) {
      const currentDevice = devices.filter(device =>
        filterDevice(device, pathname),
      );
      return currentDevice.length ? currentDevice[0] : {};
    } else {
      return {};
    }
  },
  selectControllerDetails: (state: any, interactType, pathname) => {
    const sliceName = interactType;
    const devices = state[sliceName].devices;
    if (devices && Object.keys(devices).length) {
      const currentDevice = devices.filter(device =>
        filterDevice(device, pathname),
      );
      const device = currentDevice.length ? currentDevice[0] : null;
      const { controllerChannelName, deviceChannelNames, remoteChannelNames } =
        getControllerMessageParameters(device, sliceName);
      return { controllerChannelName, deviceChannelNames, remoteChannelNames };
    } else {
      return {
        controllerChannelName: null,
        deviceChannelNames: [],
        remoteChannelNames: [],
      };
    }
  },
  /* `selectIsConnected` is a selector function that returns the value of the `isConnected` property
  from the state. It takes the state as an argument and uses the `stateSelector` function to select
  the `isConnected` property from the state. */
  selectIsConnected: (state: any) => stateSelector(state.isConnected),
});

export const mobileInteractSelectors = createSliceSelectors(
  selectMobileInteractSlice,
);
export const lrInteractSelectors = createSliceSelectors(selectLrInteractSlice);
