import { Flex, Icon, Spacer, Text, Tooltip } from '@chakra-ui/react';
import { CopyToClipboard } from 'react-copy-to-clipboard';
import React, { useState } from 'react';
import { MdContentCopy } from 'react-icons/md';
import { TiTick } from 'react-icons/ti';
import { Link } from 'react-router-dom';

const WebInspectorUrl = ({ text }) => {
  const [copyIcon, setCopyIcon] = useState(true);
  const setCopyIconHandler = () => {
    console.log('on copy trigger');
    setCopyIcon(false);
    setTimeout(() => {
      setCopyIcon(true);
    }, 2000);
  };
  return (
    <Flex
      bg="white2"
      padding="0px 10px"
      align="center"
      borderRadius="5px"
      h="35px"
      color="blue1"
      textTransform="none"
    >
      <>
        <Text
          as="a"
          textTransform="none"
          href={text}
          target="_blank"
          rel="noopener noreferrer"
          textDecoration="underline"
        >
          {text}
        </Text>
        <Spacer />
        <Tooltip label={text} fontSize="10px" placement="auto-start">
          <span>
            {copyIcon ? (
              <CopyToClipboard text={text} onCopy={setCopyIconHandler}>
                <Icon cursor="pointer" height="20px" as={MdContentCopy}></Icon>
              </CopyToClipboard>
            ) : (
              <Icon height="20px" as={TiTick}></Icon>
            )}
          </span>
        </Tooltip>
      </>
    </Flex>
  );
};

export default WebInspectorUrl;
