// @ts-nocheck
import { Flex } from '@chakra-ui/react';
import React from 'react';

function DashedButton({
  buttonHandler,
  children,
  width,
  height,
  isBorderBottom,
  name,
}) {
  return (
    <Flex
      align="center"
      padding="10px"
      data-dashed-button={name}
      color="grey13"
      onClick={buttonHandler}
      cursor="pointer"
      borderRadius="4px"
      bg="white"
      marginTop="2px"
      border="1.3px dashed #7E8299"
      borderBottom={isBorderBottom && '0px solid'}
      fontWeight="400"
      w={width || '200px'}
      h={height || '40px'}
    >
      {children}
    </Flex>
  );
}

export default DashedButton;
