import locationIcon from 'app/Assets/Icon material-place.svg';
import displayIcon from 'app/Assets/Icon awesome-mobile-alt.svg';
import deviceInfoIcon from 'app/Assets/speaker_black_24dp.svg';
import plateformIcon from 'app/Assets/dns_black_24dp.svg';
import networkIcon from 'app/Assets/network_check_black_24dp.svg';

const getExtractedData = device => {
  const data = {
    serial: device?.serial || '',
    brand: device?.brand || '',
    modelName: device?.modelName || '',
    modelNumber: device?.modelNumber || '',
    platform: device?.platform || '',
    osVersion: device?.osVersion || '',
    city: device?.controllerParameters?.city || '',
    country: device?.controllerParameters?.country || '',
    screenResolutionWidth:
      device?.deviceParameters?.screenResolution?.width || '',
    screenResolutionHeight:
      device?.deviceParameters?.screenResolution?.height || '',
    physicalWidth: device?.deviceParameters?.physicalScreenSize?.width || '',
    physicalHeight: device?.deviceParameters?.physicalScreenSize?.height || '',
    os: device?.platform || '',
    osVersion: device?.osVersion || '',
    networkType: device?.deviceParameters?.networkType || '',
    mobileNumber:
      device?.deviceParameters?.cellularInfo[0]?.cellularNumber || '',
  };
  return data;
};
const getExtractedOldDeviceData = device => {
  const data = {
    serial: device?.serial || '',
    brand: device?.brand || '',
    modelName: device?.modelName || '',
    modelNumber: device?.modelNumber || '',
    platform: device?.platform || '',
    osVersion: device?.osVersion || '',
    city: device?.deviceParameters?.city || '',
    country: device?.deviceParameters?.country || '',
    screenResolutionWidth: device?.deviceParameters?.width || '',
    screenResolutionHeight: device?.deviceParameters?.height || '',
    physicalWidth: device?.deviceParameters?.physicalWidth || '',
    physicalHeight: device?.deviceParameters?.physicalHeight || '',
    screenSize: device?.deviceParameters?.screenSize || '',
    os: device?.tvos || device?.platform || '',
    osVersion: device?.osVersion || '',
    networkType: device?.deviceParameters?.network || '',
    mobileNumber:
      device?.deviceParameters?.network === 'Wi-Fi'
        ? 'NA'
        : device?.deviceParameters?.mobile,
  };
  return data;
};
const getDisplayData = downloadJson => {
  const data = [
    {
      category: 'Device Information',
      icon: deviceInfoIcon,
      items: [
        { key: 'Serial Number', value: downloadJson?.serial },
        { key: 'Brand', value: downloadJson?.brand },
        { key: 'Model', value: downloadJson?.modelName },
        { key: 'Model Number', value: downloadJson?.modelNumber },
      ],
    },
    {
      category: 'Platform',
      icon: plateformIcon,
      items: [
        { key: 'OS', value: downloadJson?.os },
        { key: 'OS Version', value: downloadJson?.osVersion },
      ],
    },
    {
      category: 'Physical Location',
      icon: locationIcon,
      items: [
        { key: 'City', value: downloadJson?.city },
        { key: 'Country', value: downloadJson?.country },
      ],
    },
    {
      category: 'Display',
      icon: displayIcon,
      items: [
        {
          key: 'Resolution',
          value: `${downloadJson?.screenResolutionWidth}x${downloadJson?.screenResolutionWidth}`,
        },
        {
          key: 'Screen Size',
          value: downloadJson?.screenSize, //`${downloadJson?.physicalWidth}x${downloadJson?.physicalHeight}`,
        },
      ],
    },
    {
      category: 'Network Info',
      icon: networkIcon,
      items: [
        {
          key: 'Network',
          value: downloadJson?.networkType,
        },
        {
          key: 'Mobile Number',
          value:
            downloadJson?.networkType === 'Wi-Fi'
              ? 'NA'
              : downloadJson?.mobileNumber,
        },
      ],
    },
  ];
  return data;
};

const getDeviceMetaData = device => {
  if (device.hasOwnProperty('logParameters')) {
    const fileName = `${device?.brand}-${device?.serial}`;
    const downloadJson = getExtractedOldDeviceData(device);
    const displayData = getDisplayData(downloadJson);
    return { displayData, downloadJson, fileName };
  }
  const fileName = `${device?.brand}-${device?.serial}`;
  const downloadJson = getExtractedData(device);
  const displayData = getDisplayData(downloadJson);
  return { displayData, downloadJson, fileName };
};

export { getDeviceMetaData };
