import {useEffect, useState} from 'react';
import {getExtractMessageShouldNotKey,} from '../slice/utils/getExtractMessage';
import * as zlib from 'zlib';

const useReceivedMessage = (ref, setStatusMessage, setTimer, streamControl) => {
  const [log, setLog] = useState('');
  useEffect(
    () => {
      //@ts-ignore
      console.log('trigger effect', { log });

      if (
        ref &&
        ref.current &&
        ref.current.videoState &&
        ref.current.videoState.dataChannel
      ) {
        ref.current.videoState.dataChannel.onmessage = msg => {
          // console.log('controller messagessssssssssssssss', msg?.data);
          setStatusMessage('');
          try {
            let data = msg?.data;
            // console.log(
            //   'contorller data message buffer condition',
            //   data && data instanceof ArrayBuffer,
            // );
            if (data && data instanceof ArrayBuffer) {
              const buffer = Buffer.from(data);
              let uncompressedData = zlib.inflateSync(buffer).toString();
                console.log('uncompressedData --> ', uncompressedData);
              data = uncompressedData;
              if (streamControl === 'play') {
                setStatusMessage(uncompressedData);
              }
              // setTimer()
            }
            const received = getExtractMessageShouldNotKey(
              data,
              'list.packages',
            );
            setLog(received);
          } catch (e) {
            console.error('parsing log failed:', e);
          }
        };
      }
      return () => {
        setLog('');
      };
    },
    [ref, ref?.current, ref?.current?.videoState?.dataChannel, streamControl],
    // setStatusMessage,
  );

  return log;
};

export default useReceivedMessage;
