import { useEffect, useState } from 'react';
import { Navigate, Outlet } from 'react-router-dom';
import { Flex } from '@chakra-ui/react';
import ROUTES from 'app/constants/Routes';
import Navbar from 'app/pages/HomePage/components/Navbar';
import OutletWrapper from '../MainContainerWrapper';
import DynamicSidebar from 'app/components/DynamicSideBar';
import { useAuth } from 'utils/authentication/useAuth';
import { useTestingContext } from 'app/hooks';
import Breadcrumb from '../SubHeaderItems/Breadcrumb';

const PrivateOutlet = () => {
  const { isAuthenticated, userRole } = useAuth();
  const { testingType, featureRoute, fullPath } = useTestingContext();
  const [sidebarToggle, setsidebarToggle] = useState(false);
  const [isLoading, setIsLoading] = useState(true);
  const handleSidebar = () => {
    setsidebarToggle(!sidebarToggle);
  };
  useEffect(() => {
    setTimeout(() => {
      setIsLoading(false);
    }, 0);
  }, []);

  if (isLoading) {
    return <div>Loading...</div>; // Show loading indicator until authentication data is loaded
  }

  return isAuthenticated ? (
    <>
      <Flex width="100%" flexDirection="column">
        <Navbar />

        <>
          <DynamicSidebar
            handleSidebar={handleSidebar}
            sidebarToggle={sidebarToggle}
          ></DynamicSidebar>
          <OutletWrapper
            sidebarToggle={sidebarToggle}
            handleSidebar={handleSidebar}
          >
            <Breadcrumb />
            <Outlet
              context={{
                isAuthenticated,
                userRole,
                testingType,
                featureRoute,
                fullPath,
              }}
            />
          </OutletWrapper>
        </>
      </Flex>
    </>
  ) : (
    <>
      {console.log('navigate to login')}
      <Navigate to={ROUTES.LOGIN_PAGE} />
    </>
  );
};

export default PrivateOutlet;
