import { Flex, useMediaQuery } from '@chakra-ui/react';
import React from 'react';

function ResponsiveAliginWrapper({ children }) {
  const [isLargerScreen] = useMediaQuery('(min-width: 1900px)');
  return (
    <Flex
      bg="white"
      width="100%"
      flexDirection="column"
      align={isLargerScreen ? 'center' : 'left'} // Use 'align' instead of 'alignContent'
      justifyContent={isLargerScreen ? 'center' : 'flex-start'}
      minHeight="100vh"
    >
      {children}
    </Flex>
  );
}

export default ResponsiveAliginWrapper;
