import {
  mobileControllerChannel,
  mobileChannel,
  lrControllerChannel,
  lrChannel,
  lrRemoteChannel,
} from '.';

const getControllerMessageParameters = (device, interactType) => {
  try {
    switch (interactType) {
      case 'mobileInteract':
        return {
          controllerChannelName: mobileControllerChannel(device),
          deviceChannelNames: [mobileChannel(device)],
          remoteChannelNames: [],
        };
      case 'lrInteract':
        return {
          controllerChannelName: lrControllerChannel(device),
          deviceChannelNames: [lrChannel(device)],
          remoteChannelNames: lrRemoteChannel(device),
        };
      default:
        return {
          controllerChannelName: null,
          deviceChannelNames: [],
          remoteChannelNames: [],
        };
    }
  } catch (e) {
    console.log(e);
    return {
      controllerChannelName: null,
      deviceChannelNames: [],
      remoteChannelNames: [],
    };
  }
};

export default getControllerMessageParameters;
